.strip {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 100%;
	height: auto;
	padding: 10px 0;

	background-color: var(--redesign-main-color);
	color: var(--redesign-seventh-color);

	text-wrap: balance;
	font-style: normal;
	font-weight: 500;
	font-size: 12px;
	line-height: 1.4;
	text-align: center;
	letter-spacing: .1px;

	@media (min-width: 768px) {
		font-size: 14px;
	}

	@media (min-width: 992px) {
		font-size: 17px;
		letter-spacing: 0.35px;
		word-spacing: 0.5px;
	}

	&_green-yellow {
		background-color: var(--redesign-main-color);
		color: var(--redesign-second-color);
	}
}