%letter {
  letter-spacing: -0.01px;
}

$borderSepColor: #ccc;

@import "badge";

//  
.in_stock {
  .item-storage__name {
    color: var(--redesign-main-color);
  }
}

//   
.no_stock {
  .item-storage__name {
    color: #CC2500;
  }
}

.ecommerce {
  @media screen and (max-width: 320px) {
    width: 320px;
    min-width: 320px;
  }
}

$search-bar-height: 48px;
.ecommerce-application {
  .content-body {
    position: relative;
  }

  // To overlay in whole page
  .body-content-overlay {
    position: fixed !important;
    z-index: 12 !important;
  }

  // Shop Sidebar
  .sidebar-shop {
    margin-top: 0.85rem;
    width: $sidebar-width;
    z-index: 14;

    // for heading
    .filter-heading {
      margin-bottom: 1.75rem;
    }

    // for title
    .filter-title {
      margin-bottom: 1rem;
      margin-top: 2.5rem;
    }

    // Sidebar price range and categories list
    .price-range,
    .categories-list {
      li:not(:last-child) {
        margin-bottom: 0.75rem;
      }
    }

    // Sidebar brand list
    .brand-list {
      li {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 0.75rem;
      }
    }

    // Sidebar for slider
    .range-slider.noUi-horizontal {
      .noUi-handle {
        .noUi-tooltip {
          opacity: 0;
          transform: translate(-50%, -15%);

          &:before {
            content: '$ ';
          }
        }

        &:active {
          .noUi-tooltip {
            opacity: 1;
          }
        }
      }
    }

    // for stars in rating
    .ratings-list {
      display: flex;
      justify-content: space-between;
      margin-bottom: 0.5rem;

      &:last-child {
        margin-bottom: 2.5rem;
      }

      ul {
        margin-bottom: 0;

        .ratings-list-item {
          svg,
          i {
            width: 1.25rem;
            height: 1.25rem;
            font-size: 1.25rem;
          }
        }
      }
    }
  }

  // ratings style
  .filled-star {
    fill: var(--redesign-second-color);
    stroke: var(--redesign-second-color);
    color: var(--redesign-second-color);
  }

  .unfilled-star {
    stroke: $gray-100;
    color: $gray-100;
  }

  // E-commerce shop main content area

  //ecommerce header
  .ecommerce-header-items {
    display: flex;
    justify-content: space-between;

    .result-toggler {
      display: flex;
      align-items: center;

      // Sidebar toggler
      .shop-sidebar-toggler {
        padding-left: 0;

        &:active,
        &:focus {
          outline: 0;
        }

        .navbar-toggler-icon {
          height: auto;

          i,
          svg {
            color: $body-color;
            height: 1.5rem;
            width: 1.5rem;
            font-size: 1.5rem;
          }
        }
      }

      // Search result title
      .search-results {
        font-weight: $font-weight-bold;
        color: $headings-color;
      }
    }
  }

  // Product search bar
  .ecommerce-searchbar {
    .input-group {
      box-shadow: 0 2px 8px 0 rgba($black, 0.14);
      border-radius: 0.3rem;
    }
  }

  // Search bar
  .search-product,
  .input-group-text {
    height: $search-bar-height;
    border: none;
    font-size: 0.95rem;
    padding-left: 1.25rem;

    &::placeholder {
      font-size: 0.95rem;
    }
  }

  // main Products
  .ecommerce-card {

    // ratings
    .item-rating {
      ul {
        margin-bottom: 0;
      }

      svg,
      i {
        height: 1.143rem;
        width: 1.143rem;
        font-size: 1.143rem;
      }
    }

    // Item name
    .item-name {
      margin-bottom: 8px;
      @include mobile() {
        margin-bottom: 5px;
      }

      a {
        font-size: 16px;
        letter-spacing: -0.01px;
        line-height: 19px;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        transition: 0.3s;


        @include mobile() {
          -webkit-line-clamp: 3;
          font-size: 12px;
          line-height: 120%;
        }
        -webkit-box-orient: vertical;
        overflow: hidden;
        text-overflow: ellipsis;

        &:hover {
          color: var(--redesign-second-color);
        }
      }
    }

    .item-storage {

      display: flex;
      flex-direction: column;
      align-items: flex-start;
      padding: 0;
      margin: 10px 0;
      height: auto;
      @include mobile() {
        margin-bottom: 5px;
        margin-top: 5px;
      }

      &__name {
        @extend %letter;
        font-size: 14px;

        @include mobile() {
          font-size: 10px;

          height: 14px;
          line-height: 12px;

        }

        line-height: 120%;
        height: 20px;
      }
    }

    .item-prices {
      height: 45px;
      flex: none;
      order: 3;
      flex-grow: 0;
      margin: 30px 0 0 0;

      &__mod {
        display: flex;
        flex-direction: row;
        align-items: center;
        height: 15px;
        font-size: 12px;

        @include mobile() {
          font-size: 10px;
        }
        padding: 1px 2px;

        &__sale_price, &__old_price {
          display: flex;
          align-items: center;
          text-align: center;
          flex-direction: column;
          flex-grow: 0;
          flex: none;
        }

        &__old_price {
          margin: 2px 2px 2px 0;
          order: 0;
          color: #999999;
          text-decoration-line: line-through;
          font-style: italic;
        }

        &__sale_price {
          background: var(--redesign-second-color);
          border-radius: 3px;
          order: 1;
          margin: 0 0 0 10px;
          height: 15px;
          padding: 1px 2px;


          span {
            @extend %letter;
            font-weight: bold;
            line-height: 12px;
            display: flex;
            align-items: center;
            text-align: center;
            color: var(--redesign-second-text-color);
            flex: none;
            order: 0;
            flex-grow: 0;
            margin: 1px 0;
          }
        }

      }


      &__price {
        @extend %letter;
        padding: 6px 0 5px 0;
        height: 30px;
        font-size: 18px;
        line-height: 20px;
        font-weight: bold;
        @include mobile() {
          font-size: 23px;
        }
      }
    }

    .item-article {
      color: #999999;
      height: 30px;
      font-size: 15px;
      line-height: 18px;
      display: flex;
      align-items: center;

      @include mobile() {
        height: 24px;
      }

      .tooltip {
        .tooltip-inner {
          height: 30px;
          font-size: 10px;
          line-height: 90%;
          display: flex;
          align-items: center;
        }
      }

      &__text {
        font-size: 15px;
        line-height: 18px;
        letter-spacing: -0.01px;
        color: #999999;

       margin-left: 5px;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        overflow: hidden;
        text-overflow: ellipsis;

        @include mobile() {
          font-size: 12px;
        }

      }

      &__3dmodel,&__yottube, &__ies {
        width: 30px;
        height: 30px;
        position: relative;

        display: flex;
        align-items: center;
        flex: none;
        order: 0;
        flex-grow: 0;


        @include mobile() {
          margin-right: 2px;
          margin-left: -3px;
        }

        &:after {
          position: absolute;
          content: "";
          width: 30px;
          height: 30px;
