          }
        }
      }

      &__ies {
        cursor: pointer;
        &:after {
          background-image: url("data:image/svg+xml;base64,");
        }
        &:hover {
          &:after {
            background-image: url("data:image/svg+xml;base64,");
          }
        }
      }

      &__yottube {
        cursor: pointer;
        &:after {
          background-image: url("data:image/svg+xml;base64,");
        }
        &:hover {
          &:after {
            background-image: url("data:image/svg+xml;base64,");
          }
        }
      }
    }




    .item-description {
      font-size: 0.875rem;
    }

    // buttons style
    .btn-wishlist,
    .btn-cart {
      @extend %letter;
      color: #fff;
      border-radius: 2px;
      font-size: 14px;
      line-height: 14px;
      padding: 12px 20px;
      height: 40px;
      width: 137px;
      text-align: center;
      position: relative;
      font-weight: 300;

      span {
        position: absolute;
        top: -8px;
        right: -2px;
        font-size: 8px;
        line-height: 10px;
        font-weight: 600;
        border-radius: 4px;
        padding: 3px;
        color: var(--redesign-second-text-color);
        background: var(--redesign-second-color);

        @media screen and (min-width: 576px) {
          right: -8px;
        }
      }
    }


    .item-buttons {
      flex: none;
      order: 4;
      flex-grow: 0;
      margin: 10px 0;

      .shipped {
        position: relative;
        display: flex;
        align-items: center;
        gap: 5px;
        width: 100%;
        height: 25px;
        margin-bottom: 5px;

        color: var(--redesign-main-color);
        font-size: 10px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;

        @media screen and (min-width: 576px) {
          font-size: 14px;
          width: max-content;
        }

        &::before {
          content: '';
          display: inline-block;
          position: relative;
          width: 25px;
          height: 25px;
          background: transparent no-repeat center;
          background-image: url("data:image/svg+xml;base64,");
        }

        &:hover {
          .shipped_tooltip {
            display: flex;
          }
        }

        &_tooltip {
          display: none;
          align-items: center;
          column-gap: 5px;
          position: absolute;
          top: -120px;
          left: 50%;
          transform: translateX(-50%);
          width: 200px;
          height: auto;
          border-radius: 3px;
          padding: 11px 15px;
          font-size: 14px;
          line-height: 18px;
          color: var(--redesign-second-text-color);
          background: var(--redesign-second-color);
          z-index: 5;

          @media (min-width: 440px) {
            left: 0;
            transform: translateX(0);
          }

          @media (min-width: 767px) {
            top: -70px;
            left: 50%;
            transform: translateX(-50%);
            width: 300px;
            height: 56px;
          }

          &:after {
            position: absolute;
            content: '';
            bottom: -22px;
            left: 50%;
            transform: translateX(-50%);
            width: 0;
            height: 0;
            border-left: 25px solid transparent;
            border-right: 25px solid transparent;
            border-top: 38px solid var(--redesign-second-color);
          }

          div {
            position: relative;
            z-index: 1;
          }

          svg {
            flex-shrink: 0;
            display: block;
            width: 25px;
            height: 25px;
            object-fit: contain;
          }
        }
      }
    }

    .btn-cart, .item-list {
      float: left;
    }

    .item-list {
      padding: 6px 0 0 0;

      &__item {
        width: 30px;
        height: 30px;
        margin: 0 5px;
        position: relative;
        display: inline-block;
        cursor: pointer;

        @include mobile() {
          margin: 0 28px 0 0;
        }

        &:after {
          content: "";
          position: absolute;
          width: 30px;
          height: 30px;
          left: 5px;
          background-repeat: no-repeat;

          @include mobile() {
            left: 0;
          }
        }
      }

      .btn-favorite {
        &:after {
          background-image: url("data:image/svg+xml;base64,");
        }

        &.voted {
          &:after {
            background-image: url("data:image/svg+xml;base64,");
          }
        }
      }

      .btn-compare {
        &:after {
          background-image: url("data:image/svg+xml;base64,");
        }

        &.added {
          &:after {
            background-image: url("data:image/svg+xml;base64,")
          }
        }
      }
    }

    .item-info {
      display: none;
    }

    .item-dots {
      position: absolute;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
      z-index: 2;
    }

    .item-images {
      display: flex;
      width: 90%;
      height: 100%;
      margin: 0 auto;
      z-index: 9;
      left: 0;
      top: 0;
      flex-direction: row;

      @include mobile() {
        top: 224px;
        height: auto;
      }

      &__image_hide {
        display: none;
      }

      &__zone {
        flex-grow: 1;
        height: 100%;
      }
    }

    .item-slider {
      position: absolute;
      /*bottom: -50px;*/
      display: -webkit-box;
      display: -webkit-flex;
      display: -moz-box;
      display: -ms-flexbox;
      display: flex;
      width: 100%;
      left: 0;
      cursor: default;
      -webkit-box-pack: center;
      -webkit-justify-content: center;
      -moz-box-pack: center;
      -ms-flex-pack: center;
      justify-content: center;
      margin-top: 20px;

      &__dot {
        width: 7px;
        height: 7px;
        border-radius: 50%;
        background: #999;
        margin: 0 5px;
        border: 1px solid #fff;
        -webkit-transition: .2s;
        transition: .2s;
        cursor: pointer;

        &.active {
          position: relative;
          background: var(--redesign-second-color);
          border: 1px solid var(--redesign-second-color);
          color: var(--redesign-second-text-color);
        }
      }
    }

    .item-badges {
      display: flex;
      align-items: center;
      column-gap: 5px;
      position: absolute;
      bottom: 0;
      left: 0;
      width: auto;
      height: 24px;
      z-index: 3;

      &__el {
        display: inline-block;
        position: relative;
        height: 24px;
        background-position: center;
        background-repeat: no-repeat;
        cursor: pointer;

        &_water-drop {
          width: 48px;
          background-image: url("data:image/svg+xml;base64,");
        }

        &_tv {
          width: 25px;
          //     
          // background-image: url("data:image/svg+xml;base64,");
          background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='25' height='24' viewBox='0 0 25 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath fill-rule='evenodd' clip-rule='evenodd' d='M22.3223 8.4628C21.7067 7.8561 20.3666 6.61361 18.3491 5.11398C16.3317 3.61436 14.7601 2.68577 14.0073 2.26958C13.6979 2.09885 13.3532 2.00654 13.0019 2.00033C12.6506 1.99413 12.303 2.07422 11.988 2.23392C11.1647 2.65199 9.74139 3.49377 7.63931 5.12806C5.56146 6.74124 4.34851 7.82842 3.69584 8.45576C3.43015 8.71067 3.23037 9.02931 3.11445 9.38305C2.99853 9.73678 2.97011 10.1145 3.03174 10.4823C3.16437 11.2753 3.50008 12.7974 4.34805 15.5429C5.22438 18.3807 5.84869 19.8296 6.21779 20.5485C6.37734 20.8563 6.60373 21.1224 6.87947 21.3262C7.15521 21.53 7.47293 21.6661 7.80808 21.7239C8.55131 21.8496 10.0556 21.9993 13.0029 21.9993C15.9502 21.9993 17.4554 21.8492 18.1987 21.7234C18.5315 21.6664 18.8473 21.532 19.1217 21.3306C19.3962 21.1291 19.6221 20.8659 19.7821 20.5611C20.1603 19.8339 20.7929 18.3722 21.6573 15.5405C22.4911 12.8125 22.8291 11.2847 22.9663 10.4837C23.0293 10.1185 23.0034 9.74303 22.8911 9.39052C22.7787 9.03801 22.5834 8.71936 22.3223 8.4628ZM21.0207 10.1332C20.894 10.8746 20.5747 12.3095 19.773 14.9329C18.9094 17.7576 18.3158 19.0733 18.0386 19.6058C18.0226 19.6366 17.9997 19.6631 17.9719 19.6832C17.9442 19.7034 17.9122 19.7165 17.8785 19.7217C17.2085 19.8348 15.8203 19.9704 13.0047 19.9704C10.1892 19.9704 8.80195 19.8348 8.13144 19.7217C8.09695 19.716 8.06424 19.7021 8.03596 19.6811C8.00767 19.6601 7.9846 19.6325 7.96862 19.6007C7.69694 19.0733 7.11104 17.766 6.23517 14.93C5.41922 12.2944 5.10409 10.8704 4.98152 10.1398C4.9754 10.1028 4.97829 10.0649 4.98994 10.0294C5.0016 9.99382 5.02164 9.9618 5.04829 9.93615C5.5546 9.44957 6.71999 8.38773 8.83351 6.74546C10.9164 5.12665 12.2537 4.36323 12.8657 4.05261C12.8959 4.03745 12.9291 4.02958 12.9627 4.02962C12.9995 4.02968 13.0358 4.03902 13.0683 4.05683C13.7777 4.4491 15.2564 5.32372 17.1888 6.