        }
      }

      .tooltip.right .tooltip-arrow {
        &::before {
          border-right-color: black;
        }
      }

      .tooltip-inner {
        font-size: 10px;
        line-height: 12px;
        background-color: black;
        border-radius: 2px;
        padding: 10px 10px 9px 9px;

        @media (min-width: 1200px) {
          white-space: nowrap;
        }
      }
    }

    // Item Image
    .item-img {
      text-align: center;
      margin-top: 33px;


      // no drag body
      user-select: none;
      touch-action: pan-y;

      &__water-drop-badge {
        display: block;
        position: absolute;
        bottom: 0;
        left: 0;
        width: 48px;
        height: 24px;
        z-index: 3;
        cursor: pointer;

        @media (min-width: 1200px) {
          overflow: hidden;

          &:hover {
            overflow: initial;

            span {
              opacity: 1;
              right: 0;
            }
          }
        }

        span {
          display: flex;
          justify-content: center;
          align-items: center;
          position: absolute;
          top: -3px;
          left: 0;
          z-index: 1;
          width: 92px;
          height: auto;
          transform: translateY(calc(-100% - 9px));
          min-height: 34px;
          margin: 0;
          padding: 5px;
          background: #000;
          border-radius: 2px;
          font-style: normal;
          font-weight: 400;
          font-size: 10px;
          line-height: 12px;
          text-align: center;
          color: #fff;
          opacity: 0;
          transition: all .2s linear;

          &.visible {
            @media (max-width: 1199px) {
              top: 0;
              opacity: 1;
            }
          }

          @media (min-width: 1200px) {
            width: initial;
            height: 24px;
            min-height: initial;
            padding: 5px 7.02px;
            white-space: nowrap;
            top: 50%;
            left: initial;
            right: -3px;
            transform: translate(calc(100% + 7px), -50%);
          }

          &::before {
            content: '';
            display: block;
            position: absolute;
            width: 14px;
            height: 9px;
            bottom: 0;
            left: 50%;
            transform: translate(-50%, 8px);
            background: url("https://cdn.ms.fandeco.ru/i/380/7d47a7277a.png") center no-repeat;

            @media (min-width: 1200px) {
              width: 8px;
              height: 13px;
              top: 50%;
              bottom: initial;
              left: 0;
              transform: translate(-7px, -50%);
              background-image: url("https://cdn.ms.fandeco.ru/i/381/5addd360c0.png");
            }
          }
        }
      }

      @include desktop() {
        min-height: 15.85rem;
        &__list {
          min-height: 224px;
        }
      }

      position: relative;

      &__list {
        display: flex;
        justify-content: center;
        @include mobile() {
          min-height: 140px;
        }
      }
    }
  }

  // margin for both views
  .grid-view,
  .list-view {
    &:not(.wishlist-items) {
      margin-top: 2rem;
    }
  }

  .collections-view {
    display: flex;
    flex-wrap: wrap;

    @media (min-width: 768px) {
      column-gap: 40px;
    }

    @media (min-width: 1200px) {
      padding-top: 20px;
      column-gap: 20px;
    }

    .ecommerce-card {
      //border-radius: 4px;
      border-radius: 0;
      border-color: transparent;
      width: 100%;
      box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
      transition: all .17s linear;

      @media (min-width: 768px) {
        width: calc(50% - 40px / 2);
      }

      @media (min-width: 992px) {
        &:nth-child(odd)::after {
          left: 0;
          right: initial;
        }

        &:nth-child(even)::after {
          left: initial;
          right: 0;
        }
      }

      @media (min-width: 1200px) {
        width: calc(50% - 20px / 2);
        padding: 19px;
        margin-bottom: 40px;
      }

      @media (hover: hover) and (pointer: fine) {
        &:hover {
          box-shadow: 0 0 20px rgba(0, 0, 0, 0.25);
        }
      }

      &::after {
        content: "";
        display: block;
        position: absolute;
        top: initial;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
        width: calc(100% + 42px);
        height: 1px;
        background-color: #bababa;

        @media (min-width: 992px) {
           transform: none;
        }

        @media (min-width: 1200px) {
          bottom: -21px;
        }
      }

      &__body {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        width: 100%;
      }

      .item-img {
        width: calc(50% - 9px);
        margin-top: 54px;

        @media (min-width: 1200px) {
          width: calc(50% - 10px);
          margin-top: 30px;
          padding-right: 20px;
        }

        &__thumb-wrap {
          display: block;
          position: relative;
          width: 150px;
          height: 140px;

          @media (min-width: 1200px) {
            width: 100%;
            height: auto;

            &::before {
              content: "";
              display: block;
              padding-bottom: 100%;
            }
          }
        }

        &__thumb {
          width: 100%;
          height: 100%;
          object-fit: contain;

          @media (min-width: 1200px) {
            display: block;
            position: absolute;
            top: 0;
            left: 0;
          }
        }
      }

      .card-content {
        width: calc(50% - 9px);
        margin-top: 20px;

        @media (min-width: 1200px) {
          width: calc(50% + 10px);
          margin-top: 2px;
          padding-left: 20px;
          padding-right: 20px;
        }
      }

      .card-body {
        padding: 0;
      }

      .card-collection {
        width: 100%;
        height: auto;
        border-top: 1px solid #bababa;
        margin-top: 12px;
        padding-top: 18px;
        padding-bottom: 26px;

        @media (min-width: 1200px) {
          margin-top: 19px;
          padding-top: 19px;
          padding-bottom: 0px;
        }

        p {
          display: flex;
          align-items: baseline;
          column-gap: 30px;
          padding: 0;
          margin: 0 0 10px 0;
          font-style: normal;
          font-weight: 400;
          font-size: 12px;
          line-height: initial;
          color: #000000;

          @media (min-width: 1200px) {
            column-gap: 37px;
            font-size: 14px;
            line-height: 24px;
            margin-bottom: 7px;
          }

          a {
            color: #00aae9;
            text-decoration: none;
            transition: color .1s linear;

            @media (min-width: 1200px) {
               &:hover {
                 color: var(--redesign-second-color);
               }
            }
          }
        }

        ul {
          display: flex;
          align-items: flex-start;
          column-gap: 10px;
          width: 100%;
          height: auto;
          list-style: none;
          margin: 0;
          padding: 0;

          @media (min-width: 1200px) {
            column-gap: 22px;
          }

          li  {
            width: 100%;

            &:nth-child(n+5) {
              display: none;
            }

            @media (min-width: 1200px) {
              display: block !important;

              &:nth-child(n+6) {
                display: none !important;
              }
            }

            a {
              display: flex;
              flex-direction: column;
              align-items: center;
              width: 100%;

              @media (min-width: 1200px) {
                 &:hover {
                   img {
                     opacity: 0.7;
                   }

                   span {
                     color: #00aae9;
                   }
                 }
              }
            }

            div {
              width: 100%;
              height: auto;
              position: relative;

              &::before {
                content: '';
                display: block;
                padding-bottom: 100%;
              }
            }

            img {
              position: absolute;
              top: 0;
              left: 0;
              width: 100%;
              height: 100%;
              object-fit: contain;
              transition: all .1s linear;
            }

            span {
              display: block;
              margin-top: 11px;
              font-style: normal;
              font-weight: 400;
              font-size: 12px;
              line-height: 14px;
              letter-spacing: 0.01em;
              color: #000000;
              text-align: center;
              transition: all .1s linear;

              @media (min-width: 1200px) {
                margin-top: 11px;
              }
            }
          }
        }
      }

      .item-label {
        left: 0;
        width: calc(50% - 13px);

        @media (min-width: 1200px) {
          top: 19px;
          left: 19px;
        }
      }

      .item-name {
        height: 42px;

        @media (min-width: 1200px) {
            height: 36px;
            margin-bottom: 12px;
        }

        a {
          font-size: 12px;
          line-height: 14px;
          -webkit-line-clamp: 3;

          @media (min-width: 1200px) {
            font-size: 16px;
            line-height: 18px;
            -webkit-line-clamp: 2;
          }
        }
      }

      .item-article__text {
        font-size: 12px;

        @media (min-width: 1200px) {
          font-size: 16px;
          line-height: 18px;
        }
      }

      .item-prices {
        @media (min-width: 1200px) {
          margin-bottom: 6px;
        }
      }

      .item-prices__price {
        padding-top: 5px;
        font-size: 24px;

        @media (min-width: 992px) {
          padding-top: 6px;
          font-size: 18px;
          line-height: 20px;
        }
      }

      .item-storage {
        margin-bottom: 6px;
        margin-top: 3px;

        @media (min-width: 1200px) {
          margin-top: 11px;
          margin-bottom: 48px;
        }
      }

      .item-buttons {
        margin-top: 11px;

        @media (min-width: 1200px) {
          margin: 0;
        }
      }

      .btn-cart {
        width: 150px;
        border-radius: 3px;

        @media (min-width: 1200px) {
          flex: 1 0 auto;
          width: 124px;
          max-width: 124px;
          margin-right: 5px;
        }

        @media (min-width: 1440px) {
          margin-right: 12px;
        }
      }

      .item-list {
        width: 100%;
        padding-top: 11px;

        @media (min-width: 1200px) {
          width: auto;
          padding-top: 0;
          display: flex;
          align-items: center;
        }
      }

      .item-buttons__btn {
        @media (min-width: 1200px) {
          display: flex;
          align-items: center;
        }
      }

      .item-list__item {
        margin-right: 7px;

        @media (min-width: 1200px) {
          margin-right: 5px;
          margin-left: 0;
        }

        @media (min-width: 1440px) {
          margin-right: 14px;
        }
      }

      .item-badges {
        bottom: 8px;
      }

      .item-storage__name {
        height: 14px;
        font-size: 10px;
        line-height: 12px;

        @media (min-width: 1200px) {
          height: 20px;
          font-size: 14px;
          line-height: 18px;
        }
      }
    }
  }

  .item-properties {
    display: none;
  }

  .item-buttons {
    .item-storage {
      display: none;
    }
  }

  .cards-view {

    .card-body, .item-buttons, .item-img {
      @media (min-width: 1200px) {
        position: relative;
        z-index: 1;
      }
    }

    .item-label {
      top: 15px;
    }

    .item-storage {
      @media (min-width: 1200px) {
        margin: 12px 0 10px;
      }
    }

    .ecommerce-card {
      @media (min-width: 1200px) {
        padding: 0 !important;

        &::after {
          bottom: 20px !important;
        }

        &:nth-child(3n+3)::after {
          left: initial !important;
          right: 0 !important;
        }
      }

      @media (min-width: 1400px) {
        &:nth-child(3n+3)::after {
          left: 0 !important;
          right: initial !important;
        }

        &:nth-child(4n+4)::after {
          left: initial !important;
          right: 0 !important;
        }
      }
    }

    .ecommerce-card__body {
      @media (min-width: 1200px) {
        position: relative;
        width: 100% !important;
        padding: 15px 16px 15px 15px;

        &::before {
          content: "";
          position: absolute;
          top: 0;
          left: 0;
          z-index: 0;
          width: 100%;
          height: 100%;
          //border-radius: 4px;
          box-shadow: 0 0 0 rgba(0, 0, 0, 0);
          transition: all .17s linear;
        }

        &:hover::before {
          box-shadow: 0 0 20px rgba(0, 0, 0, 0.25);
        }
      }
    }

    &.list-view {

      .ecommerce-card {
        @media (min-width: 1200px) {
          padding-top: 10px !important;
          margin-top: 23px;
        }
      }

      .ecommerce-card__body {
        @media (min-width: 1200px) {
          &::before {
            top: 10px;
          }
        }
      }
    }
  }
  
  // Grid View Specific style
  .grid-view {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    column-gap: 20px;

    &.wishlist-items {
      grid-template-columns: 1fr 1fr 1fr 1fr;
    }

    &.wishlist-items-inline {
      display: flex;
      margin-top: 0 !important;

      .ecommerce-card {
        flex-shrink: 0;
        position: relative;
        transition-property: transform;
        padding: 20px 0;
        max-width: 244px;
        min-width: 150px;

        @include mobile() {
          max-width: 160px;
        }
      }
    }

    .wishlist-slide {
      .ecommerce-card {
        flex-shrink: 0;
        width: 100%;
        height: 100%;
        position: relative;
        transition-property: transform;
      }
    }

    .ecommerce-card {
      @include media-breakpoint-up(lg) {
        min-width: 224px !important;
      }

      border-width: 0;
      border-radius: 0;
      padding: 20px 0 20px 0;

      // Item name
      .item-name {
        margin-top: 6px;
        height: 42px;
        @include mobile() {
          margin-top: 12px;
        }
      }

      @include mobile() {
        padding-bottom: 16px;
      }

      .btn-cart, .item-list {
        @include mobile() {
          float: none;
        }
      }

      @include mobile() {
        .btn-cart {
          width: 100%;
        }

        .item-img {
          img {
            width: 140px;
          }
        }
      }

      &__body {
        width: 100%;
        @include media-breakpoint-up(md) {
          width: 224px;
        }
      }


      // Item content wrapper
      .item-wrapper {
        display: flex;
        justify-content: space-between;
        align-items: baseline;
      }

      // Hiding list view items
      .shipping,
      .item-company,
      .item-options .item-price {
        display: none;
      }

      .card-body {
        padding: 17px 0 0 0;
      }

      .item-list {
        height: 33px;
        @include mobile() {
          padding: 13px 0 0 0;
        }
      }

      .item-buttons {
        .item-prices {
          display: none;
        }
      }
    }
  }

  // List View Specific style
  .list-view {
    display: grid;
    grid-template-columns: 1fr;

    .ecommerce-card {
      border-width: 0;
      border-radius: 0;
      padding-top: 20px;
      margin-top: 20px;
      padding-bottom: 20px;
      min-height: 301px;

      &:first-child {
        margin-top: 0;
      }


      @include mobile() {
        padding-bottom: 10px;
        margin-top: 10px;
      }

      > .d-flex {
        display: block !important;
        width: 100%;
      }

      .ecommerce-card__body {
        display: grid;
        grid-template-columns: 1fr 2fr 1fr;
        border-width: 0;
        border-radius: 0;
      }

      @include desktop() {
        border-top: 1px solid $borderSepColor;
        .item-img {
          max-height: 244px;
          max-width: 224px;
          margin-right: 20px;
          margin-left: 20px;
          margin-top: 19px;
        }
      }

      &:last-child {
        border-bottom: 1px solid $borderSepColor;
      }


      // list view image

      .card-body {
        padding: 0 0 0 0;
        display: flex;
        flex-direction: column;

        .item-prices {
          display: none;
        }

        // item name and ratings order
        .item-wrapper {
          order: 2;
        }

        .item-name {
          margin-top: 20px;
        }

        // .item-rating,
        .item-price {
          display: none;
        }
      }

      .item-img {
        @include mobile() {
          margin-bottom: 34px;
          margin-top: 20px;
        }
      }

      .item-slider {
        @include mobile() {
          margin-top: 9px;
        }
      }

      .item-buttons {
        @extend .d-flex;
        @extend .flex-column;

        @include desktop() {
          padding: 0 20px;
          margin-top: auto;
          margin-bottom: 0;
        }


        &__btn {
          width: 100%;
          @include media-breakpoint-up(xl) {
            min-width: 224px;
          }
        }

        .item-prices {
          order: 0;
          width: 100%;
        }
      }

      .item-list {
        @include mobile() {
          padding-left: 12px;
        }
      }

    }

    .item-properties {
      display: block;
      margin-top: 10px;

      &__line {
        display: flex;
        flex-direction: column;
        gap: 0;
        margin-bottom: 5px;

        @media (min-width: 1200px) {
          display: flex;
          flex-direction: row;
          gap: 10px;
        }

        &:last-child {
          margin-bottom: 0;
        }
      }
      &__name {
        font-size: 14px;
        line-height: 20px;
        color: #383633;
      }

      &__value {
        font-size: 14px;
        line-height: 20px;
        font-weight: 600;
        color: #383633;
      }
    }

    .card-body {
      .item-storage {
        display: none;
      }
    }

    .item-buttons {
      .item-storage {
        display: flex;
      }
    }
  }

  // for checkout-items and options
  // Remove padding from stepper header & content area
  .checkout-tab-steps {
    .bs-stepper-header,
    .bs-stepper-content {
      padding: 0;
      margin: 0;
    }
  }

  // Checkout items list
  .checkout-items {
    .ecommerce-card {
      .item-img {
        img {
          width: 200px;
        }
      }

      // adding order as we have updated order for shop list view
      .item-name {
        order: 0 !important;
      }

      .item-company,
      .item-rating {
        margin-bottom: 0.4rem !important;
      }

      .item-quantity {
        display: flex;
        align-items: center;
      }

      .delivery-date {
        margin-top: 1.2rem;
        margin-bottom: 0.25rem;
      }

      // Item options button
      .item-options {
        .btn {
          display: flex;
          align-items: center;
          justify-content: center;
        }
      }
    }
  }

  .checkout-options {
    .coupons {
      &:focus-within {
        box-shadow: none;
      }

      input {
        border: none;
        padding-left: 0;
        color: $body-color;
        font-weight: $font-weight-bolder;

        &::placeholder {
          color: $body-color;
          font-weight: $font-weight-bolder;
        }
      }

      .input-group-append {
        margin: 0;
      }

      .input-group-text {
        height: auto;
        font-weight: $font-weight-bolder;
        padding: inherit;
      }
    }

    .price-details {
      .price-title {
        font-weight: $font-weight-bolder;
        margin-bottom: 0.75rem;
        margin-top: 1.5rem;
      }

      .price-detail {
        display: flex;
        justify-content: space-between;
        margin-bottom: 0.75rem;

        .detail-title {
          &.detail-total {
            font-weight: $font-weight-bolder;
          }
        }
      }
    }
  }

  .payment-type {
    .gift-card {
      cursor: pointer;
    }
  }

  // checkout wizard

  .checkout-tab-steps {
    background-color: transparent !important;
    box-shadow: none !important;

    .bs-stepper-header {
      border: none;
    }
  }

  // To hide toggler on lg and up and grid view for btn-wishlist and list view for checkout
  @include media-breakpoint-up(lg) {
    .ecommerce-header-items {
      // Hide toggler in > lg screen
      .shop-sidebar-toggler {
        display: none;
      }
    }

    // Checkout list view
    .product-checkout {
      &.list-view {
        grid-template-columns: 2fr 1fr;
        column-gap: 2rem;
      }
    }
  }

  /* products list */
  .ecommerce-filters {
    height: inherit;
    /*overflow: hidden;*/

    .grid-view {
      margin-top: 20px;

      .ecommerce-card {

        @include desktop() {
          padding: 20px 0 60px 0;
          margin-bottom: 40px;
        }

        &:after {
          content: "";
          position: absolute;
          height: 1px;
          border-bottom: 1px solid $borderSepColor;
          bottom: 40px;
          width: 110%;
          left: -1px;
          right: 4px;
          @include mobile() {
            width: 125%;
            left: -20px;
            right: -40px;
            bottom: 0;
          }
        }
      }
    }


    @include mobile() {
      .list-view {
        .ecommerce-card {
          &:after {
            content: "";
            position: absolute;
            height: 1px;
            border-bottom: 1px solid $borderSepColor;
            bottom: 40px;
            width: 110%;
            left: -1px;
            right: 4px;
            @include mobile() {
              width: 125%;
              left: -20px;
              right: -40px;
              bottom: 0;
            }
          }
        }
      }
    }
  }

}

.ecommerce_slider.ecommerce-application .grid-view {
  column-gap: 0 !important;
}

// Media queries to change grid columns in different Screens and to hide search results below 768px
@include media-breakpoint-down(xl) {
  .ecommerce-application {
    // Header item area
    .ecommerce-header-items {
      .btn-group {
        align-items: center;

        .btn-icon {
          padding: 0.6rem 0.736rem;
        }
      }
    }

    .grid-view.wishlist-items {
      grid-template-columns: 1fr 1fr 1fr;
    }

    // overlay style for e-commerce page
    .body-content-overlay {
      position: fixed;
      opacity: 0;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;

      &.show {
        opacity: 1;
      }
    }

    // Horizontal Menu scss
    &.horizontal-layout {
      .body-content-overlay {
        z-index: 998 !important;
      }

      .sidebar-shop {
        z-index: 999 !important;
      }
    }
  }
}

@include media-breakpoint-down(md) {
  .ecommerce-application {
    .sidebar-left {
      .sidebar {
        // removed radius and margin as showing sidebar in full height
        .card {
          border-radius: 0;
          margin: 0;
        }

        // Sidebar style
        .sidebar-shop {
          transform: translateX(-112%);
          transition: all 0.25s ease;
          position: fixed;
          top: 0;
          left: 0;
          height: 100%;
          overflow-y: scroll;
          margin: 0;

          &.show {
            transition: all 0.25s ease;
            transform: translateX(0);
          }
        }
      }
    }

    // Grid view for responsive
    .grid-view {
      grid-template-columns: 1fr 1fr;
    }

    // List View
    .list-view {
      .ecommerce-card {
        .ecommerce-card__body {
          grid-template-columns: 1fr;

        }
      }
    }

    .grid-view.wishlist-items {
      grid-template-columns: 1fr 1fr;
    }

    // to hide search results
    .ecommerce-header-items {
      .result-toggler {
        .search-results {
          display: none;
        }
      }
    }
  }
}

@include media-breakpoint-down(sm) {
  .ecommerce-application {
    .grid-view.wishlist-items {
      grid-template-columns: 1fr 1fr;
    }


  }

}

@include mobile() {
  .list-view {
    .ecommerce-card {
      .card-body {
        margin-top: 0;

        .item-name {
          margin-top: 2px !important;
        }

        .item-storage {
          height: auto;
          margin-bottom: 0;
        }
      }

      .item-buttons {
        margin-top: 5px;
      }

      .item-prices__mod {
        margin-bottom: 7px;
      }

      .item-prices {
        margin-bottom: 12px;
      }
    }
  }

}

@include media-breakpoint-up(sm) {
  .list-view {
    .ecommerce-card {

      .item-info {
        text-align: left;
        width: 100%;
        display: block;
        line-height: 16px;

        a {
          display: block;
          color: #666;
          height: 24px;
          font-size: 14px;

          &:hover {
            color: $primary;
          }
        }

        &__sep {
          display: block;
          border-top: 1px solid #EDEDED;
          max-width: 104px;
          margin-bottom: 13px;
        }
      }
    }
  }
}

.list-view {
  .ecommerce-card {
    &:first-child {
      border-top: 0;
    }

    .item-label {
      left: 20px;
      top: 39px;
      max-width: 244px;
      @include mobile() {
        left: 0;
      }
    }
  }
}


.item-img {
  user-select: none;
  /*    ,        X */
  touch-action: pan-y;
}

/* slider*/
.ecommerce_slider {
  &.swiper-container {
    padding-bottom: 20px;
  }

  .swiper-scrollbar-drag {
    background-color: var(--redesign-third-color);
  }
}


//         --- START
.swiper-slide .ecommerce-card__body {
  flex-shrink: initial !important;
  max-width: 100%;
}

.adaptive-mobile {

  .item-label .item-label__badge:first-child:not(.item-label__badge_fast-view),
  .item-label .item-label__badge_fast-view + .item-label__badge {
    display: block !important;
  }

  .item-label__badges .item-label__badge_fast-view {
    @media (min-width: 992px) {
        display: block !important;
    }
  }

  .item-buttons__btn {
    @media (min-width: 992px) {
      .item-label__badge_fast-view {
        display: none !important;
      }
    }
  }

  .filters_product_banner {
    height: auto !important;
    border: none !important;
    margin-top: 30px;
  }

  &.grid-view {

    .filters_product_banner {
      @media (min-width: 991px) {
        margin-top: 0;
      }
    }
  }

  .item-buttons {
    .item-split {
      display: none;
    }
  }

  &.list-view .card-body .item-split {
    @media (min-width: 992px) {
        margin-top: 16px;
    }
  }

  @media (max-width: 991px) {

    &.grid-view,
    &.list-view {
      .ecommerce-card {
        padding: 0;
        margin: 0;

        .item-info {
          display: none;
        }

        .item-img__list {
          min-height: initial;
        }

        .eslide,
        .eslider,
        .eslider-list {
          width: 100% !important;
          height: auto;
          aspect-ratio: 1 / 1;
        }

        .item-badges + .item-label__badge_fast-view {
          display: none !important;
        }

        .item-slider {
          justify-content: center;
          margin-top: 5px;
          z-index: 10;
        }

        .item-slider__dot {
          width: 6px;
          height: 6px;
          transition: all .2s linear;
        }

        .item-name {
          a {
            transition: none;
          }
        }

        .item-label__badges {
          column-gap: 10px;
          row-gap: 5px;
        }

        .item-label__badge {
          margin: 0;
          padding: 0 9px;
          color: #000;
          text-align: center;
          font-size: 14px;
          font-weight: 400;
          line-height: 120%; /* 16.8px */
          max-width: none;

          div {
            padding: 7px 0 4px;
            max-width: none;
          }
        }

        .item-label .item-label__caret {
          flex: none;
          position: relative;
          margin: 0;

          &:after {
            width: 100%;
            height: 100%;
            background-color: #fff !important;
          }
        }

        .item-label {
          z-index: 5;
        }

        .item-name {
          overflow: hidden;

          a {
            color: #000;
            font-size: 16px;
            font-style: normal;
            font-weight: 400;
            line-height: 119%;
          }
        }

        .item-article__text {
          margin-left: 0;
          color: #5A5A5A;
          font-size: 14px;
          font-style: normal;
          font-weight: 400;
          line-height: normal;
        }

        .item-storage {
          margin-top: 6px;
          gap: 5px;
        }

        .item-storage__name {
          //color: var(--redesign-main-color);
          font-size: 14px;
          font-style: normal;
          font-weight: 400;
          line-height: normal;

          &:hover {
            color: var(--redesign-second-color);
          }
        }

        .item-article__text {
          margin-left: 3px;

          &:first-of-type {
            margin-left: 0;
          }
        }

        .item-prices {
          margin-top: 18px;
          height: auto;

          & + .item-split {
            display: block;
          }
        }

        .item-prices__mod {
          padding: 0 0 0 1px;
        }

        .item-prices__mod__old_price {
          color: #5a5a5a;
          font-size: 14px;
          font-style: normal;
          white-space: nowrap;
          margin-top: 2px;
        }

        .item-prices__mod__sale_price {
          height: 21px;
          display: flex;
          align-items: center;
          justify-content: center;
          padding: 2px 3px 0 3px;
          margin-left: 0;
          margin-top: -2px;

          span {
            color: var(--redesign-second-text-color);
            text-align: center;
            font-size: 14px;
            font-style: normal;
            font-weight: 400;
            line-height: 120%; /* 16.8px */
            white-space: nowrap;
          }
        }

        .item-prices__price {
          color: #000;
          font-size: 16px;
          font-weight: 700;
          line-height: 111.687%; /* 17.87px */
        }

        .btn-cart,
        .btn-wishlist {
          height: 35px;
          padding: 0;
          display: inline-flex;
          align-items: center;
          justify-content: center;
          color: #fff;
          text-align: center;
          font-size: 16px;
          font-style: normal;
          font-weight: 400;
          line-height: 111.687%; /* 17.87px */
        }

        .item-list {
          display: flex;
          align-items: center;
          justify-content: space-between;
          height: auto;

          .item-list__item {
            margin: 0;
          }

          .item-label__badge_fast-view {
            display: inline-block!important;
            width: 30px;
            height: 30px;
            opacity: 1;
            padding: 0;
            margin: 0;
            border: 0;

            .btn_oneClick {
              background-image: url("data:image/svg+xml;base64,");
            }
          }
        }

        .item-buttons {
          margin: 0;
        }
      }
    }

    &.grid-view {
      .ecommerce-card {

        .item-prices__price {
          padding-top: 14px;
        }

        .item-img {
          margin-top: 105px;
        }

        .item-name {
          height: 58px;
          margin-top: 13px;
        }

        .item-label {
          top: 35px;
        }

        .item-buttons {
          margin-top: 15px;
          margin-bottom: 30px;
        }

        .item-buttons .item-prices {
          & + .item-split {
            display: none;
          }
        }

        .item-list {
          padding-top: 10px;
        }

        .item-prices {
          margin-bottom: 6px;
        }
      }
    }

    &.list-view {
      .ecommerce-card {

        &::after {
          @media (min-width: 576px) {
            width: calc(100% + 40px) !important;
            right: 0 !important;
          }
        }

        margin-bottom: 30px;

        .ecommerce-card__body {
          display: flex;
          flex-wrap: wrap;

          @media (min-width: 768px) {
              display: block;
          }
        }

        .item-img {
          width: 37.5%;
          margin-right: 10px;
          margin-top: 105px;
          margin-bottom: 30px;

          @media (min-width: 768px) {
              width: 40.11%;
              float: left;
              margin-top: 76px;
          }
        }

        .item-label {
          top: 30px;
          max-width: none;

          @media (min-width: 768px) {
              top: 52px;
          }
        }

        .card-body {
          flex: none;
          width: calc(100% - 37.5% - 10px - 2px);

          @media (min-width: 768px) {
            width: calc(100% - 40.11% - 10px - 2px);
          }
        }

        .item-buttons {
          align-items: flex-start !important;
          width: 100%;

          @media (min-width: 768px) {
            width: calc(100% - 40.11% - 10px - 2px);
            margin-top: 26px;
          }
        }

        .item-name {
          height: 38px;
          margin-top: 105px !important;
          margin-bottom: 6px;

          @media (min-width: 768px) {
            margin-top: 92px!important;
            height: auto;
          }

          a {
            @media (min-width: 768px) {
                -webkit-line-clamp: 2;
            }
          }
        }

        .card-body .item-prices {
          & + .item-split {
            display: none;
          }
        }

        .item-prices {
          margin-bottom: 0;
        }

        .item-prices__price {
          padding-top: 8px;

          @media (min-width: 768px) {
              height: 28px;
          }
        }

        .item-buttons__btn {
          display: flex;
          align-items: center;
          gap: 20px;
          width: 100%;
          margin-top: 17px;

          @media (min-width: 768px) {
            margin-top: 20px;
          }
        }

        .btn-cart,
        .btn-wishlist {
          flex-shrink: 0;
          width: 150px;
        }

        .item-list {
          width: 100%;
          max-width: 50%;
          padding: 0;

          @media (min-width: 768px) {
              width: auto;
              gap: 30px;
          }
        }
      }
    }
  }

  @media (min-width: 768px) {
      .ecommerce-card__body {
        flex-shrink: 0 !important;
      }
  }
}
//         --- FINISH

.ecommerce-application,
.adaptive-mobile.grid-view,
.adaptive-mobile.list-view {
  .ecommerce-card .item-storage__name {
    min-height: 20px;
    font-size: 12px !important;
    line-height: 1.3 !important;

    @media (min-width: 992px) {
      font-size: 14px !important;
    }

    @media (max-width: 375px) {
      &:first-child {
        min-height: 30px;
      }
    }
  }
}

