.ncatslrange {
    padding-left: 7px;
    padding-right: 10px;
    margin-bottom: 18px;
    padding-top: 6px;
}

.ncatslrange .noUi-horizontal {
    height: 2px;
}

.ncatslrange .noUi-connects {
    height: 2px;
}

.ncatslrange .noUi-connect {
    background: var(--redesign-main-color);
}

.ncatslrange .noUi-horizontal .noUi-handle {
    width: 14px;
    height: 14px;
    -webkit-box-shadow: none;
    box-shadow: none;
    right: -7px;
    border: none;
    background: var(--redesign-main-color);
}

.ncatslrange .noUi-handle:before,
.ncatslrange .noUi-handle:after {
    display: none;
}

.ncatslrange input {
    width: 100%;
    padding: 0 10px 0 28px;
    height: 30px;
    font-size: 13px;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    color: #000000;
    border: 1px solid #999999;
    border-radius: 3px;
}

.ncatslrange .polzun_input {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    width: auto;
    margin-left: -10px;
    margin-right: -10px;
    position: relative;
    margin-bottom: 14px;
}
.ncatslrange .polzun_input_el {
    position: relative;
    min-width: 50%;
    max-width: 50%;
    padding-left: 5px;
    padding-right: 5px;
}
.ncatslrange .polzun_input_el .lab {
    position: absolute;
    left: 16px;
    top: 7px;
    font-size: 13px;
    z-index: 1;
    color: #999999;
}
.ncatslrange .polzun_input input {
    width: 100%;
    padding: 0 10px 0 28px;
    height: 30px;
    font-size: 13px;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    color: #000000;
    border: 1px solid #999999;
    border-radius: 3px;
}

.noUi-touch-area {
    cursor: pointer;
}

#mse2_filters .mse2_number_slider {
    background: #ccc;
    margin-bottom: 25px;
}

//     . !!!    !!! --- START
#mse2_filters {
    .mse2_number_slider {
        display: none;
    }

    .ncatslrange .polzun_input {
        margin-bottom: 5px !important;
    }

    .ncatfilcontainer {
        margin-top: 5px !important;
        padding-top: 0 !important;
    }

    .ncatfiltersector_top_filter .mse2_number_slider {
        display: block !important;
        margin-top: 20px !important;
        margin-bottom: 20px !important;
    }
}
//     . --- FINISH