.about-product {

    &__wrapper {
        display: flex;
        flex-direction: column;
    }

    &__item {
        display: flex;
        flex-direction: column;
        position: relative;
        padding: 20px;
        margin: 0 0 20px 0;
        background: #f8f8f8;

        @media (min-width: 768px) {
            margin-bottom: 40px;
        }
    }

    &__caption {
        font-style: normal;
        font-weight: 400;
        font-size: 24px;
        line-height: 28px;
        letter-spacing: 0.01em;
        color: #000000;
        margin: 0 0 20px 0;
        padding: 0;

        @media (min-width: 768px) {
            font-size: 28px;
            line-height: 34px;
        }
    }

    &__text {
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;
        letter-spacing: 0.01em;
        color: #000000;
        margin: 0 0 20px 0;
        padding: 0;

        @media (min-width: 768px) {
            font-size: 16px;
            line-height: 21px;
            letter-spacing: 0.2px;
        }

        p {
            display: block;
            position: relative;
            margin: 0;
            padding: 0;

            font-weight: 400;
            font-size: 14px;
            line-height: 17px;
            letter-spacing: 0.0101em;
            color: #000000;

            @media (min-width: 768px) {
                font-size: 16px;
                line-height: 21px;
                letter-spacing: 0.205px;
            }
        }
    }

    &__btn {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        margin: 0 0 30px 0;

        @media (min-width: 768px) {
            margin-bottom: 40px;
        }

        .btn {
            display: flex;
            justify-content: center;
            align-items: center;
            letter-spacing: 0.01px;
            padding: 8px 18px 7px 19px;
            transition: all .1s linear;

            @media (min-width: 768px) {
                letter-spacing: 0.01px;
                padding: 13px 32px 12px 32px;
            }

            @media (min-width: 1200px) {
               &:hover {
                   color: #fff;
               }
            }
        }
    }

    &__picture-list {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-gap: 20px;
        position: relative;
        margin: 0;
        padding: 0;
        list-style: none;

        @media (min-width: 576px) {
            grid-template-columns: repeat(3, 1fr);
        }

        @media (min-width: 768px) {
            grid-template-columns: repeat(5, 1fr);
        }

        @media (min-width: 991px) {
            left: -1px;
            top: -1px;
        }
    }

    &__picture-item {
        display: block;
        position: relative;
        height: auto;
        margin: 0;
        padding: 0;
        background-color: #ffffff;

        &::before {
            content: '';
            display: block;
            padding-bottom: 100%;
        }

        img {
            display: block;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    .shop_list_title {
        padding-top: 22px;
        padding-bottom: 31px;
        box-shadow: inset 0px -1px 0px #9b9b9b;
        margin-bottom: 20px;

        @media (max-width: 650px) {
            padding-top: 30px;
        }

        @media (min-width: 651px) and (max-width: 768px) {
            padding-top: 11px;
        }

        @media (max-width: 768px) {
            padding-bottom: 18px;
        }
    }
}