.stretch-banner {
    position: relative;
    width: 100%;
    height: 56px;
    overflow: hidden;
    background-color: var(--redesign-main-color);

    @include media-breakpoint-up(md) {
        height: 42px;
    }

    &_close {
        .stretch-banner__viewport {
            width: fit-content;
            overflow: hidden;
            animation: stretch-banner-slider 20s linear infinite normal;
        }

        .stretch-banner__content {
            width: auto;
            padding-right: 50px;
        }
    }

    &__viewport {
        display: flex;
        width: 100%;
        height: 100%;
    }

    &__content {
        width: 100%;
        height: 100%;
        margin: 0;
        padding: 22px 10px;
        font-family: $font-family;
        font-style: normal;
        font-weight: 700;
        font-size: 12px;
        line-height: 15px;
        text-align: center;
        letter-spacing: 0.11px;
        color: var(--redesign-second-text-color);
        white-space: nowrap;


        @include media-breakpoint-up(md) {
            height: 42px;
            padding: 11px;
            font-size: 14px;
            line-height: 20px;
            letter-spacing: 0.35px;
        }

        @include media-breakpoint-up(lg) {
            font-size: 17px;
        }
    }

    &__location {
        display: none;
        position: relative;
        opacity: 0;
        transition: all .3s linear;

        &.active {
            display: inline;
        }

        &.visible {
            opacity: 1;
        }
    }
}

@keyframes stretch-banner-slider {
    from {transform: translateX(0%);}
    to {transform: translateX(-50%);}
}

.dolyami-banner {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    z-index: 10;
    margin:0;
    width: 100%;
    height: 56px;

    @media (min-width: 768px) {
        height: 42px;
    }

    &_bright {
        background-color: #ffcced;
    }

    &_light {
        background-color: #f6f7f8;
    }

    &_dark {
        background-color: var(--redesign-text-color);

        span {
            color: var(--redesign-second-color) !important;
        }

        button, a {
            color: var(--redesign-text-color) !important;
            background-color: var(--redesign-second-text-color) !important;

           @media (min-width: 992px) {
               color: var(--redesign-second-text-color) !important;
           }

            &::after {
                background-color: var(--redesign-second-text-color) !important;
            }
        }

        mark {
            background-image: url("data:image/svg+xml;base64,");

            @media (min-width: 992px) {
                background-image: url("data:image/svg+xml;base64,");
            }
        }
    }

    &_bright, &_light {
        @media (min-width: 1441px) {
            button, a {
                &:hover,
                &:active,
                &:focus {
                    color: var(--redesign-text-color) !important;
                }
            }
        }

        mark {
            background-image: url("data:image/svg+xml;base64,");

            @media (min-width: 992px) {
                background-image: url("data:image/svg+xml;base64,");
            }
        }
    }

    &__wrap {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 12px 24px;
        column-gap: 11px;

        @media (min-width: 500px) {
            justify-content: center;
        }

        @media (min-width: 992px) {
            column-gap: initial;
        }

        @media (min-width: 1441px) {
            padding: 9px 0;
        }
    }

    span {
        display: inline-block;
        position: relative;
        top: 1px;
        width: 100%;
        height: auto;
        font-family: 'Factor A', 'Roboto', sans-serif;
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 16px;
        letter-spacing: -0.24px;
        color: var(--redesign-text-color);

        @media (min-width: 500px) {
            width: auto;
        }

        @media (min-width: 992px) {
            top: initial;
        }

        @media (min-width: 1441px) {
            font-size: 13px;
            line-height: 24px;
            letter-spacing: 0;
        }
    }

    button, a {
        display: inline-flex;
        align-items: center;
        justify-content: center;
        flex: 1 0 auto;
        position: relative;
        background-color: var(--text-color);
        border: none !important;
        border-radius: 999px;
        padding: 2px 6px 0 0;
        letter-spacing: 0;
        width: 92px;
        height: 24px;
        font-family: 'Factor A', 'Roboto', sans-serif;
        font-style: normal;
        font-weight: 500;
        font-size: 13px;
        line-height: 16px;
        color: var(--second-text-color);

        @media (min-width: 500px) {
            flex-grow: initial;
        }

        @media (min-width: 992px) {
            display: inline-block;
            width: auto;
            height: auto;
            background-color: transparent !important;
            padding: initial;
            color: var(--text-color);
            margin-left: 3px;
            margin-right: 14px;
            font-family: 'Factor A',Roboto,sans-serif;
            font-style: normal;
            font-weight: 500;
            font-size: 14px;
            line-height: 16px;

            &::after {
                content: '';
                display: block;
                position: absolute;
                bottom: -1px;
                left: 0;
                width: 100%;
                height: 1px;
                background-color: rgba(0,0,0,.8);

                @media (min-width: 1441px) {
                    bottom: 5px;
                }
            }
        }

        @media (min-width: 1441px) {
            font-size: 13px;
            line-height: 24px;

            &:hover::after,
            &:active::after,
            &:focus::after {
                display: none;
            }
        }
    }

    mark {
        display: inline-block;
        flex: 1 0 auto;
        padding: 0;
        margin: 0;
        background-color: transparent;
        width: 24px;
        height: 24px;
        background-position: center;
        background-repeat: no-repeat;

        @media (min-width: 500px) {
            flex-grow: initial;
        }

        @media (min-width: 992px) {
            order: 1;
            width: 64px;
            height: 20px;
        }
    }
}