.tob_banner_street_slide {
  opacity: 0;
  position: absolute;
  top: -35px;
  left: 0;
  width: 1000px;
  margin-left: -500px;
  text-align: center;
  transition: 0.3s;
}

.tob_banner_street_slide a {
  display: inline-block !important;
  font-size: 24px;
}

.tob_banner_street_slide.active {
  opacity: 1;
}


.ncatlist .ncatpaginator .page-link:not(.page-item) {
