.new_card_sliders {
  display: flex;
  min-width: 572px;
  max-width: 572px;
  margin-right: 30px;
  margin-bottom: auto;

  .new_card_sliderDots {
    min-width: 60px;
    max-width: 60px;
    padding-top: 25px;
    padding-bottom: 25px;
    margin-right: 20px;
    position: relative;
    margin-bottom: auto;
    max-height: 463px;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;

    .swiper-wrapper {
      display: flex;
      flex-direction: column;
    }

    .swiper-slide {
      display: block;
      margin-bottom: 10px;

      &:before {
        content: "";
        position: absolute;
        z-index: 10;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
      }

      a {
        display: flex;
      }

      height: 60px !important;
      cursor: pointer;
      border: 1px solid #c4c4c4;

      img {
        max-width: 100%;
        min-width: 100%;
        width: auto;
        margin: auto;
        z-index: 1;
      }

      &.swiper-slide-thumb-active {
        border: 1px solid var(--redesign-text-color)
      }
    }

    .swiper-button-next {
      width: 60px;
      left: 0;
      right: initial;
      bottom: 0;
      top: initial;
      position: absolute;
      z-index: 2;
      height: 21px;
      width: 60px;
      opacity: 1;
      display: flex;
      cursor: pointer;
      background: var(--redesign-second-text-color);
      cursor: pointer;
      transition: 0.2s;

      &:hover {
        background: #f1f0f0;
      }

      svg {
        margin: auto;
      }

      &:after {
        display: none;
      }
    }

    .swiper-button-prev {
      width: 60px;
      left: 0;
      right: initial;
      bottom: initial;
      top: 21px;
      position: absolute;
      z-index: 2;
      height: 21px;
      width: 60px;
      opacity: 1;
      display: flex;
      cursor: pointer;
      transform: rotate(-180deg);
      background: var(--redesign-second-text-color);
      transition: 0.2s;

      &:hover {
        background: #f1f0f0;
      }

      svg {
        margin: auto;
      }

      &:after {
        display: none;
      }
    }
  }
}

