      @media (min-width: 1441px) {
        font-size: 13px;
        line-height: 24px;
        letter-spacing: 0;
      }
    }

    button, a {
      display: inline-flex;
      align-items: center;
      justify-content: center;
      flex: 1 0 auto;
      position: relative;
      background-color: var(--redesign-text-color);
      border: none;
      border-radius: 999px;
      padding: 2px 6px 0 0;
      letter-spacing: 0;
      width: 92px;
      height: 24px;
      font-family: 'Factor A', 'Roboto', sans-serif;
      font-style: normal;
      font-weight: 400;
      font-size: 13px;
      line-height: 16px;
      color: var(--redesign-second-text-color);

      @media (min-width: 500px) {
        flex-grow: initial;
      }

      @media (min-width: 992px) {
        display: inline-block;
        width: auto;
        height: auto;
        background-color: transparent;
        padding: initial;
        color: var(--redesign-text-color);
        margin-left: 3px;
        margin-right: 15px;
        font-family: 'Factor A',Roboto,sans-serif;
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 16px;
        letter-spacing: -.24px;

        &::after {
          content: '';
          display: block;
          position: absolute;
          bottom: -1px;
          left: 0;
          width: 100%;
          height: 1px;
          background-color: rgba(0,0,0,.8);

          @media (min-width: 1441px) {
            bottom: 5px;
          }
        }
      }

      @media (min-width: 1441px) {
        font-size: 13px;
        line-height: 24px;
        
        &:hover::after {
          display: none;
        }
      }
    }

    img {
      display: inline-block;
      flex: 1 0 auto;

      @media (min-width: 500px) {
        flex-grow: initial;
      }
    }

    @media (min-width: 993px) {
      margin-top: 0;
    }

    @media (min-width: 1241px) {
      position: absolute;
      bottom: 0;
      left: 0;
    }
  }
}

