      @media (min-width: 768px) {
        margin-top: 20px;
        margin-bottom: 9px;
        font-size: 24px;
        line-height: 28px;
      }
    }

    p {
      display: block;
      margin: 0;
      padding: 0;
      font-family: 'Roboto', sans-serif;
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      line-height: 17px;
      letter-spacing: 0.01em;
      color: rgba(0, 0, 0, 0.6);

      @media (min-width: 768px) {
        font-weight: 700;
        line-height: 18px;
      }
    }

    ul {
      display: flex;
      flex-direction: column;
      row-gap: 12px;
      margin: 16px 0;
      padding: 0;
      list-style: none;

      @media (min-width: 768px) {
        flex-direction: row;
        row-gap: initial;
        column-gap: 20px;
        margin: 19px 0 20px;
      }

      li {
        display: flex;
        align-items: center;

        font-family: 'Roboto', sans-serif;
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;
        letter-spacing: 0.01em;
        color: var(--redesign-text-color);

        @media (min-width: 768px) {
          flex: 1 0 auto;
          font-weight: 700;
          line-height: 18px;
        }

        img {
          display: block;
          margin-right: 12px;
        }

        span {
          position: relative;
          top: 0;

          @media (min-width: 768px) {
            top: 1px;
          }
        }
      }
    }

    ol {
      display: flex;
      column-gap: 4px;
      margin: 0;
      padding: 12px;
      list-style: none;
      background: #f8f8f8;
      border-radius: 12px;

      @media (min-width: 768px) {
        padding: 17px 16px 16px;
        column-gap: 8px;
      }

      li {
        flex: 1 0 auto;
        display: flex;
        flex-direction: column;
        row-gap: 4px;
        position: relative;
        padding-bottom: 21px;
        margin-bottom: 5px;
        font-family: 'Roboto', sans-serif;
        font-style: normal;
        font-weight: 400;
        font-size: 12px;
        line-height: 14px;
        letter-spacing: 0.01em;
        color: rgba(0, 0, 0, 0.4);

        @media (min-width: 768px) {
          row-gap: 5px;
        }

        &::after {
          content: '';
          display: block;
          position: absolute;
          bottom: 0;
          left: 0;
          width: 100%;
          height: 4px;
          background-color: #b1b1b1;
          border-radius: 3px;
        }

        &:first-child {
          color: var(--redesign-text-color);

          &::after {
            background-color: var(--redesign-main-color);
          }
        }

        span {
          &:last-child {
            font-size: 14px;
            line-height: 17px;
          }
        }
      }
    }
  }
}

