.new_card_buy {
  .for-order {
    display: flex;
    align-items: center;
    gap: 10px;
    width: 100%;
    height: 40px;

    color: var(--redesign-second-color);
    font-size: 15px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;

    &::before {
      content: '';
      display: inline-block;
      position: relative;
      width: 25px;
      height: 25px;
      background: transparent no-repeat center;
      background-image: url("data:image/svg+xml;base64,");
    }
  }
  .shipped {
    position: relative;
    display: flex;
    align-items: center;
    gap: 10px;
    width: max-content;
    height: 40px;

    color: var(--redesign-second-color);
    font-size: 15px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;

    &::before {
      content: '';
      display: inline-block;
      position: relative;
      width: 25px;
      height: 25px;
      background: transparent no-repeat center;
      background-image: url("data:image/svg+xml;base64,");
    }

    &:hover {
      .shipped_tooltip {
        display: flex;
      }
    }

    &_tooltip {
      display: none;
      align-items: center;
      column-gap: 5px;
      position: absolute;
      top: -120px;
      left: 0;
      transform: translateX(0);
      width: 200px;
      height: auto;
      border-radius: 3px;
      padding: 11px 15px;
      font-size: 14px;
      line-height: 18px;
      color: var(--redesign-second-text-color);
      background: var(--redesign-second-color);

      @media (min-width: 576px) {
        top: -70px;
        left: 50%;
        transform: translateX(-50%);
        width: 300px;
        height: 56px;
      }

      &:after {
        position: absolute;
        content: '';
        bottom: -22px;
        left: 50%;
        transform: translateX(-50%);
        width: 0;
        height: 0;
        border-left: 25px solid transparent;
        border-right: 25px solid transparent;
        border-top: 38px solid var(--redesign-second-color);
      }

      div {
        position: relative;
        z-index: 1;
      }

      svg {
        flex-shrink: 0;
        display: block;
        width: 25px;
        height: 25px;
        object-fit: contain;
      }
    }
  }
}

