.new_card_sliders {
  &_video {
    position: relative;

    @media (max-width: 450px) {
      justify-content: center;
    }

    .new_card_sliderImg {
      @media (max-width: 450px) {
        order: 1;
      }
    }

    .new_card_sliderDots {
      width: 100%;

      @media (max-width: 450px) {
        order: 3;
        margin: initial;
        max-width: 215px;
      }

      @media (min-width: 451px) {
        padding-top: 132px;
        padding-bottom: 21px;
        max-height: 430px;
      }

      .swiper-button-prev,
      .swiper-button-next {
        @media (min-width: 451px) {
          border: 1px solid #c4c4c4;
        }
      }

      .swiper-button-prev {
        @media (min-width: 451px) {
          top: 123px;
        }
      }
    }

    .show-video {
      display: flex;
      flex-direction: column;
      gap: 20px;
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 60px;
      height: 101px;
      padding: 0;
      background: white;

      @media (max-width: 450px) {
        align-items: center;
        justify-content: center;
        order: 2;
        position: relative;
        width: 70px;
        height: 110px;
        margin-right: 10px;
      }

      &::after {
        content: '';
        display: none;
        width: 100%;
        height: 1px;
        background: #c4c4c4;

        @media (min-width: 451px) {
          display: block;
        }
      }

      a {
        display: block;
        position: relative;
        padding: 0;
        width: 70px;
        height: 70px;
        border: 1px solid #c4c4c4;
        transition: all .2s linear;
        cursor: pointer;

        @media (hover: hover) and (pointer: fine) {
          &:hover {
            border-color: #000;
          }
        }

        @media (min-width: 451px) {
          width: 60px;
          height: 60px;
        }

        img {
          display: block;
          position: absolute;
          z-index: 1;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          object-fit: cover;

          &::before {
            content: '';
            display: block;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: #f5f5f5;
          }
        }

        &::after {
          content: '';
          display: block;
          position: absolute;
          z-index: 2;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background: transparent no-repeat center;
          background-image: url("data:image/svg+xml;base64,");
        }
      }
    }
  }
}
