.fl_chek.color {
	padding-left: 49px !important;
	position: relative;
}
.fl_chek.color:after {
	content: "";
	position: absolute;
	left: 24px;
	top: 3px;
	width: 20px;
	height: 20px;
	border: 1px solid #cccccc;
	border-radius: 50%;
	z-index: 1;
}

