.btn-success, .ecommerce-selected .mse2_selected_link {
  color: #fff;
}

.btn-success, .btn-info, .btn-warning, .btn-primary, .btn-danger, .btn-secondary, .btn-dark {
  &.btn-outline {
    &:hover, &.hover {
      background-color: #fff;
      color: #000;
    }
  }
}

.btn-order {
  background-color: var(--redesign-second-color);
  border-color: var(--redesign-second-color);

  &:hover {
    background-color: var(--redesign-sixth-color);
    border-color: var(--redesign-sixth-color);
  }
}

@each $color, $value in $theme-colors {
  .btn-outline-#{$color} {
    color: #000;
  }
}


.btn_addcart--analogs {
  display: flex;
  justify-content: center;
  align-items: center
}

