.mobile-search {

    height: 60px;
    background-color: #fff;
    box-shadow: 0px -1px 0px rgba(0, 0, 0, 0.25),
        inset 0px -1px 0px #9b9b9b;

    @media (min-width: 993px) {
        display: none;
    }

    .col {
        @media (min-width: 768px) {
            padding: 0;
        }
    }

    &__wrap {
        width: 100%;
        height: 60px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 10px;
    }

    &__burger {
        display: block;
        flex: 1 0 auto;
        border: none;
        margin: 0;
        padding: 0;
        width: 24px;
        height: 24px;
        background: url("data:image/svg+xml;base64,") center no-repeat;
    }

    &__call {
        display: block;
        flex: 1 0 auto;
        border: none;
        margin: 0;
        padding: 0;
        width: 24px;
        height: 24px;
        background-size: 20px 20px !important;
        background: url("data:image/svg+xml;base64,") center no-repeat;
    }

    &__input {
        display: flex;
        align-items: center;
        margin: 0;
        padding: 0;
        width: 100%;
        height: 40px;
        background: #fff;
        overflow: hidden;
        border: none;
        box-shadow: inset 0 0 0 1px #9b9b9b;
        border-radius: 3px;
    }

    &__text {
        display: block;
        padding: 2px 10px 1px;
        width: 100%;
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 18px;
        letter-spacing: 0.01em;
        color: #9b9b9b;
        text-align: left;
    }

    &__lens {
        display: block;
        position: relative;
        right: 1px;
        flex: 1 0 auto;
        width: 39px;
        height: calc(100% - 2px);
        background: var(--redesign-second-color) url("data:image/svg+xml;base64,") center no-repeat;
    }
}