
.headers_user {
  display: flex;
  align-items: flex-start;
  padding-left: 0;

  &_container {
    width: 100%;
    min-height: 70px;
    padding-top: 11px;
  }

  @include media-breakpoint-down(xl) {
    justify-content: space-between !important;
  }

  @include media-breakpoint-down(lg) {
    justify-content: space-around !important;
    margin-left: 0;
    margin-right: 0;
  }


  @include media-breakpoint-down(md) {
    justify-content: space-between !important;
    margin: 0px -7px;
  }


  li {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 68px;
    margin: 0 3px;


    @include desktop() {
      &:first-child {
        margin-left: -7px;
      }
    }
  }

  .headers_user_icon {
    color: #999;
    font-size: 11px;
    position: relative;
    text-align: center;
    width: 94px;
    @include mobile() {
      font-size: 12px;
      zoom: 95%;
    }

    i {
      width: 30px;
      height: 30px;
      min-width: 30px;
      max-width: 30px;
      color: #000;
      background-repeat: no-repeat;
      margin: 5px auto 0 auto;
      display: block;
    }

    /*&.active {
      &:not(.headers_user_icon_full) {
        i {
          filter: $filterPrimary;
        }
      }
    }*/

    .headers_user_icon_filter_none {
      filter: none;
    }

    &:hover {
      opacity: 0.8;
    }

    &_lk {

      i {
        top: -2px;
        position: relative;
