      }

    }

    &_fit {

      i {
        top: -2px;
        position: relative;
        background-image: url("data:image/svg+xml;base64,");
      }

    }

    &_favorite {
      i {
        background-image: url("data:image/svg+xml;base64,");
      }

      &.active i {
        background-image: url("data:image/svg+xml;base64,");
      }

    }

    &_comparison {
      i {
        background-image: url("data:image/svg+xml;base64,");
      }

      &.active i {
        background-image: url("data:image/svg+xml;base64,");
      }

    }

    &_cart {
      i {
        background-image: url("data:image/svg+xml;base64,");
      }

    }


    &_title {
      position: relative;
      top: -3px;
    }

    .badge {
      position: absolute;
      top: 3px;
      right: 6px;
      z-index: 3;
      border-radius: 50%;
      background: var(--redesign-main-color);
      color: #fff !important;
      font-size: 12px;
      padding: 4px 0 4px 0;
      text-align: center;
      font-weight: 200;
      display: none;
      width: 20px;
      height: 20px;
      min-width: 20px;
      max-width: 20px;
      min-height: 20px;
    }

    &:not(.active){
      .badge {
        display: none;
      }
    }

    &.active {
      color: var(--redesign-second-color);

      .badge {
        display: block;
      }
    }


    &.headers_user_icon_full {
      .badge {
        display: none;
      }

      svg {
        width: 40px;
        height: 40px;
      }
    }


  }
}
