.navigation {
    display: none;
    position: relative;
    z-index: 5;
    color: var(--redesign-text-color);

    &.active {
        display: block;
    }

    ul, li { list-style: none; }

    a { color: inherit; }

    .container {
        position: relative;
        max-width: 100%;
        padding-left: 0;

        @media (min-width: 768px) {
            max-width: 768px;
        }

        @media (min-width: 992px) {
            max-width: 960px;
        }
    }

    &__background {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 0;
        background-color: rgba(0,0,0,0.6);
        cursor: pointer;
    }

    &__toggle {
        display: block;
        position: absolute;
        top: 14px;
        right: 10px;
        width: 24px;
        height: 24px;
        z-index: 1;
        cursor: pointer;
        transition: opacity 0.1s linear;

        &::before, &::after {
            content: '';
            display: block;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            transition: opacity 0.1s linear;
        }

        &::before {
            opacity: 1;
            background: url("data:image/svg+xml;base64,") center no-repeat;
        }

        &::after {
            opacity: 0;
            background: url("data:image/svg+xml;base64,") center no-repeat;
        }

        &:hover {
            &::before { opacity: 0; }
            &::after { opacity: 1; }
        }
    }

    &__wrapper {
        position: absolute;
        width: 100%;
        height: auto;
        overflow-x: hidden;
        background-color: white;
    }

    &__body {
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        width: 100%;
        height: auto;
        padding-left: 20px !important;
    }

    &__list {
        display: flex;
        flex-direction: column;
        margin: 0;
        padding: 0 0 46px;
        width: 20.986%;
        min-width: 260px;
        flex: 1 0 auto;
        background-color: #f7f7f7;

        .active {
            background-color: var(--redesign-second-text-color);
        }
    }

    &__item {
        margin: 0;
        padding: 0;
        position: relative;
        z-index: 1;

        &:first-child > a,
        &:first-child > span,
        &:first-child > div {
            border-top: none;
        }
    }

    &__item-caption {
        display: block;
        position: relative;
        text-decoration: none;
        width: 100%;
        height: auto;
        padding: 13px 44px 12px 20px;
        font-weight: 700;
        font-size: 17px;
        line-height: 20px;
        letter-spacing: 0.01em;
        cursor: pointer;
        // border-bottom: 1px solid #bababa;
        background-color: transparent;
        transition: all .1s linear;
        background-color: transparent !important;

        &_arrow {
            &::after {
                content: '';
                display: block;
                position: absolute;
                top: 50%;
                right: 10px;
                width: 24px;
                height: 24px;
                background: url("data:image/svg+xml;base64,") center no-repeat;
                transform: translateY(-50%) scale(1, 1);
            }
        }
    }

    &__sublist {
        display: flex;
        flex-direction: column;
        margin: 0;
        padding: 0;
        position: relative;
        // border-bottom: 1px solid #bababa;
        z-index: 1;
    }

    &__subitem {
        margin: 0;
        padding: 0;
        position: relative;
        z-index: 1;

        &:first-child > a,
        &:first-child > span,
        &:first-child > div {
            border-top: none;
        }
    }

    &__item-subcaption {
        display: block;
        position: relative;
        text-decoration: none;
        width: 100%;
        height: auto;
        padding: 8px 44px 8px 20px;
        font-weight: 400;
        font-size: 15px;
        line-height: 20px;
        letter-spacing: 0.01em;
        cursor: pointer;
        background-color: transparent;
        transition: all .1s linear;

        &:hover {
            background-color: var(--redesign-second-text-color);
        }
    }

    &__menu {
        display: none;
        position: relative;
        margin: 0 -20px 0 0;
        padding: 0;
        width: 100%;
        height: auto;

        &.active {
            display: flex;
        }
    }

    &__column {
        position: relative;
        width: 33.3333333333%;
        height: auto;
        margin: 6px 20px 40px;
        padding: 0;

        &:last-child {
            margin-right: 0;
        }
    }

    &__category {
        margin: 0;
        padding: 0;

        & + ul {
            margin-top: 40px;
        }
    }

    &__title {
        width: 100%;
        height: auto;
        padding: 12px 10px 9px 20px;
        // border-bottom: 1px solid #bababa;
        font-weight: 700;
        font-size: 14px;
        line-height: 18px;
        letter-spacing: 0.01em;
    }

    &__link {

    }

    &__anchor {
        display: block;
        position: relative;
        padding: 12px 10px 10px 20px;
        width: 100%;
        height: auto;
        font-weight: 400;
        font-size: 14px;
        line-height: 18px;
        letter-spacing: 0.01em;
        transition: all .1s linear;

        &:hover {
            background-color: #f8f8f8;
            box-shadow: 0 -1px 0 #bababa, inset 0 -1px 0 #bababa;
        }
    }

    &__accent-link {
        display: flex;
        align-items: center;
        position: relative;
        margin-top: 41px;
        padding: 8px 10px 8px 20px;
        width: 100%;
        height: auto;
        flex-grow: 0;
        font-weight: 700;
        font-size: 14px;
        line-height: 18px;
        letter-spacing: 0.01em;
        color: var(--redesign-second-color) !important;

        &:hover {
            color: #b24512 !important;

            &:after {
                background: url("data:image/svg+xml;base64,") center no-repeat;
            }
        }

        &:after {
            content: '';
            display: inline-block;
            position: relative;
            margin-left: 5px;
            width: 24px;
            height: 24px;
            background: url("data:image/svg+xml;base64,") center no-repeat;
            transition: all .1s linear;
        }
    }
}

