.navigation_main {
    display: block;
    position: fixed;
    top: 0;
    left: -9999px;
    z-index: 1100;
    width: auto;
    height: 100vh;
    overscroll-behavior: none;
    transition: left 0s linear 1s;

    &.active {
        left: 0;
        transition: left 0s linear 0s;

        .navigation__background { opacity: 1; pointer-events: initial; }
        .navigation__wrapper { transform: translateX(0) }
    }

    .navigation__background {
        position: absolute;
        width: 100vw;
        height: 100vh;
        opacity: 0;
        background-color: rgba(0,0,0,.3);
        transition: opacity .3s linear;
        pointer-events: none;
    }

    .navigation__wrapper {
        transform: translateX(calc(-100% - 100px));
        transition: all .5s cubic-bezier(0.16, 1, 0.3, 1);
    }

    .navigation__wrapper,
    .navigation__wrapper .container,
    .navigation__body {
        position: relative;
        width: auto;
        max-width: initial;
        height: 100%;
        max-height: 100%;
        overflow: initial;
        margin: 0;
        padding: 0;
    }

    .navigation__toggle {
        width: 44px;
        height: 44px;
        left: initial;
        top: 0;
        right: -64px;
        background: var(--redesign-second-text-color);
    }

    .navigation__list {
        @extend %custom-scroll-vertical;

        overflow-x: hidden;
        overflow-y: auto;
        overscroll-behavior: contain;

        width: 322px;
        height: 100%;
        background: transparent;
        min-width: initial;
        margin-right: 20px;

        .active {
            background-color: #f8f8f8;
        }
    }

    .navigation__item {
        width: 298px;

        &:first-child {
            z-index: 5;
        }
    }

    .navigation__item-caption {
        padding: 13px 10px 12px 10px;
    }

    .navigation__sublist {
        overflow-x: hidden;
        overflow-y: auto;
        margin-bottom: -1px;
    }

    .navigation__item-subcaption {
        display: flex;
        align-items: center;
        column-gap: 10px;
        padding: 6px 10px;
        font-size: 15px;

        span {
            display: block;
            position: relative;
            width: 100%;
            height: auto;
        }

        mark {
            flex: 1 0 auto;
            display: block;
            position: relative;
            width: 24px;
            height: 24px;
            margin: 0;
            padding: 0;
            background-color: transparent;
            transition: all .1s linear;

            &::before {
                content: '';
                display: block;
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                background-position: center;
                background-size: cover;
                background-repeat: no-repeat;
                background-color: var(--redesign-second-text-color);
                transition: all .1s linear;
                background-image: url("data:image/svg+xml;base64,");
            }

            &.fdk_icon_ljustry::before { background-image: url("data:image/svg+xml;base64,"); }
            &.fdk_icon_podvesnyie-svetilniki::before { background-image: url("data:image/svg+xml;base64,"); }
            &.fdk_icon_potolochnyie-svetilniki::before { background-image: url("data:image/svg+xml;base64,"); }
            &.fdk_icon_trekovye::before { background-image: url("data:image/svg+xml;base64,"); }
            &.fdk_icon_prazdnichnoe-osveshhenie::before { background-image: url("data:image/svg+xml;base64,"); }
            &.fdk_icon_magnitnyie-trekovyie-sistemyi::before { background-image: url("data:image/svg+xml;base64,"); }
            &.fdk_icon_podsvetki::before { background-image: url("data:image/svg+xml;base64,"); }
            &.fdk_icon_ulichnoe-osveschenie::before { background-image: url("data:image/svg+xml;base64,"); }
            &.fdk_icon_nastennyie-svetilniki-i-bra::before { background-image: url("data:image/svg+xml;base64,"); }
            &.fdk_icon_nastolnyie-lampyi-i-nochniki::before { background-image: url("data:image/svg+xml;base64,"); }
            &.fdk_icon_tochechnyie-svetilniki::before { background-image: url("data:image/svg+xml;base64,"); }
            &.fdk_icon_spoty::before { background-image: url("data:image/svg+xml;base64,"); }
            &.fdk_icon_torshery::before { background-image: url("data:image/svg+xml;base64,"); }
            &.fdk_icon_ofisnoe-osveshhenie::before { background-image: url("data:image/svg+xml;base64,"); }
            &.fdk_icon_svetodiodnye-lenty::before { background-image: url("data:image/svg+xml;base64,"); }
            &.fdk_icon_speczializirovannyie-svetilniki::before { background-image: url("data:image/svg+xml;base64,"); }
            &.fdk_icon_lampochki::before { background-image: url("data:image/svg+xml;base64,"); }
            &.fdk_icon_aksessuaryi::before { background-image: url("data:image/svg+xml;base64,"); }
            &.fdk_icon_engineering-systems::before { background-image: url("data:image/svg+xml;base64,"); }
            &.fdk_icon_aksessuaryi-dlya-dekora::before { background-image: url("data:image/svg+xml;base64,"); }
            &.fdk_icon_zerkala::before { background-image: url("data:image/svg+xml;base64,"); }
            &.fdk_icon_posuda::before { background-image: url("data:image/svg+xml;base64,"); }
            &.fdk_icon_mebel::before { background-image: url("data:image/svg+xml;base64,"); }
            &.fdk_icon_nastennyij-dekor::before { background-image: url("data:image/svg+xml;base64,"); }
            &.fdk_icon_nastolnyij-dekor::before { background-image: url("data:image/svg+xml;base64,"); }
            &.fdk_icon_napolnyij-dekor::before { background-image: url("data:image/svg+xml;base64,"); }
            &.fdk_icon_tekstil::before { background-image: url("data:image/svg+xml;base64,"); }
            &.fdk_icon_xranenie::before { background-image: url("data:image/svg+xml;base64,"); }
            &.fdk_icon_chasyi-kartinyi::before { background-image: url("data:image/svg+xml;base64,"); }
            &.fdk_icon_bloki-pitaniya::before { background-image: url("data:image/svg+xml;base64,"); }
            &.fdk_icon_upravlenie-svetom::before { background-image: url("data:image/svg+xml;base64,"); }
            &.fdk_icon_istochniki-sveta::before { background-image: url("data:image/svg+xml;base64,"); }
            &.fdk_icon_sistemyi-osveshheniya::before { background-image: url("data:image/svg+xml;base64,"); }
        }

        &.active mark::before { background-color: #f8f8f8; }
    }

    .navigation__menu {
        width: 644px;
        margin: 0;
        padding: 0;
    }

    .navigation__column {
        @extend %custom-scroll-vertical;

        width: 100%;
        max-width: initial;
        margin: 0;
        padding: 6px 0 40px 0;
        overflow-x: hidden;
        overflow-y: auto;
        overscroll-behavior: none;

        &:first-child {
            flex: 1 0 auto;
            padding-right: 10px;
            max-width: 292px;
        }
    }

    .navigation__anchor {
        box-shadow: none;
    }

    .navigation__preview {
        display: block;
        position: relative;
        margin: 34px auto auto 31px;
        width: 296px;
        height: 297px;
        border-radius: 38px;
        overflow: hidden;

        &:hover {
            img {
                filter: brightness(90%);
                transform: scale(1.02);
                will-change: transform;
            }
        }

        img {
            display: block;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            object-fit: cover;
            border-radius: 38px;
            outline: 0 !important;
            border: none !important;
            transition: all .2s linear;

            &::before {
                content: '';
                display: block;
                position: absolute;
                top: -1px;
                left: -1px;
                width: calc(100% + 2px);
                height: calc(100% + 2px);
                border-radius: 38px;
                overflow: hidden;
                background-position: center;
                background-size: cover;
                background-repeat: no-repeat;
                background-image: url("data:image/svg+xml;base64,");
                background-color: #fcfcfc;
            }
        }
    }

    .navigation__title {
        a {
            font-size: inherit;
            font-weight: inherit;
            transition: color .1s linear;
            color: var(--redesign-second-color);

            &:hover {
                color: #2399e2;
            }
        }
    }
}

%custom-scroll-vertical {
    //-   firefox
    // scrollbar-color: #666666 transparent;
    // scrollbar-width: thin;

    //- 
    &::-webkit-scrollbar {
        width: 4px;
        height: 0;
        background: transparent;
        border-radius: 999px;
        cursor: pointer;
    }

    //-     
    &::-webkit-scrollbar-button {
        display: block;
        width: 4px;
        height: 3px;
        background-color: var(--redesign-second-text-color);
    }

    //-  ,     .
    &::-webkit-scrollbar-track {
        background: #d8d8d8;
        border-radius: 4px;
    }

    //-   
    &::-webkit-scrollbar-track-piece {
    }

    //-   ,     .
    &::-webkit-scrollbar-thumb {
        background-color: #bababa;
        border-radius: 4px;

        &:hover {
            background-color: #666666;
        }
    }

    //-     
    &::-webkit-scrollbar-corner {
        display: none;
    }

    //-    .
    &::-webkit-resizer {
    }
}