.ecommerce-header {
  /* @include desktop() {
     margin-bottom: 20px;
   }
 */
  border-bottom: 1px solid #ccc;
  padding-bottom: 20px;

  .btn {
    padding: 9px 28px 9px 13px;
    max-height: 40px;
    font-size: 13px;

    &:not(.active) {
      border-color: #999;

      /* &:hover {
         background-color: #fff !important;
         color: #000 !important;
       }   */
    }

  }

  &-items {
    > div {
      margin-right: 17px;

      &:last-child {
        margin-right: 0;
      }
    }
  }

  &__separator {
    @include mobile() {
      display: none !important;
    }
  }

  &__sort {
    margin-right: 20px !important;

    @include mobile() {
      display: none !important;
    }

    .dropdown-menu {
      margin-top: 2px;
      font-size: 13px;
      width: 100%;
      padding: 0;
      border-color: #999999;


      .dropdown-item {
        position: relative;
        padding: 9px 10px 9px 40px;
        font-size: 14px;
        color: #000;

        &:hover, &:focus {
          background-color: #F7F7F7;
        }
      }

      li {
        border-bottom: 0.5px dashed #999999;

        &:last-child {
          border-bottom: 0;
        }
      }
    }


    .open {
      .dropdown-menu {
        display: block;
      }

      .ecommerce-header__sort__btn {
        border-color: var(--redesign-main-color) !important;
        background-color: #fff !important;
        color: #000 !important;
      }
    }

    &__btn {

      cursor: pointer;
      padding-left: 40px !important;
      text-align: left;
      @include desktop() {
        min-width: 240px;
      }

      &:hover {
        border-color: var(--redesign-main-color) !important;
        background-color: #fff !important;
        color: #000 !important;

        &.active {
          border-color: var(--redesign-main-color) !important;
          color: #fff !important;
          background-color: #fff !important;
        }
      }

      &.active {
        background-color: #fff !important;
        color: #000 !important;
        border-color: #ccc !important;
      }
    }

    &__name {
      font-size: 14px;
    }

    &__icon {
      position: absolute;
      background-repeat: no-repeat;
      width: 21px;
      height: 20px;
      left: 10px;
      top: 13px;
      background-image: url("data:image/svg+xml;base64,");
      -webkit-transition: .3s;
      transition: .3s;
    }

    &__asc {
      .ecommerce-header__sort__icon {
        transform: rotate(180deg);
        top: 8px;
      }
    }
  }


  &__view {
    margin-right: 0 !important;

    @include mobile() {
      margin-right: 20px !important;
    }

    &__btn {
      &-grid-view, &-list-view {
        &:after {
          content: "";
          position: relative;
          width: 30px;
          height: 30px;
          background-repeat: no-repeat;
          display: block;
        }

        &:not(.active) {
          &:hover {
            background-color: #eee;
          }
        }
      }

      &-list-view {
        padding-right: 5px !important;

        &:after {
          background-image: url("data:image/svg+xml,%3Csvg width='30' height='30' viewBox='0 0 30 30' fill='none' xmlns='http://www.w3.org/2000/svg'%3E %3Ccircle cx='3.75' cy='6.75' r='1.75' fill='%23999999'/%3E %3Crect x='8' y='5' width='20' height='3.5' rx='1.75' fill='%23999999'/%3E %3Ccircle cx='3.75' cy='14.9502' r='1.75' fill='%23999999'/%3E %3Crect x='8' y='13.2002' width='20' height='3.5' rx='1.75' fill='%23999999'/%3E %3Ccircle cx='3.75' cy='23.1499' r='1.75' fill='%23999999'/%3E %3Crect x='8' y='21.3999' width='20' height='3.5' rx='1.75' fill='%23999999'/%3E %3C/svg%3E ");


        }

        &.active {
          &:after {
            background-image: url("data:image/svg+xml,%3Csvg width='30' height='30' viewBox='0 0 30 30' fill='none' xmlns='http://www.w3.org/2000/svg'%3E %3Ccircle cx='3.75' cy='6.75' r='1.75' fill='white'/%3E %3Crect x='8' y='5' width='20' height='3.5' rx='1.75' fill='white'/%3E %3Ccircle cx='3.75' cy='14.9502' r='1.75' fill='white'/%3E %3Crect x='8' y='13.2002' width='20' height='3.5' rx='1.75' fill='white'/%3E %3Ccircle cx='3.75' cy='23.1499' r='1.75' fill='white'/%3E %3Crect x='8' y='21.3999' width='20' height='3.5' rx='1.75' fill='white'/%3E %3C/svg%3E ");
          }
        }
      }


      &-grid-view {
        padding-left: 5px !important;

        &:after {
          background-image: url("data:image/svg+xml,%3Csvg width='30' height='30' viewBox='0 0 30 30' fill='none' xmlns='http://www.w3.org/2000/svg'%3E %3Crect x='16' y='16' width='9' height='9' fill='%23999999'/%3E %3Crect x='5' y='16' width='9' height='9' fill='%23999999'/%3E %3Crect x='16' y='5' width='9' height='9' fill='%23999999'/%3E %3Crect x='5' y='5' width='9' height='9' fill='%23999999'/%3E %3C/svg%3E ");
        }


        &.active {
          &:after {
            background-image: url("data:image/svg+xml,%3Csvg width='30' height='30' viewBox='0 0 30 30' fill='none' xmlns='http://www.w3.org/2000/svg'%3E %3Crect x='16' y='16' width='9' height='9' fill='white'/%3E %3Crect x='5' y='16' width='9' height='9' fill='white'/%3E %3Crect x='16' y='5' width='9' height='9' fill='white'/%3E %3Crect x='5' y='5' width='9' height='9' fill='white'/%3E %3C/svg%3E ");
          }
        }
      }
    }

    @include mobile() {
      margin-left: 0;
    }

    .btn {
      padding: 4px;
      @include mobile() {
        border-radius: 3px !important;
        display: none;
        &.active {
          display: block;

        }
      }
    }

    svg {
      width: 18px;
      height: 18px;
    }

  }

  &__button_filter {
    @include desktop() {
      display: none;
    }

    .btn {
      font-size: 18px;
      position: relative;

      &:before {
        content: "";
        background-image: url("data:image/svg+xml;base64,");
        position: absolute;
        width: 16px;
        height: 16px;
        top: 10px;
        left: 10px;
      }
    }

    &__btn {
      position: relative;
      top: -5px;
    }

    &__badge {
      margin-left: 5px;
      color: $primary;
      font-weight: bold;
    }
  }

  &__total {

    @include mobile() {
      display: none;
    }

    .btn {
      cursor: default;
      background-color: #fff !important;
      color: #000 !important;
      padding-right: 13px;
      padding-left: 13px;
      text-align: center;
    }

  }

  &_collections {
    .ecommerce-header-items {
      flex-wrap: wrap;

      @media (min-width: 768px) {
        flex-wrap: nowrap;
      }

      @media (min-width: 992px) {
        flex-wrap: wrap;
      }

      @media (min-width: 1200px) {
        flex-wrap: nowrap;
      }

      .ecommerce-header {
        &__separator {
          display: flex !important;
          align-items: center;
          justify-content: center;
          order: 1;
          width: 100%;
          height: auto;
          margin: 20px 0 0;
          padding-left: 60px;

          @media (min-width: 768px) {
            justify-content: flex-start;
            order: initial;
            margin: 0 20px 0 0;
            width: auto;
            padding: 0;
          }

          @media (min-width: 992px) {
            justify-content: center;
            order: 1;
            width: 100%;
            margin: 20px 0 0;
          }

          @media (min-width: 1200px) {
            justify-content: flex-start;
            order: initial;
            margin: 0 20px 0 0;
            width: auto;
            padding: 0;
          }

          .toggle-collections {
            display: flex;
            align-items: center;
            column-gap: 10px;
            position: relative;
            width: auto;
            height: 40px;
            pointer-events: none;

            mark {
              display: flex;
              align-items: center;
              width: auto;
              height: auto;
              padding: 0;
              background-color: transparent;
              font-weight: 400;
              font-size: 14px;
              line-height: 24px;
              transition: all .1s linear;
            }

            input {
              position: absolute !important;
              width: 1px !important;
              height: 1px !important;
              margin: -1px !important;
              border: 0 !important;
              padding: 0 !important;
              white-space: nowrap !important;
              clip-path: inset(100%) !important;
              clip: rect(0 0 0 0) !important;
              overflow: hidden !important;
              z-index: -1 !important;

              & ~ mark:first-of-type {
                color: #666666;
              }

              & ~ mark:last-of-type {
                color: black;
              }
            }

            input:checked {
              & ~ span::before {
                left: 3px;
              }

              & ~ mark:first-of-type {
                color: black;
              }

              & ~ mark:last-of-type {
                color: #666666;
              }
            }

            span {
              display: block;
              position: relative;
              width: 51px;
              height: 24px;
              border-radius: 999px;
              background: var(--redesign-main-color);
              pointer-events: auto;
              cursor: pointer;

              &::before {
                content: '';
                display: block;
                position: absolute;
                top: 50%;
                left: 30px;
                transform: translateY(-50%);
                width: 18px;
                height: 18px;
                background-color: white;
                border-radius: 50%;
                transition: all .1s ease-out;
              }
            }
          }
        }
      }
    }
  }
}

.ecommerce-selected {

  #mse2_selected {
    @extend .d-flex;
    @extend .flex-nowrap;

    gap: 10px;
    margin: 20px 0 0 0;
    white-space: nowrap;
    overflow: hidden;

    /*           */
    user-select: none;
    /*    ,        X */
    // touch-action: pan-y;

    // @include media-breakpoint-up(xxl) {
    //   margin-left: 20px;
    //   margin-right: 20px;
    // }
  }

  .mse2_selected_link {
    @extend .btn;
    @extend .btn-sm;
    @extend .btn-success;

    cursor: pointer;
    font-style: italic;
    font-size: 12px;
    color: #fff;
    margin-bottom: 10px;

    &:first-child {
      margin-left: 0;
    }

  }

  sup {
    background-image: url("data:image/svg+xml;base64,");
    width: 8px;
    height: 8px;
    position: relative;
    display: inline-block;
    margin-left: 5px;
    top: 0px;
  }
}

