.add-mark {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 5px;
    position: absolute;
    z-index: 10;
    top: 10px;
    right: 10px;
    padding: 2px 5px;
    background-color: rgba(0,0,0, .2);
    border-radius: 3px;
    cursor: pointer;

    @media (min-width: 768px) {
        padding: 3px 10px;
    }

    &.hidden {
        display: none;
    }

    &__icon {
        display: block;
        width: 15px;
        height: 15px;
        background-color: transparent;
        background-repeat: no-repeat;
        background-position: center;
        background-image: url("data:image/svg+xml;base64,");

        @media (min-width: 768px) {
            width: 20px;
            height: 20px;
            background-image: url("data:image/svg+xml;base64,");
        }
    }

    &__title {
        display: block;
        color: white;
        font-size: 9px;
        font-style: normal;
        font-weight: 500;
        line-height: normal;
        letter-spacing: 0;

        @media (min-width: 768px) {
            font-size: 10px;
        }
    }

    &__tooltip {
        display: none;
        flex-direction: row;
        gap: 10px;
        position: relative;
        width: auto;
        height: auto;
        background-color: white;
        border-radius: 3px;
        padding: 6px 6px 7px 7px;
        cursor: default;
        position: absolute;
        bottom: 0;
        right: 0;
        transform: translateY(calc(100% + 21px));

        &.show {
            display: flex;
        }

        @media (min-width: 768px) {
            padding: 10px 5px 10px 9px;
        }

        &::before {
            content: '';
            display: block;
            position: absolute;
            top: -19px;
            right: 20px;
            width: 23px;
            height: 24px;
            background-color: transparent;
            background-position: center;
            background-repeat: no-repeat;
            background-image: url("data:image/svg+xml;base64,");
        }
    }

    &__content {
        display: flex;
        flex-direction: column;

        @media (min-width: 768px) {
            gap: 5px;
        }
    }

    &__close {
        display: block;
        position: relative;
        width: 15px;
        height: 15px;
        border: none;
        background-color: transparent;
        background-repeat: no-repeat;
        background-position: center;
        background-image: url("data:image/svg+xml;base64,");
        cursor: pointer;

        @media (min-width: 768px) {
            width: 20px;
            height: 20px;
            background-image: url("data:image/svg+xml;base64,");
        }
    }

    &__company {
        display: block;
        position: relative;
        white-space: nowrap;
        color: #000;
        font-size: 9px;
        font-style: normal;
        font-weight: 400;
        line-height: 2;
        letter-spacing: 0.09px;

        @media (min-width: 768px) {
            font-size: 10px;
            line-height: 1.8;
            letter-spacing: 0.1px;
        }
    }

    &__copy {
        display: flex;
        align-items: center;
        gap: 10px;
        position: relative;
        border: none;
        background-color: transparent;
        color: var(--redesign-second-color);
        font-size: 9px;
        font-style: normal;
        font-weight: 400;
        line-height: 2;
        letter-spacing: 0.09px;
        margin: 0;
        padding: 0;
        cursor: pointer;

        @media (min-width: 768px) {
            gap: 5px;
            font-size: 10px;
            line-height: 1.8;
            letter-spacing: 0.1px;
        }

        &.disabled {
            pointer-events: none;
            i {
                background-image: url("data:image/svg+xml;base64,");
            }
        }

        i {
            display: block;
            position: relative;
            width: 15px;
            height: 15px;
            background-color: transparent;
            background-repeat: no-repeat;
            background-position: center;
            background-image: url("data:image/svg+xml;base64,");

            @media (min-width: 768px) {
                top: -1px;
            }
        }
    }
}

.service-banner__card {
    .add-mark {
        @media (min-width: 992px) {
          top: 71px;
          right: 31px;
        }
    }
}