.terms_filters_store label.fl_chek span {
  white-space: normal !important;
  line-height: 13px;
  font-size: 13px;
  display: block;
  max-width: 191px;
}
.main_filters {
  margin-bottom: 20px;
}
.ncatlist #mse2_marker_0 + label:before,
.ncatlist #mse2_marker_1 + label:before {
  background-image: url("data:image/svg+xml;base64,") !important;
  background-color: var(--redesign-main-color) !important;
  border-color: var(--redesign-main-color) !important;

}
.ncatlist #mse2_marker_0:checked + label:before,
.ncatlist #mse2_marker_1:checked + label:before {
    background-image: none !important;
    background-color: transparent !important;
    border: 1px solid #b1b1b1 !important;
}
@media screen and (min-width: 768px) {
  .ncatlist .ncatcheckbox + label:before {
    //top: 7px;
  }
}
@media screen and (max-width: 768px) {
  .main_filters .clean_filters {
    display: none;
  }

  .ncatlist .newfilterj .ncatfiltersector.ncatfiltersectorbtn {
    padding: 0;
  }

  .ncatlist .ncatfiltersectorbtn .df {
    width: calc(100%);
  }

  .ncatslrange {
    margin-right: 15px !important;
    margin-left: 20px !important;
    margin-top: 20px !important;
  }
  .ncatslrange .noUi-horizontal .noUi-handle {
    width: 25px;
    height: 25px;
  }
  .ncatslrange .noUi-connects {
    height: 12px;
  }

  #mse2_filters .mse2_number_slider {
    margin-bottom: 36px;
  }
}

.select_li_choice_parent.selected {
  font-weight: 700;
}

.catalog-list {
  overflow: hidden;

  &__filter {
    @media (min-width: 992px) {
      padding: 0;
      width: 300px;
    }
  }

  &__items {
    @media (min-width: 992px) {
      padding: 0 0 0 20px;
      width: calc(100% - 300px);
    }
  }

  div.grid-view.wishlist-items {
    @media (min-width: 992px) and (max-width: 1199px) {
      grid-template-columns: 1fr 1fr;
    }

    @media (min-width: 1200px) and (max-width: 1439px) {
      grid-template-columns: 1fr 1fr 1fr;
    }
  }
}

// From default/_custom.scss
.filters_product_banner {
  position: relative;
  text-align: center;
  width: 100%;
  margin-top: 10px;
  margin-bottom: 5px;
}

.filters_product_banner img {
  width: 100%;
}

// From inline page styles
.filters_product_banner {
  position: relative;
  height: 275px;
  grid-row-start: 8;
  grid-row-end: 3;
  grid-column-start: 1;
  grid-column-end: 5;
}

.grid-view .filters_product_banner {
  height: auto;
  margin-top: 20px;
  grid-row-start: 3;
  grid-column-end: 4;
  border-bottom: 1px solid #ccc;

  @media (min-width: 992px) {
    grid-column-end: 3;
    margin-top: 0;
  }

  @media (min-width: 1200px) {
     grid-column-end: 4;
  }

  @media (min-width: 1440px) {
    grid-column-end: 5;
  }
}

.list-view .filters_product_banner {
  grid-column-start: 1;
  grid-column-end: 1;
  border-top: 1px solid #ccc;
}

.grid-view .filters_product_banner img {
  margin-bottom: 20px;

  @media (min-width: 992px) {
    margin-bottom: 40px;
  }
}

.tlist-view .filers_product_banner img {
  margin-top: 20px;
}

@media (max-width: 767px) {
  .filters_product_banner {
    display: none !important;
  }
}

//       
.catalog-list__filter_lite {

  @media (min-width: 992px) {
    display: flex;
    flex-direction: column;
  }

  .listing_filters_wrapperHeadline {
    font-size: 17px;
    line-height: 20px;
    letter-spacing: .01em;
    border: none;
    padding: 20px 20px 20px 0;
    text-transform: uppercase;

    &::after {
      width: 24px;
      height: 24px;
      background: url("data:image/svg+xml;base64,");
      right: 20px;
      top: 18px;
    }

    &.active {
      &::after {
      background-image: url("data:image/svg+xml;base64,");
      }
    }
  }

  .ncatfiltersector.ncatfiltersector_top_filter {
    box-shadow: none !important;
    padding-left: 0 !important;

    &::before {
      display: none !important;
    }
  }

  .ncatfiltersector_top_filter {

    box-shadow: none !important;
    padding-left: 0 !important;

    .listing_filters_wrapperHeadline {
      padding: 10px 20px 10px 0;
      cursor: default;
      pointer-events: none;

      &::after {
        display: none;
      }
    }

    .listing_filters_wrapper {
      box-shadow: none !important;

      &::before {
        display: none !important;
      }
    }

    .listing_filters_wrapperList {
      padding-bottom: 0;
    }
  }

  .listing_filters_slide_choice {
    background-color: transparent;
    font-weight: 600;
    font-size: 14px;
    line-height: 18px;
    letter-spacing: 0.01em;
    border: none;
    padding-right: 20px;

    &::after {
      display: none;
    }

    a {
      padding: 7px 0 5px 35px;
      position: relative;
      transition: all .1s linear;

      &::after {
        content: '';
        display: block;
        position: absolute;
        width: 24px;
        height: 24px;
        background-image: url("data:image/svg+xml;base64,");
        right: initial;
        background-color: transparent;
        top: 3px;
        left: 0;
      }

      &.selected {
        background-color: #f8f8f8;
        cursor: default;

        &::after {
          background-image: url("data:image/svg+xml;base64,");
        }
      }

      @media (min-width: 1200px) {
        &:not(.selected):hover {
          color: var(--redesign-main-color);
        }
      }
    }

    &.toggle {
      
      a {
        &::after {
          background-image: url("data:image/svg+xml;base64,");
        }
      }
    }
  }

  .select_li_choice {
    background-color: transparent;
    border: none;
    line-height: 18px;
    letter-spacing: 0.01em;
    color: #666666;

    &::after {
      display: none;
    }

    a {
      padding: 7px 20px 5px 50px;
      &:hover {
        color: var(--redesign-main-color);
      }
    }

    @media (min-width: 1200px) {
      &:hover {
        color: var(--redesign-main-color);
      }
    }
  }

  .listing_filters_wrapper {
    position: relative;
    background: #ffffff;
    margin-bottom: 20px;
    box-shadow: 0 0 0 rgba(0, 0, 0, 0);
    

    &::before {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      left: -20px;
      width: calc(100% + 20px);
      height: 100%;
      box-shadow: 0 0 0 rgba(0, 0, 0, 0);
      opacity: 0;
      transition: all .1s linear;
    }

    @media (min-width: 1200px) {
       &:hover {
        &::before {
          opacity: 1;
          box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
        }   
       }
    }
  }

  .ncatfiltersector {
    position: relative;
    padding: 0 20px !important;
    transition: all .1s linear;

    &::before {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      left: -20px;
      width: calc(100% + 20px);
      height: 100%;
      box-shadow: 0 0 0 rgba(0, 0, 0, 0);
      opacity: 0;
      transition: all .1s linear;
    }

    @media (min-width: 1200px) {
      &:hover {
        &::before {
          opacity: 1;
          box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
        }   
      }
    }
  }

  .ncatfiltersectoropener_inline {
    
    .ncatfilterun {
      display: flex;
      align-items: center;
    }

    .ncatfiltit {
      flex: 1 0 auto;
      width: 100px;
      margin-right: 10px !important;
      padding: 6px 0 4px!important;
      font-weight: 400 !important;
      font-size: 14px !important;
      line-height: 18px;
      letter-spacing: .01em;
      color: #666;
      pointer-events: none;

      &::before {
        display: none;
      }
    }

    .ncatfilcontainer {
      display: block !important;
    }
  }

  .listing_filters_wrapperList {
    padding-bottom: 40px;
  }

  .ncatfiltit {
    margin: 0 !important;
    padding: 12px 0 10px !important;
    font-weight: 600;
    font-size: 14px !important;
    line-height: 18px;
    letter-spacing: .01em;

    &::before {
      width: 24px;
      height: 24px;
      background: url("data:image/svg+xml;base64,");
      right: 0;
      top: 14px;
      transform: rotate(0deg) !important;
    }

    &.active {
      &::before {
        background-image: url("data:image/svg+xml;base64,");
        transform: rotate(90deg) !important;
      }
    }
  }
  
  #mse2_filters .ncatfilcontainer,
  .ncatfilcontainer {
    margin: 0 !important;
    padding: 0 0 5px 0 !important;
  }

  .ncatcheckbox + label, 
  .ncatradio + label {
    display: flex;
    align-items: center;
    margin: 0 !important;
    padding: 10px 0 9px 35px !important;

    &.color {
      padding-left: 60px !important;

      &::after {
        left: 34px;
        top: 10px;
        border-color: #b1b1b1;
      }
    }

    span, sup {
      margin: 0 !important;
      font-weight: 400;
      font-size: 14px;
      line-height: 18px;
      letter-spacing: 0.01em;
      transition: .2s;
      max-width: initial;
    }

    span {
      color: #666666;
    }

    sup {
      color: #bababa;
      top: initial;
      left: initial;
    }

    &.zero_result {
      cursor: default;
      pointer-events: none;
      opacity: 1;

      span, sup {
        color: #d8d8d8 !important;

        img {
          opacity: 0.4;
          filter: grayscale(100%);
        }
      }

      &::before {
        background-color: #b1b1b1 !important;
        border-color: #b1b1b1 !important;
        background-image: none !important;
        opacity: 0.25;
      }
    }
  }

  .ncatcheckbox + label {
    &::before {
      width: 20px;
      height: 20px;
      border: 1px solid #b1b1b1;
      border-radius: 3px;
      background: none;
      left: 0px;
      top: 10px;
      background-repeat: no-repeat !important;
      background-position: center !important;
      background-image: none !important;
    }

    @media (min-width: 1200px) {
      &:hover {
        span {
          color: var(--redesign-main-color);
        }
      }
    }
  }

  .ncatcheckbox:checked + label {
    &:before {
      background-image: url("data:image/svg+xml;base64,") !important;
      background-color: var(--redesign-main-color) !important;
      border-color: var(--redesign-main-color) !important;
    }
  }

  #mse2_filters .ncatslrange .polzun_input {
    display: flex !important;
    align-items: center !important;
    column-gap: 10px;
    margin: 0 !important;
    padding: 0 !important;
    height: 46px;

    &::after {
      display: none;
    }
  }

  .ncatslrange .polzun_input_el {
    margin: 0;
    padding: 0;
    min-width: initial;
    max-width: initial;

    input {
      padding: 1px 11px 0;
      margin: 0;
      height: 30px;
      border: 1px solid #b1b1b1 !important;
      // box-shadow: inset 0 0 0 1px #b1b1b1;
      border-radius: 4px;
      font-weight: 400;
      font-size: 14px;
      line-height: 18px;
      letter-spacing: 0.01em;
      color: #666666;
      appearance: none !important;
      box-sizing: border-box !important;

      &:hover, &:focus, &:active {
        border: 1px solid var(--redesign-main-color) !important;
        // box-shadow: inset 0 0 0 2px #5dac0d;
      }
    }
  }

  .ncatfilcontainer .the_search {
    margin: 0;
    padding: 8px 0;

    input {
      padding: 1px 10px 0;
      height: 30px;
      border: 1px solid #b1b1b1 !important;
      // box-shadow: inset 0 0 0 1px #b1b1b1;
      border-radius: 4px;
      font-weight: 400;
      font-size: 14px;
      line-height: 18px;
      letter-spacing: 0.01em;
      transition: all .1s linear;
      color: #666666;
      // color: #b1b1b1;

      &:hover, &:focus, &:active {
        border: 1px solid var(--redesign-main-color) !important;
        // box-shadow: inset 0 0 0 2px #5dac0d;
      }
    }

    .btn_reset_search {
      top: 8px;
    }
  }

  .ncatfilcheck_marker {
    margin: 0;
    padding-left: 20px;
    padding-right: 20px;

    hr {
      margin: 9px 0 10px;
    }
  }

  .ncatslrange {
    padding: 0 20px !important;
  }

  .main_filters .fl_chek span > img {
    height: 20px;
  }

  #mse2_filters .mse2_number_slider {
    display: block !important;
    margin-top: 19px !important;
    margin-bottom: 19px !important;
    border: none !important;
  }

  .ncatslrange .noUi-horizontal .noUi-handle {
    width: 20px;
    height: 20px;
    top: -9px;
    right: -10px;
    background: white;
    border: 2px solid var(--redesign-main-color);
  }

  .ncatslrange .noUi-connect {
    background: var(--redesign-main-color);
    height: 2px !important;
  }

  .ncatslrange .noUi-connects {
    height: 2px !important;
  }

  .apply-filters-float-btn {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    row-gap: 4px;
    width: 123px;
    height: 58px;
    right: -133px;
    background: var(--redesign-main-color) !important;
    padding: 4px 2px 0px 2px;
    font-weight: 400;
    line-height: 18px;
    letter-spacing: 0.01em;
    color: white;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.1) !important;

    @media (max-width: 992px) {
      right: 10px;
    }

    @media (min-width: 1200px) {
      &:hover {
        .sk-link::after {
          opacity: 0;
        }
      }
    }

    &:before {
      left: 9px;
      top: initial;
      bottom: -10px;
      width: 30px;
      height: 12px;
      transform: none !important;
      border-radius: 0;
      box-shadow: none !important;
      background-color: transparent !important;
      background-position: center;
      background-repeat: no-repeat;
      background-image: url("data:image/svg+xml;base64,");

      @media (min-width: 992px) {
        background: var(--redesign-main-color) !important;
        left: -6px;
        top: 23px;
        bottom: initial;
        width: 13px;
        height: 13px;
        border-radius: 2px;
        transform: rotate(135deg) !important;
      }
    }

    &::after {
      display: none;
    }

    .sk-wave {
      position: relative;
      width: 100%;
      height: 18px;
      top: initial;
      left: initial;
      height: auto;
      margin: initial;
    }

    .sk-rect {
      background-color: white;
      height: 15px;
      margin: 0 2px;
      width: 15px;
      display: inline-block;
      border-radius: 50%;
      animation-name: sk-wave-stretch-delay-custom;
    }

    .sk-link {
      position: relative;
      font-weight: 400;
      font-size: 14px;
      line-height: 18px;
      letter-spacing: 0.01em;
      color: white;

      &::after {
        content: '';
        display: block;
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 0;
        border-bottom: 1px dashed white;
        opacity: 1;
      }
    }

    .sk-result {
      display: flex;
      column-gap: 3px;
    }

    @keyframes sk-wave-stretch-delay-custom {
      0%,
      40%,
      100% { transform: scale(0.4); }
      20%  { transform: scale(1);   }
    }
  }


  .newfilterj .check_wrap {
    height: auto !important;
    max-height: 240px;
    overflow: hidden;
    transition: all .25s linear;
    padding-left: 0 !important;

    &.dropped {
      max-height: 480px;
    }

    &:has(.terms_filter_empty_results) + .show-more {
      display: none;
    }

    &:has(.terms_filter_empty_results) + .filter_more {
      display: none;
    }
  }

  .ncatfilterun {
    max-height: initial;
  }

  .ps:hover > .ps__rail-y {
    opacity: 1 !important;
  }

  .ps__rail-y {
    opacity: 1 !important;

    width: 4px !important;    
    background-color: #d8d8d8 !important;
    border-radius: 4px;
    right: 0px !important;

    .ps__thumb-y {
      width: 4px !important;
      background: #b1b1b1 !important;
      border-radius: 4px !important;
      right: 0 !important;
    }
  }

  .show-more {
    display: flex;
    align-items: center;
    position: relative;
    z-index: 1;
    padding: 2px 0 0;
    margin: 0;
    width: 100%;
    height: 40px;
    border: none;
    box-shadow: none;
    background-color: transparent;
    transition: all .1s linear;
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    letter-spacing: 0.01em;
    color: var(--redesign-main-color);

    &::before {
      content: ' ';
      display: inline;
    }

    &::after {
      content: '';
      display: none;
    }

    &.show {
      &::before {
        display: none;
      }
      &::after {
        display: inline;
      }
    }

    @media (min-width: 1200px) {
      &:hover {
        color:var(--redesign-second-color);
      }
    }
  }

  input:focus::-webkit-input-placeholder {
    color: transparent
  }

  input:focus::-moz-placeholder {
    color: transparent
  }

  input:focus:-moz-placeholder {
    color: transparent
  }

  input:focus:-ms-input-placeholder {
    color: transparent
  }

  .ncatfiltit.ncatfiltit_price {
    span {
      font-size: 17px;
      line-height: 20px;
      letter-spacing: .01em;
      border: none;
      padding: 20px 20px 20px 0;
      text-transform: uppercase;
    }
  }

  .listing_filters_wrapper.listing_filters_wrapper_price {
    .listing_filters_wrapperHeadline {
      pointer-events: none;

      &::after {
        display: none;
      }
    }

    .listing_filters_wrapperList {
      display: block !important;
    }
  }

  .noUi-connect, .noUi-origin {
    right: 10px;
    width: calc(100% - 20px);
  }

  .terms_filter_empty_results {
      position: relative;
      top: 0;
      margin-bottom: 10px;
  }

  .check_ps_scroll .ncatfilcontainer {
      border: none;
  }

  .ncatfiltersectorbtn .df {
    position: relative;
  }

  &.updated {
    .main_filters {
      margin-bottom: 35px;
    }

    .listing_filters_wrapper {
      margin: 0;
    }

    .ncatfiltersector {
      padding: 0 20px 0 35px !important;

      //    
      &::before {
        display: none;
      }
    }

    .ncatcheckbox + label {
      @media (hover: hover) and (pointer: fine) {
          &:hover span {
            color: var(--redesign-second-color);
          }
      }
    }

    .ncatfilcontainer .the_search {
      &.toggleable {
        height: 0;
        overflow: hidden;
        padding: 0;
        opacity: 0;
        transition: all .3s linear;

        &.show {
          height: 46px;
          padding: 8px 0;
          opacity: 1;
        }
      }
    }

    .ps__rail-y {
      width: 4px !important;
      background-color: #d6d6d6 !important;
    }

    .ncatfiltit.ncatfiltit_price span {
      letter-spacing: 0.17px;
      padding: 15px 20px 15px 0;
      text-transform: none;
    }

    .listing_filters_wrapperHeadline {
      letter-spacing: 0.17px;
      padding: 15px 20px 20px 0;
      text-transform: none;
    }

    .listing_filters_wrapper_price .listing_filters_wrapperHeadline {
      padding-bottom: 12px;
    }

    .newfilterj .check_wrap {
      max-height: 280px;

      &.dropped {
        max-height: 400px;
      }
    }

    #mse2_filters .ncatslrange .polzun_input {
      column-gap: 20px;
    }

    fieldset,
    .ncatfiltit + .ncatslrange fieldset {
      padding: 0;
    }

    .show-more {
      color: var(--redesign-main-color);

      @media (hover: hover) and (pointer: fine) {
          &:hover {
            color: var(--redesign-second-color);
          }
      }
    }

    .ncatfiltersectoropener_inline .ncatfiltit {
      margin-right: 0 !important;
      padding-bottom: 11px !important;
    }

    .ncatslrange {
      padding: 7px 40px 0 36px !important;
    }

    .ncatfilcheck_marker {
      padding-left: 36px;
      margin-bottom: 5px;
    }

    .listing_filters_wrapperList {
      padding-bottom: 0;
    }

    .ncatfiltit_price-title {
      display: block;
      position: relative;
      margin-left: -36px;
      padding: 17px 20px 12px 0;
      color: black;
      font-size: 17px;
      font-style: normal;
      font-weight: 700;
      line-height: 20px; /* 117.647% */
      letter-spacing: 0.17px;
    }

    hr:not([size]) {
      width: 91%;
    }

    .ncatfilcheck_radio {
      display: flex;
      justify-content: space-between;
      gap: 5px;
      margin-left: -37px;
      padding: 25px 4px 18px 0;

      .ncatfilcheck_show {
        @media (hover: hover) and (pointer: fine) {
            &:hover {
              span {
                color: var(--redesign-second-color);
              }
              mark {
                  background: #d6d6d6;
              }
            }

            &:has(input:checked) {
                &:hover {
                    mark {
                        background: var(--redesign-main-color) !important;
                    }
                }
            }
        }
      }

      input:checked + label mark {
          background: var(--redesign-main-color);

          &::before {
              left: 23px;
          }
      }

      .ncatfilcheck_show label {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 3px;
        margin: 0 !important;
        padding: 0 !important;
        width: 100%;
        height: auto;

        &::before {
          display: none;
        }

        span {
          display: block;
          margin: 0 !important;
          padding: 0 !important;
          color: black;
          font-size: 14px;
          font-style: normal;
          font-weight: 400;
          line-height: 120%; /* 16.8px */
          letter-spacing: 0.14px;
          transition: all .1s linear;
        }

        mark {
            display: block;
            position: relative;
            width: 40px;
            height: 20px;
            border-radius: 999px;
            background: #b1b1b1;
            transition: all .1s ease-out;

            &::before {
                content: '';
                display: block;
                position: absolute;
                top: 3px;
                left: 3px;
                width: 14px;
                height: 14px;
                border-radius: 50%;
                background: white;
                transition: all .1s ease-out;
            }
        }
      }
    }
  }
}

