.service-banner {
  width: 100%;
  height: auto;
  overflow: hidden;
  position: relative;

  &__slider {
    width: 100%;
    height: auto;
  }

  &__slide {
    height: auto;

    .service-banner__card {
      height: 100%;
    }

    .service-banner__picture {
      flex: 1 0 auto;

      @media (min-width: 768px) {
        flex: initial;
      }
    }

    .service-banner__caption {
      height: 100%;
    }
  }

  &__pagination {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    width: 100%;
    height: 34px;
    margin-top: 0;
    bottom: 0!important;

    .swiper-pagination-bullet {
      position: relative;
      height: 100%;
      flex: 1 0 auto;
      background: 0 0;
      border-radius: 3px;
      opacity: 1;
      cursor: pointer;

      &:first-child {
        margin-left: 0;
      }

      &:last-child {
        margin-right: 0;
      }

      &::before {
        content: '';
        display: block;
        position: absolute;
        top: 50%;
        left: 0;
        width: 100%;
        height: 4px;
        background-color: #b1b1b1;
        -webkit-transform: translateY(-50%);
        transform: translateY(-50%);
        border-radius: 3px;
        -webkit-transition: all .17s linear;
        transition: all .17s linear;
      }

      &:hover {
        &::before {
          background-color: var(--redesign-main-color);
        }
      }

      &.swiper-pagination-bullet-active {
        cursor: default;
        pointer-events: none;

        &::before {
          background-color: var(--redesign-second-color)!important;
        }
      }
    }
  }

  &__card {
    display: flex;
    flex-direction: column;
    position: relative;
    width: 100%;
    height: auto;
    margin: 0;
    padding: 0;
    background-color: white;
    overflow: hidden;
    text-decoration: none;

    @media (min-width: 768px) {
      flex-direction: row-reverse;
    }

    @media (min-width: 992px) {
      flex-direction: column;
      padding: 20px 20px 73px;
      border: 1px solid #b1b1b1;
    }
  }

  &__picture {
    display: block;
    position: relative;
    width: 100%;
    height: auto;

    @media (min-width: 992px) {
      order: 2;
    }

    &::before {
      content: '';
      display: block;
      position: relative;
      width: 100%;
      height: auto;
      padding-bottom: 51.563%;
      background-color: #f5f5f5;

      @media (min-width: 992px) {
        padding-bottom: 58.67%;
      }
    }

    img {
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__caption {
    display: flex;
    align-items: center;
    gap: 10px;
    background-color: #333333;
    padding: 10px;

    @media (min-width: 576px) {
      padding: 20px;
      gap: 20px;
    }

    @media (min-width: 768px) {
      flex-direction: column;
      align-items: flex-start;
      justify-content: space-between;
      width: 65%;
    }

    @media (min-width: 992px) {
      flex-direction: row;
      align-items: center;
      justify-content: flex-start;
      order: 1;
      padding: 0 0 10px;
      background-color: white;
      width: 101%;
    }
  }

  &__title {
    margin: 0;
    padding: 0;
    width: 100%;
    height: auto;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 1.2;
    color: white;
    letter-spacing: 0;

    @media (min-width: 576px) {
      font-size: 16px;
      line-height: 1.3;
      font-weight: 300;
      letter-spacing: .2px;
    }

    @media (min-width: 768px) {
      font-size: 17px;
      line-height: 1.4;
    }

    @media (min-width: 992px) {
      font-weight: 700;
      font-size: 14px;
      line-height: 16px;
      color: black;
      min-height: 32px;
    }
  }

  &__button {
    flex: 1;
    width: auto;
    height: auto;

    @media (min-width: 768px) {
      flex: initial;
    }

    @media (min-width: 992px) {
      position: absolute;
      width: 100%;
      bottom: 0;
      left: 0;
      padding: 15px 20px 20px;
    }
  }

  &__link {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
    width: 100px;
    height: 30px;
    border: 1px solid white;
    border-radius: 3px;

    @media (min-width: 576px) {
      width: 140px;
      height: 40px;
      padding: 10px 15px;
    }

    @media (min-width: 992px) {
      justify-content: center;
      width: 100%;
      height: auto;
      padding: 0;
      background: #262626;
      border-radius: 2px;
      border-color: #262626;
      transition: all .1s linear;

      &:hover {
        background-color: white;

        span {
          color: black;
        }
      }
    }

    &::before {
      content: '';
      display: block;
      font-family: 'Roboto';
      font-style: normal;
      font-weight: 400;
      font-size: 10px;
      line-height: 14px;
      color: white;

      @media (min-width: 576px) {
        font-size: 14px;
        line-height: initial;
      }
    }

    &::after {
      content: '';
      display: block;
      position: relative;
      width: 16px;
      height: 16px;
      background-position: center;
      background-repeat: no-repeat;
      background-image: url("data:image/svg+xml;base64,");
    }

    &::before,
    &::after {
      @media (min-width: 992px) {
        display: none;
      }
    }

    span {
      display: none;
      padding: 10px 10px;
      font-family: 'Roboto';
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      line-height: 16px;
      text-align: center;
      color: white;
      transition: all .1s linear;

      @media (min-width: 992px) {
        display: block;
      }
    }
  }

  .swiper-lazy-preloader {
    border-top-color: transparent !important;
    border-color: #ccc;
  }
}

