.analog-target {
  display: flex;
  gap: 10px;
  position: relative;
  width: 100%;
  height: auto;
  overflow: hidden;
  margin-top: 20px;
  margin-bottom: 20px;
  padding: 9px;
  border: 1px solid #b1b1b1;

  @media (min-width: 576px) {
    padding: 19px;
  }

  @media (min-width: 992px) {
    flex-direction: column;
    align-items: flex-start;
    order: -1;
  }

  &__pic {
    flex: 1 0 auto;
    display: block;
    position: relative;
    width: 36.67%;
    height: auto;
    margin: 0 auto;
    padding: 0;

    @media (min-width: 576px) {
      max-width: 40%;
    }

    @media (min-width: 768px) {
      max-width: 30%;
    }

    @media (min-width: 992px) {
      width: 100%;
      max-width: initial;
    }

    &::before {
      content: '';
      display: block;
      position: relative;
      width: 100%;
      height: auto;
      padding-bottom: 100%;

      @media (min-width: 992px) {
        padding-bottom: 94.235%;
      }
    }

    img {
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 100% !important;
      height: 100% !important;
      max-width: initial !important;
      max-height: initial !important;
      object-fit: contain;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    gap: 5px;
    align-items: flex-start;
    width: 100%;

    @media (min-width: 420px) {
      gap: 10px;
    }

    @media (min-width: 992px) {
      padding-top: 0;
    }
  }

  &__price {
    display: block;
    color: #000;
    font-size: 12px;
    font-style: normal;
    font-weight: 700;
    line-height: 14px;
    letter-spacing: 0.036px;

    @media (min-width: 420px) {
      font-size: 24px;
      line-height: 1.2;
      letter-spacing: 0;
    }

    @media (min-width: 992px) {
      font-size: 18px;
      line-height: 20px;
    }
  }

  &__code {
    display: block;
    color: #999;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: 14px;
    letter-spacing: 0;

    @media (min-width: 992px) {
      font-size: 16px;
      line-height: 18px;
    }
  }

  &__name {
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    width: 100%;
    height: auto;
    min-height: 28px;
    color: #000;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: 14px;
    letter-spacing: 0;
    text-decoration: none;
    cursor: pointer;
    transition: all .1s linear;

    @media (min-width: 576px) {
      font-size: 16px;
      line-height: 18px;
      min-height: 36px;
    }

    @media (min-width: 1200px) {
        &:hover {
          color: var(--redesign-second-color)
        }
    }
  }

  .item-buttons__btn {
    display: flex;
    align-items: center;
    gap: 22.5px;
    width: 100%;
    height: auto;
    margin-top: auto;

    @media (min-width: 992px) {
      gap: 20px;
      margin: 4px 0;
      padding-right: 12px;
    }

    form {
      display: block !important;
      position: relative !important;
      overflow: initial !important;
      background: transparent !important;
      margin: 0 !important;
      padding: 0 !important;
      width: 100% !important;
      opacity: 1 !important;
    }

    .btn, button {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
      height: 25px;
      padding: 0;
      color: #fff;
      transition: all .1s linear;

      @media (min-width: 420px) {
        height: 40px;
      }

      @media (min-width: 768px) {
        max-width: 70%;
      }

      @media (min-width: 992px) {
        max-width: initial;
      }
    }
  }

  .item-list {
    display: flex;
    align-items: center;
    gap: 22.5px;

    @media (min-width: 992px) {
      gap: 20px;
    }
  }

  .item-list__item {
    flex: 1 0 auto;
    display: inline-block;
    position: relative;
    width: 24px;
    height: 24px;
    cursor: pointer;

    &::after {
      content: "";
      position: absolute;
      width: 100%;
      height: 100%;
      background-position: center;
      background-repeat: no-repeat;
      transition: all .1s linear;
    }

    &.btn-favorite::after {
      background-image: url("data:image/svg+xml;base64,");
    }

    &.btn-favorite.voted::after {
      background-image: url("data:image/svg+xml;base64,");
    }

    &.btn-compare::after {
      background-image: url("data:image/svg+xml;base64,");
    }

    &.btn-compare.added::after {
        background-image: url("data:image/svg+xml;base64,")
    }
  }
}

