.maindbox-slider-5col,
.maindbox-slider-4col,
.maindbox-slider-3col {
	.popmechanic {
		&-header {
			padding: 0 0 20px 0;
			font-size: 24px;
			font-weight: 400;
			line-height: 1.4;

			@media (min-width: 768px) {
				padding-bottom: 30px;
			}

			@media (min-width: 1200px) {
				font-size: 28px;
			}
		}

		//- swiper
		&-items-wrapper {
			position: relative;
			padding-bottom: 25px;
			overflow: hidden;
			width: 100%;
			touch-action: pan-y;
		}

		//- swiper-wrapper
		&-items {
			position: relative;
			width: 100%;
			height: 100%;
			z-index: 1;
			display: flex;
			transition-property: transform;
			box-sizing: content-box;
			transform: translate3d(0,0,0);
		}

		//- swiper-scrollbar
		&-scrollbar {
			position: absolute;
			left: 0;
			bottom: 0;
			z-index: 10;
			height: 25px;
			width: 100%;
			-ms-touch-action: none;
			background: transparent;

			@media (min-width: 992px) {
				left: 20px;
				width: calc(100% - 40px);
			}

			&::before {
				content: '';
				display: block;
				position: absolute;
				top: 50%;
				left: 0;
				margin-top: -2px;
				width: 100%;
				height: 5px;
				background-color: var(--scroll-color, #ededed);
				border-radius: 999px;
			}

			.swiper-scrollbar-drag {
				cursor: pointer;
				height: 100%;
				width: 100%;
				position: relative;
				background: transparent;
				left: 0;
				top: 0;

				&::before {
					content: '';
					display: block;
					position: absolute;
					top: 50%;
					left: 0;
					width: 100%;
					height: 5px;
					margin-top: -2px;
					background-color: var(--redesign-popular-categories-bg, #333333);
					border-radius: 999px;
				}
			}
		}
		&-main {
			position: relative;
		}
	}

	//- mb an abbreviation of a Maindbox
	.mb {

		//- swiper-slide
		&__card {
			flex-shrink: 0;
			width: 100%;
			height: auto;
			position: relative;
			transition-property: transform;

			@media (min-width: 992px) {
				max-width: 350px;
			}

			&-wrap {
				display: flex;
				flex-direction: column;
				justify-content: space-between;
				position: relative;
				max-width: 150px;
				height: 100%;
				padding: 10px 0;

				@media (min-width: 992px) {
					max-width: 264px;
					padding: 20px 10px 20px 20px;
				}
			}

			&-labels {
				display: flex;
				flex-wrap: wrap;
				gap: 6px;
				position: absolute;
				top: 10px;
				left: 0;
				width: 100%;
				margin: 0;
				padding: 0;
				z-index: 1;
				list-style: none;

				@media (min-width: 992px) {
					top: 20px;
					left: 20px;
				}
			}

			&-label {
				display: flex;
				align-items: center;
				justify-content: center;
				position: relative;
				padding: 0 8px;
				width: auto;
				height: 24px;
				background: var(--redesign-second-text-color, #fff);
				border-width: 1px;
				border-style: solid;
				border-radius: 3px;
				font-size: 10px;
				line-height: 12px;

				&_lite { border-color: var(--redesign-main-color, #6A8835); }
				&_bright { border-color: var(--fail-color, #ff0000); }

				@media (min-width: 992px) {
					height: 30px;
					font-size: 12px;
					line-height: 14px;
				}
			}

			&-img {
				display: block;
				position: relative;
				width: 100%;
				height: auto;
				padding: 34px 5px 0;

				@media (min-width: 992px) {
					padding: 36px 10px 0 0;
				}

				& > * {
					display: block;
					position: relative;
					width: 100%;
					height: auto;
					text-decoration: none !important;
					color: inherit !important;

					&::before {
						content: '';
						display: block;
						position: relative;
						width: 100%;
						height: auto;
						padding-bottom: 100%;
					}

					& > img {
						display: block;
						position: absolute;
						top: 0;
						left: 0;
						width: 100%;
						height: 100%;
						object-fit: contain;
					}
				}
			}

			&-body {
				height: 100%;
			}

			&-name {
				display: block;
				margin: 13px 0 5px;
				padding: 0;
				width: 100%;
				height: 58px;
				transition: all .1s linear;
				color: var(--redesign-text-color, #000);
				font-size: 1rem;
				font-weight: 400;
				line-height: 1.2;

				@media (min-width: 992px) {
					margin-top: 15px;
					margin-bottom: 8px;
					height: 42px;
				}

				@media (hover: hover) and (pointer: fine) {
					&:hover {
						color: var(--redesign-second-color, #FFA82E) !important;
					}
				}

				& > * {
					display: -webkit-box;
					-webkit-line-clamp: 3;
					-webkit-box-orient: vertical;
					overflow: hidden;
					text-overflow: ellipsis;
					font-size: 16px;
					font-style: normal;
					font-weight: 400;
					line-height: 119%;
					letter-spacing: -.01px;
					text-decoration: none !important;
					color: inherit !important;

					@media (min-width: 992px) {
						-webkit-line-clamp: 2;
						line-height: 19px;
					}
				}
			}

			&-code {
				display: flex;
				align-items: center;
				margin: 0;
				padding: 0;
				width: 100%;
				height: 24px;
				background-color: transparent;
				color: var(--mindbox-code-color, #5a5a5a);
				font-size: 14px;
				font-style: normal;
				font-weight: 400;
				line-height: normal;
				letter-spacing: -.01px;

				@media (min-width: 992px) {
					height: 30px;
					font-size: 15px;
					line-height: 18px;
					color: var(--mindbox-code-color-dark, #999999);
				}

				span {
					display: -webkit-box;
					-webkit-line-clamp: 2;
					-webkit-box-orient: vertical;
					overflow: hidden;
					text-overflow: ellipsis;
				}
			}

			&-info {
				display: flex;
				flex-direction: column;
				list-style: none;
				margin: 6px 0 0;
				padding: 0;
				width: 100%;
				height: 35px;

				@media (min-width: 992px) {
					margin: 10px 0;
				}

				li {
					display: block;
					width: 100%;
					height: 16px;
					font-size: 12px;
					line-height: 1.3;
					font-style: normal;
					font-weight: 400;
					color: var(--redesign-main-color, #6A8835);

					@media (min-width: 992px) {
						height: 20px;
						font-size: 14px;
					}
				}
			}

			&-prices {
				display: flex;
				flex-direction: column;
				justify-content: flex-end;
				width: 100%;
				height: 45px;
				margin: 17px 0 0 0;
				padding: 0;
				width: 100%;

				@media (min-width: 992px) {
					height: 39px;
				}
			}

			&-price {
				height: 20px;
				padding: 5px 0;
				color: var(--redesign-text-color, #000000);
				font-size: 16px;
				font-weight: 700;
				line-height: 111.687%;
				letter-spacing: -.01px;

				@media (min-width: 992px) {
					padding-top: 6px;
					font-size: 18px;
					line-height: 20px;
				}
			}

			&-sale {
				display: flex;
				flex-wrap: wrap;
				align-items: center;
				gap: 5px;
				width: 100%;
				height: 25px;

				@media (min-width: 992px) {
					height: 19px;
					gap: 10px;
				}
			}

			&-old-price {
				font-style: italic;
				line-height: 12px;
				text-decoration-line: line-through;
				color: var(--mindbox-code-color, #5a5a5a);
				font-size: 14px;
				font-style: normal;
				white-space: nowrap;
				line-height: 21px;
				margin: 2px 2px 2px 0;

				@media (min-width: 992px) {
					font-style: italic ;
					font-size: 12px;
				}
			}

			&-sale-value {
				display: flex;
				align-items: center;
				justify-content: center;
				font-weight: 700;
				font-size: 10px;
				line-height: 12px;
				color: var(--redesign-text-color, #000000);

				height: 21px;
				padding: 2px 3px 0 3px;
				margin-left: 0;
				margin-top: -2px;

				background: var(--redesign-second-color, #FFA82E);
				border-radius: 3px;

				color: var(--redesign-second-text-color, #ffffff);
				text-align: center;
				font-size: 14px;
				font-style: normal;
				font-weight: 400;
				line-height: 120%;
				white-space: nowrap;

				@media (min-width: 992px) {
					font-size: 12px;
					line-height: 15px;
					height: 15px;
					padding: 3px 2px 1px 2px;
				}
			}

			&-btn {
				margin-top: 15px;

				@media (min-width: 992px) {
					margin-top: 5px;
				}

				& > * {
					display: flex;
					align-items: center;
					justify-content: center;
					width: 100%;
					height: 35px;
					color: var(--redesign-second-text-color, #ffffff) !important;
					transition: all .1s linear;
					text-decoration: none !important;
					border-radius: 2px;
					text-align: center;
					font-size: 16px;
					font-style: normal;
					font-weight: 400;
					line-height: 111.687%;

					@media (min-width: 992px) {
						width: 137px;
						height: 40px;
						font-size: 14px;
						line-height: 14px;
						font-weight: 300;
					}
				}
			}
		}
	}
}

.recommendation-mark {
	display: block;
	margin: 0;
	padding: 0;
	color: var(--recommendation-mark-color, #b1b1b1);
	font-size: 12px;
	font-style: normal;
	font-weight: 400;
	line-height: 14px;
	letter-spacing: 0.12px;
}
@media (min-width: 768px) {
	.recommendation-mark {
		font-size: 14px;
		line-height: 20px;
		letter-spacing: 0.17px;
	}
}

