.configurators {
    &_list {
        display: grid;
        grid-template-columns: 100%;
        grid-gap: 20px;
        margin-top: 40px;

        @media (min-width: 767px) {
            grid-template-columns: repeat(2, 1fr);
        }

        @media (min-width: 991px) {
            grid-template-columns: repeat(3, 1fr);
        }
    }

    &_item {
        display: flex;
        flex-direction: column;
        padding: 20px;
        border: 1px solid #E6E6E6;
        border-radius: 2px;

        @media (min-width: 991px) {
            &:nth-child(1) {
                grid-column: 1 / 2;
                grid-row: 1 / 2;
            }

            &:nth-child(2) {
                grid-column: 2 / 3;
                grid-row:  1 / 2;
            }

            &:nth-child(3) {
                grid-column: 1 / 2;
                grid-row: 2 / 3;
            }

            &:nth-child(4) {
                grid-column: 2 / 3;
                grid-row:  2 / 3;
            }

            &:nth-child(5) {
                grid-column: 3 / 4;
                grid-row:  2 / 3;
            }
        }



        &_top {
            display: flex;
            justify-content: space-between;
            align-items: center;
            column-gap: 10px;

            &_left {
                display: flex;
                align-items: center;
                column-gap: 10px;
            }
        }

        &_icon {
            display: block;
            width: 20px;
            height: 20px;
            object-fit: contain;
            flex-shrink: 0;
        }

        &_icon1 {
            background: url("data:image/svg+xml;base64,") no-repeat;
        }

        &_icon2 {
            background: url("data:image/svg+xml;base64,") no-repeat;
        }

        &_icon3 {
            background: url("data:image/svg+xml;base64,") no-repeat;
        }

        &_icon4 {
            background: url("data:image/svg+xml;base64,") no-repeat;
        }

        &_text {
            margin-top: 20px;
            flex-grow: 1;
        }

        &_bottom {
            margin-top: 20px;

            @media (min-width: 576px) {
                margin-top: 40px;
            }
        }

        &_link {
            display: block;
            width: 100%;
            padding: 12px 0;

            @media (min-width: 576px) {
                display: inline-block;
                width: auto;
                padding: 12px 72px;
            }
        }
    }
}