.billboard {
    .container {
        position: relative;
        max-width: 100%;
        padding: 0;

        @media (min-width: 768px) {
            max-width: 768px;
        }

        @media (min-width: 992px) {
            max-width: 960px;
        }
    }

    .swiper {
        width: 100%;
        height: auto;
        margin-left: auto;
        margin-right: auto;
        position: relative;
        overflow: hidden;
        list-style: none;
        padding: 0;
        z-index: 1;
    }

    .swiper-slide {
        height: auto;
    }

    &__wrap {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: stretch;
        width: 100%;
        height: 100%;

        @media (min-width: 768px) {
            flex-direction: row;
        }
    }

    &__picture-wrap {
        position: relative;
        width: 100%;
        height: 0;
        padding-bottom: 31.25%;

        @media (min-width: 768px) {
            padding-bottom: 0;
            height: auto;
            order: 3;
        }
    }

    &__picture {
        display: block;
        position: absolute;
        width: 100%;
        height: 100%;

        img {
            position: relative;
            width: 100%;
            height: 100%;
            object-fit: cover;
            z-index: 1;
        }
    }

    &__content {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: flex-start;
        width: 100%;
        height: 100%;
        min-height: 140px;
        padding: 34px 20px 20px;
        box-shadow: inset 0 15px 0 0 rgba(255,255,255,0.2);
        font-family: 'Roboto', sans-serif;
        font-style: normal;
        font-weight: 400;
        color: white;

        @media (min-width: 768px) {
            width: 256px;
            min-height: 160px;
            padding: 20px 35px 20px 20px;
            box-shadow: inset -15px 0 0 0 rgba(255,255,255,0.2);
            order: 1;
            flex: 1 0 auto;
        }

        @media (min-width: 991px) {
            width: 320px;
            min-height: 200px;
            padding-right: 40px;
            box-shadow: inset -20px 0 0 0 rgba(255,255,255,0.2);
        }

        @media (min-width: 1200px) {
            width: 480px;
            padding-right: 50px;
            box-shadow: inset -30px 0 0 0 rgba(255,255,255,0.2);
            min-height: 300px;
        }
    }

    &__caption {
        font-size: 17px;
        line-height: 20px;
        letter-spacing: 0.01em;
        color: inherit;
        margin: 0 0 19px 0;

        @media (min-width: 768px) {
            margin-bottom: 20px;
        }

        @media (min-width: 992px) {
            font-size: 20px;
            line-height: 24px;
        }

        @media (min-width: 1200px) {
            font-size: 32px;
            line-height: 38px;
            margin-bottom: 20px;
        }
    }

    &__text {
        display: block;
        font-size: 12px;
        line-height: 15px;
        letter-spacing: .01em;
        color: inherit;
        margin-top: 3px;

        @media (min-width: 992px) {
            font-size: 14px;
            line-height: 18px;
        }

        @media (min-width: 1200px) {
            font-size: 24px;
            line-height: 28px;
        }
    }

    &__button {
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        padding: 6px 33px 6px 13px;
        width: auto;
        height: auto;
        border: 2px solid white;
        border-radius: 3px;
        font-size: 10px;
        line-height: 14px;
        letter-spacing: .01em;
        color: inherit;
        background-color: transparent;
        transition: all .17s linear;

        &::before, &::after {
            content: '';
            display: inline-block;
            position: absolute;
            top: 50%;
            right: 13px;
            transform: translateY(-50%);
            width: 16px;
            height: 16px;
            background-position: center;
            background-repeat: no-repeat;
            transition: all .17s linear;

            @media (min-width: 992px) {
                width: 23px;
                height: 23px;
                right: 21px;
                background-size: cover;
            }

            @media (min-width: 1200px) {
                right: 18px;
                width: 25px;
                height: 24px;
            }
        }

        &::before {
            background-image: url("data:image/svg+xml,%3Csvg width='16' height='16' viewBox='0 0 16 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M9.36018 3.19103C9.09983 2.93633 8.67772 2.93632 8.41737 3.19101C8.15701 3.44569 8.157 3.85863 8.41735 4.11332L11.7236 7.3478H2.66667C2.29848 7.3478 2 7.63979 2 7.99997C2 8.36016 2.29848 8.65215 2.66667 8.65215H11.7236L8.41726 11.8867C8.15692 12.1414 8.15692 12.5543 8.41727 12.809C8.67762 13.0637 9.09973 13.0637 9.36008 12.809L13.7931 8.47225C13.9206 8.35346 14 8.18579 14 7.99997C14 7.8136 13.9201 7.64548 13.7919 7.52663L9.36018 3.19103Z' fill='white'/%3E%3C/svg%3E");

            @media (min-width: 1200px) {
                opacity: 1;
                background-image: url("data:image/svg+xml,%3Csvg width='25' height='24' viewBox='0 0 25 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M8.62805 4.30561C9.03361 3.89812 9.69114 3.89813 10.0967 4.30564L17.0196 11.2622C17.4251 11.6697 17.4251 12.3303 17.0196 12.7378L10.0965 19.6944C9.69098 20.1019 9.03346 20.1019 8.6279 19.6944C8.22235 19.2869 8.22234 18.6262 8.62789 18.2187L14.8166 12L8.62802 5.78131C8.22248 5.3738 8.22249 4.71311 8.62805 4.30561Z' fill='white'/%3E%3C/svg%3E");
            }
        }

        &::after {
            display: none;
            opacity: 0;
            background-image: url("data:image/svg+xml,%3Csvg width='25' height='24' viewBox='0 0 25 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M8.62805 4.30561C9.03361 3.89812 9.69114 3.89813 10.0967 4.30564L17.0196 11.2622C17.4251 11.6697 17.4251 12.3303 17.0196 12.7378L10.0965 19.6944C9.69098 20.1019 9.03346 20.1019 8.6279 19.6944C8.22235 19.2869 8.22234 18.6262 8.62789 18.2187L14.8166 12L8.62802 5.78131C8.22248 5.3738 8.22249 4.71311 8.62805 4.30561Z' fill='%23000'/%3E%3C/svg%3E%0A");

            @media (min-width: 1200px) {
                display: block;
            }
        }

        @media (min-width: 992px) {
            padding: 10px 50px 8px 21px;
            font-size: 14px;
            line-height: 18px;
            letter-spacing: -0.2px;
        }

        @media (min-width: 1200px) {
            font-size: 17px;
            line-height: 22px;
            padding: 12px 48px 12px 18px;
            letter-spacing: .01em;

            &:hover {
                color: black;
                background-color: white;

                &::before {
                    opacity: 0;
                }

                &:after {
                    opacity: 1;
                }
            }
        }
    }

    &__pagination {
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        width: 100%;
        height: 34px;
        margin-top: 0;
        padding: 0 20px;
        bottom: 0 !important;

        .swiper-pagination-bullet {
            position: relative;
            height: 100%;
            flex: 1 0 auto;
            background: transparent;
            border-radius: 3px;
            margin: 0 5px;
            opacity: 1;
            cursor: pointer;

            &::before {
                content: '';
                display: block;
                position: absolute;
                top: 50%;
                left: 0;
                width: 100%;
                height: 4px;
                background-color: #b1b1b1;
                transform: translateY(-50%);
                border-radius: 3px;
                transition: all .17s linear;
            }

            &:first-child {
                margin-left: 0;
            }

            &:last-child {
                margin-right: 0;
            }

            @media (min-width: 1200px) {
                &:hover::before {
                    background-color: var(--redesign-main-color);
                }
            }
        }

        .swiper-pagination-bullet-active {
            cursor: default;
            pointer-events: none;

            &::before {
                background-color: var(--redesign-second-color) !important;
            }
        }
    }

    &.cooperation {
        .cooperation_banner_tab,
        .cooperation_slogan {
            width: auto;
            margin-left: -15px;
            margin-right: -15px;

            @media (min-width: 768px) {
                margin-left: -39px;
                margin-right: -39px;
            }

            @media (min-width: 992px) {
                margin-left: -20px;
                margin-right: -20px;
            }
        }

        .cooperation_slogan {
            padding: 10px;
            font-size: 14px;
            line-height: 17px;
            letter-spacing: 0.01em;
            text-align: center;
            background: #5dac0d;
            color: #fff;

            @media (min-width: 768px) {
                padding-top: 6px;
                padding-bottom: 6px;
                font-size: 17px;
                line-height: 20px;
            }

            @media (min-width: 992px) {
                font-size: 20px;
                line-height: 24px;
            }

            @media (min-width: 1200px) {
                font-size: 24px;
                line-height: 28px;
            }
        }

        .cooperation_banner_tab {
            @media (min-width: 993px) {
                margin-left: 0;
                margin-right: 0;
            }

            @media (min-width: 1200px) {
                margin-bottom: 40px;
            }
        }
    }
}