@media (max-width: 992px) {
  .ncatlist .ncatfilter form.active {
    z-index: 1000 !important;
  }
}

@media screen and (max-width: 1100px) {
  .header_black_nav a {
    padding: 10px 10px;
  }
}

@media screen and (max-width: 1200px) {
  .header_black_nav a {
    padding: 10px 4px;
  }
}

.filter_more {
  color: var(--redesign-main-color);
  cursor: pointer;
  margin-top: 2px;
  padding-bottom: 5px;
  display: block;
  margin-left: 2px;
  padding-left: 28px;
  background-color: #fff;
  width: 100%;
  position: relative;
  z-index: 1;
}

@media screen and (max-width: 768px) {
  .filter_more_enable {
    margin-bottom: 0px;
    max-height: 215px;
  }
  .filter_more_enable .check_wrap {
    max-height: 150px;
  }
}

@media screen and (min-width: 768px) {
  .filter_more {
    display: none;
  }
}

