.btn_favorite {
    position: absolute;
    right: 5px;
    top: 8px;
    width: 20px;
    height: 20px;
    overflow: hidden;
    z-index: 7;
}

.btn_favorite .msfavorites span {
    width: 20px;
    height: 20px;
    font-size: 13px;
    padding-left: 24px;
    color: #2399e2;
    text-decoration: none !important;
    position: relative;
    display: inline-block;
    opacity: 0.9;
}

.btn_favorite .msfavorites span.msfavorites-text-add {
    opacity: 0.6;
}

.btn_favorite .msfavorites span:hover {
    opacity: 1;
}

.btn_favorite .msfavorites .msfavorites-text-add {
    background: url($base62_favorite) no-repeat left center;
}

.btn_favorite .msfavorites .msfavorites-text-remove {
    background: url($base62_favorite_hover) no-repeat left center;
}

.btn_like {
    background-image: url($base62_favorite);
    background-repeat: no-repeat;
    width: 30px;
    height: 30px;
    display: block;
    background-position: 5px 8px;
    background-size: 18px auto;
    cursor: pointer;
    &--active {
        background-image: url("data:image/svg+xml;base64,");
    }
    &--big {
        width: 28px;
        height: 35px;
        background-position: 2px 9px;
        background-size: 22px auto;
    }
    &--new_product {
        background-image: url("data:image/svg+xml;base64,");
        background-position: left center;
        background-size: 25px auto;
        &.btn_like--active {
            background-image: url("data:image/svg+xml;base64,");
            background-position: left center;
            background-size: 25px auto;
        }
    }
}

.btn_like_jsOne {
    display: flex;
    align-items: center;
    font-size: 14px;
    &:hover {
        text-decoration: underline !important;
    }
    .btn_like {
        margin-right: 4px;
    }
}
