.cart-item {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    position: relative;
    width: 100%;
    height: auto;
    margin: 0;
    padding: 15px 0 5px;
    box-shadow: 0 1px 0 #9b9b9b;
    transition: all .1s linear;

    &:first-child {
        box-shadow: inset 0 1px 0 #9b9b9b, 0 1px 0 #9b9b9b;
    }

    @media (min-width: 768px) {
        padding: 21px 20px 20px;
    }

    //   -- START
    &_price-change .cart-item__price-change,
    &_not-available .cart-item__not-available,
    &_report-admission .cart-item__report-admission,
    &_loading .cart-item__loading {
        display: flex;
    }

    &_count-limit {
        .cart-item__count-limit {
            display: block;
        }

        .cart-item__btn_dec {
            background: var(--redesign-second-color);
            border: 0.833327px solid #9b9b9b;
            border-radius: 2.49998px;
            cursor: default;
            pointer-events: none;

            &::before {
                color: white;
            }
        }
    }

    &_order-product {
        .cart-item__data {
            display: none;
        }

        .cart-item__order-product {
            display: block;
        }
    }

    &_item-discounted {
        .cart-item__data {
            display: none;
        }

        .cart-item__item-discounted {
            display: flex;
        }
    }
    //   -- FINISH

    &__wrapper {
        display: flex;
        flex-direction: column;
    }

    &__caption {
        display: flex;
        justify-content: space-between;
        width: 100%;
        margin-bottom: 10px;

        @media (min-width: 768px) {
            width: 100%;
            padding-left: 140px;
            margin-bottom: 1px;
        }
    }

    &__title {
        margin: 0;
        padding: 0;
        width: 100%;
        height: 51px;
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;
        letter-spacing: 0.01em;
        display: -webkit-box;
        -webkit-line-clamp: 3;
        -webkit-box-orient: vertical;
        overflow: hidden;
        text-overflow: ellipsis;

        a {
            font-style: inherit;
            font-weight: inherit;
            font-size: inherit;
            line-height: inherit;
            letter-spacing: inherit;
            text-decoration: none;
            transition: all .1s linear;
        }

        @media (min-width: 769px) {
            font-size: 17px;
            line-height: 20px;
            width: 79.937%;
            height: 41px;
            flex: 1 0 auto;
            margin-bottom: 10px;
            -webkit-line-clamp: 2;
        }

        @media (min-width: 1200px) {
           &:hover {
               color: var(--redesign-second-color);
           }
        }
    }

    &__links {
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        flex: 1 0 auto;
        margin-left: 25px;

        a {
            font-style: normal;
            font-weight: 400;
            font-size: 12px;
            line-height: 14px;
            text-align: right;
            letter-spacing: 0.01em;
            color: #2399E2;
            margin-bottom: 10px;
            transition: all .1s linear;

            @media (min-width: 769px) {
                font-size: 14px;
                line-height: 16px;
                text-decoration: underline !important;
                margin-bottom: 19px;
            }

            @media (min-width: 1200px) {
                &:hover {
                    color: #1C7AB5;
                }
            }

            &:last-child {
                margin-bottom: 0;
            }
        }

        @media (min-width: 769px) {
            flex: none;
            position: relative;
            top: 1px;
            right: -1px;
            width: auto;
        }
    }

    &__picture {
        position: relative;
        width: 31.25%;
        height: auto;

        img {
            width: 100%;
            max-width: 100%;
            max-height: 100%;
            position: relative;
            margin: 0 auto;

            @media (min-width: 768px) {
                width: auto;
                margin: 1px auto 0;
            }
        }

        @media (min-width: 768px) {
            position: absolute;
            top: 20px;
            left: 20px;
            width: 120px;
            height: calc(100% - 40px);
            z-index: 1;
        }
    }

    &__content {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        width: calc(100% - 31.25% - 10px);
        height: auto;

        @media (min-width: 768px) {
            width: 100%;
            padding-left: 140px;
        }

        .shipped {
            position: relative;
            display: flex;
            align-items: center;
            gap: 10px;
            width: 100%;
            height: 25px;
            margin-top: 10px;
            color: var(--redesign-second-color);
            font-size: 12px;
            font-style: normal;
            font-weight: 400;
            line-height: normal;

            @media screen and (min-width: 576px) {
                font-size: 15px;
                width: max-content;
            }


            &::before {
                content: '';
                display: inline-block;
                position: relative;
                width: 20px;
                height: 20px;
                background: transparent no-repeat center;
                background-image: url("data:image/svg+xml;base64,");

                @media screen and (min-width: 576px) {
                    width: 25px;
                    height: 25px;
                }
            }

            &:hover {
                .shipped_tooltip {
                    display: flex;
                }
            }

            &_tooltip {
                display: none;
                align-items: center;
                column-gap: 5px;
                position: absolute;
                top: -120px;
                left: 0;
                transform: translateX(0);
                width: 200px;
                height: auto;
                border-radius: 3px;
                padding: 11px 15px;
                font-size: 14px;
                line-height: 18px;
                color: var(--redesign-second-text-color);
                background: var(--redesign-second-color);

                @media (min-width: 576px) {
                    top: -70px;
                    left: 50%;
                    transform: translateX(-50%);
                    width: 300px;
                    height: 56px;
                }

                &:after {
                    position: absolute;
                    content: '';
                    bottom: -22px;
                    left: 50%;
                    transform: translateX(-50%);
                    width: 0;
                    height: 0;
                    border-left: 25px solid transparent;
                    border-right: 25px solid transparent;
                    border-top: 38px solid var(--redesign-second-color);
                }

                div {
                    position: relative;
                    z-index: 1;
                }

                svg {
                    flex-shrink: 0;
                    display: block;
                    width: 25px;
                    height: 25px;
                    object-fit: contain;
                }
            }
        }
    }

    &__code {
        display: flex;
        margin: 0 0 4px;
        padding: 4px 0;
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;
        letter-spacing: 0.01em;
        color: #9b9b9b;

        @media (min-width: 768px) {
            font-weight: 300;
            font-size: 17px;
            line-height: 20px;
            margin-bottom: 8px;
            padding: 0;
        }

        &::before {
            content: '';
            display: block;
            position: relative;
            width: 16px;
            height: 16px;
            margin-right: 4px;
            background: url("data:image/svg+xml;base64,") center no-repeat;

            @media (min-width: 768px) {
                width: 24px;
                height: 24px;
                top: -2px;
                margin-right: 9px;
                background: url("data:image/svg+xml;base64,") center no-repeat;
            }
        }
    }

    &__item-discounted {
        display: flex;
        align-items: center;
        margin: 0 0 4px;
        padding: 9px 0;
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 20px;
        letter-spacing: 0.01em;
        color: var(--redesign-second-color);

        @media (min-width: 768px) {
            font-size: 17px;
            line-height: 20px;
            padding: 3px 0;
            margin-bottom: 10px;
        }

        &::before {
            content: '';
            display: block;
            position: relative;
            width: 24px;
            height: 24px;
            margin-right: 4px;
            background: url("data:image/svg+xml;base64,") center no-repeat;

            @media (min-width: 768px) {
                margin-right: 10px;
            }

        }
    }

    &__data {
        display: flex;
        flex-direction: column;
        width: 100%;
        height: auto;
        margin: 0 0 4px;
        padding: 5px 0;
        list-style: none;

        @media (min-width: 768px) {
            flex-direction: row;
            margin-bottom: 7px;
        }

        li {
            margin-bottom: 4px;
            font-style: normal;
            font-weight: 400;
            font-size: 12px;
            line-height: 14px;
            letter-spacing: 0.01em;
            color: var(--redesign-main-color);
            list-style: none;

            @media (min-width: 768px) {
                font-size: 14px;
                line-height: 20px;
                margin-right: 30px;
            }

            @media (min-width: 991px) {
                font-size: 17px;
                margin-right: 55.5px;
            }

            &:last-child {
                margin-bottom: 0;

                @media (min-width: 768px) {
                    right: 3px;
                    position: relative;
                }
            }
        }

    }

    &__sale {
        display: flex;
        align-items: center;
        margin-bottom: 4px;

        @media (min-width: 768px) {
            margin-bottom: 9px;
        }
    }

    &__old-price {
        margin-right: 11px;
        padding: 2px 2px 2px 3px;
        font-style: italic;
        font-weight: 400;
        font-size: 10px;
        line-height: 12px;
        text-decoration: line-through;
        color: #b1b1b1;
        letter-spacing: 0;

        @media (min-width: 768px) {
            font-size: 14px;
            line-height: 16px;
            margin-right: 12px;
            padding: 0;
        }
    }

    &__new-price {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 2px;
        background: var(--redesign-second-color);
        border-radius: 3px;
        font-style: normal;
        font-weight: 700;
        font-size: 10px;
        line-height: 12px;
        letter-spacing: 0;

        @media (min-width: 768px) {
            font-size: 14px;
            line-height: 16px;
        }
    }

    &__actions {
        display: flex;
        flex-wrap: wrap;
        align-items: center;

        &_wrap {

        }

        &_count {
            font-size: 12px;
            line-height: 14px;
            font-weight: 700;
            color: var(--redesign-third-color);
        }
    }

    &__price {
        min-width: 83px;
        margin-right: 20px;
        font-style: normal;
        font-weight: 700;
        font-size: 12px;
        line-height: 14px;
        letter-spacing: 0.003em;

        @media (min-width: 768px) {
            min-width: 120px;
            margin-right: 40px;
            font-size: 17px;
            line-height: 20px;
            letter-spacing: 0.01em;
        }
    }

    &__cost {
        width: 100%;
        margin-top: 4px;
        padding-top: 8px;
        font-style: normal;
        font-weight: 700;
        font-size: 24px;
        line-height: 28px;
        letter-spacing: 0.01em;

        @media (min-width: 768px) {
            width: auto;
            font-size: 17px;
            line-height: 20px;
            margin-top: 0;

            & + .cart-item__actions_count {
                text-align: right;
            }
        }
    }

    &__controllers {
        display: flex;
        align-items: center;
        position: relative;
        width: 100px;
        padding: 5px 0;

        @media (min-width: 768px) {
            margin-right: 40px;
        }
    }

    &__btn {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 30px;
        height: 30px;
        background: #f8f8f8;
        border: 0.743728px solid #9b9b9b;
        border-radius: 2.23118px;
        transition: all .1s linear;

        @media (min-width: 1200px) {
            &:hover {
                background: var(--redesign-second-color);
                border: 0.833327px solid #9b9b9b;
                border-radius: 2.49998px;

                &::before {
                    color: white;
                }
            }
        }

        &::before {
            font-style: normal;
            font-weight: 700;
            font-size: 14.4283px;
            line-height: 17px;
            text-align: center;
            color: #9b9b9b;
        }

        &_inc::before {
            content: '';
        }

        &_dec::before {
            content: '+';
        }
    }

    &__count {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 40px;
        height: 100%;
        border: none;

        font-style: normal;
        font-weight: 700;
        font-size: 12px;
        line-height: 14px;
        text-align: center;
        color: #9b9b9b;
    }

    &__btn-wrap {
        display: flex;
        flex-direction: column;

        @media (min-width: 768px) {
            flex-direction: row;
            align-items: flex-start;
        }

        .btn {
            width: 320px;
            max-width: 100%;
            height: 40px;
            margin-bottom: 20px;
            transition: all .1s linear;

            @media (min-width: 768px) {
                margin-bottom: 0;
                width: 238.5px;
                margin-right: 21px;
            }

            &:last-child {
                margin: 0;
            }
        }

        .btn-outline-primary {
            background-color: var(--redesign-second-text-color);
            border: 2px solid var(--redesign-second-color);

            &:hover {
                background-color: var(--redesign-second-color);
            }
            
            &:active {
                color: #000 !important;
            }
        }
    }

    &__price-change,
    &__not-available,
    &__report-admission {
        display: none;
        flex-direction: column;
        justify-content: flex-end;
        position: absolute;
        top: 1px;
        left: 0;
        width: 100%;
        height: calc(100% - 2px);
        padding: 10px 0;
        background-color: rgba(255, 255, 255, .9);
        z-index: 2;

        @media (min-width: 768px) {
            padding: 19px 0 18px 160px;
        }

        h2 {
            display: flex;
            align-items: center;
            position: relative;
            margin: 0 0 18px;
            padding: 5px 0;
            font-style: normal;
            font-weight: 400;
            font-size: 16px;
            line-height: 20px;
            letter-spacing: 0.01em;
            color: var(--redesign-second-color);

            @media (min-width: 768px) {
                font-size: 17px;
                margin-bottom: 38px;
            }

            &::before {
                content: '';
                display: block;
                position: relative;
                width: 24px;
                height: 24px;
                margin-right: 4px;
                background: url("https://cdn.ms.fandeco.ru/i/371/5d495d9ea6.png") center no-repeat;

                @media (min-width: 768px) {
                    margin-right: 10px;
                    background: url("https://cdn.ms.fandeco.ru/i/372/f5922fc10e.png") center no-repeat;
                }
            }
        }
    }

    &__report-admission {
        .btn-outline-primary {
            letter-spacing: 0.01em;
        }
    }

    &__loading {
        display: none;
        justify-content: center;
        align-items: center;
        position: absolute;
        top: 1px;
        left: 0;
        width: 100%;
        height: calc(100% - 2px);
        margin: 0;
        padding: 0;
        background-color: rgba(255, 255, 255, .9);
        z-index: 2;

        &::before {
            content: '';
            display: block;
            width: 32px;
            height: 32px;
            position: relative;
            background: url("data:image/svg+xml;base64,") center no-repeat;
            animation: rotate-card-loader 2.5s linear infinite both;
        }
    }

    &__count-limit {
        display: none;
        position: absolute;
        top: 0;
        left: 50%;
        width: 100px;
        height: auto;
        transform: translate(-50%, calc(-100% - 8px));
        padding: 8px 10px 7px;
        letter-spacing: 0;
        background: #000000;
        border-radius: 2px;
        font-style: normal;
        font-weight: 400;
        font-size: 10px;
        line-height: 12px;
        color: #ffffff;

        @media (min-width: 768px) {
            width: 197px;
            padding: 5px 10px 5px 10px;
            transform: translate(-50%,calc(-100% - 3px));
        }

        &::after {
            content: '';
            display: block;
            position: absolute;
            bottom: 0;
            left: 50%;
            width: 14px;
            height: 8.39px;
            transform: translate(-50%, 8px);
            border-radius: 2px;
            background: url("https://cdn.ms.fandeco.ru/i/373/0216653cba.svg") center no-repeat;
        }
    }

    &__order-product {
        display: none;
        width: 203px;
        height: auto;
        background: var(--redesign-main-color);
        border-radius: 3px;
        margin: 5px 0 9px;
        padding: 4px 8px;
        font-style: normal;
        font-weight: 400;
        font-size: 10px;
        line-height: 12px;
        text-align: center;
        color: white;
        letter-spacing: 0;

        @media (min-width: 768px) {
            margin: 0 0 11px;
            padding: 8px 8px;
            width: auto;
            font-size: 12px;
            line-height: 14px;
        }
    }

    &__favorites-link {
        color: var(--redesign-second-color) !important;

        @media (min-width: 1200px) {
            &:hover {
                color: #b24512 !important;
            }
        }
    }
}

@keyframes rotate-card-loader {
    0% { transform: rotate(0); }
    100% { transform: rotate(360deg); }
}