@import "border_outline";

.j_scroll {
  padding-bottom: 13px;
}

.mobile_menu {
  margin: 0;
  padding: 0;
  list-style: none;
  background-color: #f7f7f7;

  .js_cat_menu {
    display: none;
    background: #fff;
    margin: 0;
    padding: 0;
    list-style: none;
    .dropdown-toggle::after {
      display: none;
    }
  }

  > li {
    &:after, &:before {
      @extend .border_outline;
    }

    &:before {
      top: 0;
      border-top: 0.5px dotted rgba(153, 153, 153, .4);
    }

    &:after {
      top: 49px;
      border-bottom: 0.5px dotted rgba(153, 153, 153, .4);
    }

    &:not(.open) {
      &:after {
        display: none;
      }
    }
  }

  > li > ul > li {
    &:after, &:before {
      @extend .border_outline;
    }

    &:before {
      top: 0;
      border-top: 0.5px dotted rgba(153, 153, 153, .4);
    }

    &:after {
      bottom: 0;
      border-bottom: 0.5px dotted rgba(153, 153, 153, .4);
    }

    &.open {
      &:after {
        display: none;
      }
    }
  }

  &_icons > li > ul > li {
    @extend .border_outline_bottom;

    &.dropdown.open {
      > .js_cat_menu {
        &:after {
          bottom: 0;
          top: inherit;
        }
      }
    }
  }

  > li {
    &:after {
      bottom: inherit !important;
      top: 49px !important;
    }
  }

  li {
    li {
      @extend .border_outline_bottom;
    }

    .js_cat_menu li:last-child {
      &:after {
        display: none;
      }
    }

    min-height: 50px;

    > li {
      min-height: 40px !important;

      a {
        max-height: 40px;
        min-height: 40px;
      }
    }

    display: block;
    position: relative;

    &:last-child {
      border-bottom: 0;
    }

    > a {
      display: block;
      position: relative;
      padding-top: 13.5px;
      padding-left: 55px;
      padding-bottom: 13.5px;
      min-height: 50px;
      padding-right: 20px;
    }

    &.open > ul.js_cat_menu {
      display: block;
    }

    &.open {
      @extend .border_outline_bottom;
    }
  }

  ul li {
    background-color: #fff;
  }

  > li > a {
    padding-left: 10px;
  }

  .level3.js_cat_menu {
    @extend .border_outline_bottom;
    position: relative;
    &:after {
      top: 0;
    }
  }

  .level3 ul li, ul.level3 li {
    min-height: 40px;
    margin-left: 40px;

    &:after {
      top: 39px;
    }

    a {
      font-size: 12px;
      line-height: 14px;
      min-height: 40px;
      padding: 13px 10px 0 10px !important;
    }
  }

  .dropdown {

    > a {
      &:before {
        content: "";
        position: absolute;
        z-index: 1;
        width: 12px;
        height: 8px;
        background: url(../images/header_new/more_down.svg) no-repeat center center;
        right: 9px;
        /*
        margin-top: -4px;
        top: 25px;
        */
        top: 0;
        bottom: 0;
        margin: auto;
        /*   margin-top: 12px;*/

        -webkit-transform: rotate(-90deg);
        transform: rotate(-90deg);
        -webkit-transition: 0.2s;
        transition: 0.2s;
      }
    }

    &.open > a:before {
      transform: rotate(-180deg);
      /*  margin-top: -4px;*/
    }
  }

  &.mobile_menu_sales {
    border-top: 1px dotted rgba(153, 153, 153, 0.4);

    a {
      color: var(--redesign-second-color);
      font-size: 16px;
      font-weight: 400;
      border: 0;

      &.mobile_menu_sale {
        background: #f7f7f7 url(../images/header_new/subopen_sale.svg) no-repeat 10px center;
        padding-left: 50px;
        background-position: 11px 15px;
        padding-top: 12.5px;
      }
    }

    .js_cat_menu {
      a {
        padding-left: 10px;
        padding-top: 12.5px;
      }
    }

  }

  a.mobile_menu__parent {
    @extend .border_outline_top;
    font-weight: 700;
    background: #f7f7f7;
    font-size: 16px;
  }

  > li ul > li > a {
    padding-top: 14.5px;
  }

  .level3.js_cat_menu {
    &:before {
      content: "";
      width: 100%;
      height: 1px;
      left: 0;
      position: absolute;
      top: 0px;
      z-index: 1;
      border-top: 0.5px dotted rgba(153, 153, 153, .4);
    }


  }

  &_black {
    a {
      color: #000;
    }

    .mobile_menu_bottomLink {
      a {
        font-weight: 700;
        color: var(--redesign-second-color);
        background: #f7f7f7;
        font-size: 16px;
        padding-top: 12.5px;

        &:after {
          display: none;
        }
      }
    }

    &.mobile_menu_black_pb15 {
      padding-bottom: 1px;

      .mobile_menu__parent {
        padding-bottom: 15px;

        &:after {
          margin-top: -4px;
        }
      }
    }

    &.mobile_menu_icons {
      padding-bottom: 0;
    }
  }

  &_hr {
    @extend .border_outline_top;
    display: block;
    width: 100%;
    height: 1px;
    background-color: #eee;
  }

  &_icons {
    ul {
      a {
        padding: 0 10px 0 50px;
        font-size: 14px;
        position: relative;

        i, mark {
          flex: 0 0 auto;
          display: block;
          position: absolute;
          left: 5px;
          width: 40px;
          height: 40px;
          background-repeat: no-repeat;
          background-color: transparent;
          margin: auto;
          bottom: 0;
          top: 0;
        }

        mark {
          position: relative;
          padding: 0;
          margin: initial;

          img {
            display: block;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            object-fit: contain;
            border: 0;

            &::before {
              content: "";
              position: absolute;
              top: 0;
              left: 0;
              width: 100%;
              height: 100%;
              z-index: 2;
              background-color: #f7f7f7;
              background-position: center;
              background-repeat: no-repeat;
              background-size: cover;
              //background-image: url(data:image/svg+xml;base64,);
