  }

  .dropdown-toggle::after{
    display: none;
  }
}

.js_cat_menu {
  display: none;
}

.header_icons {
  .border_outline_top {
    &:after {
      border-top: 0.5px solid rgba(186, 186, 186, .4);
    }
  }

  .border_outline_bottom {
    &:after {
      border-bottom: 0.5px solid rgba(186, 186, 186, .4);
    }
  }

  .mobile_menu {
    > li {
      &:before {
        border-top: 0.5px solid rgba(186, 186, 186, .4);
      }

      &:after {
        border-bottom: 0.5px solid rgba(186, 186, 186, .4);
      }
    }

    > li > ul > li {
      &:before {
        border-top: 0.5px solid rgba(186, 186, 186, .4);
      }

      &:after {
        border-bottom: 0.5px solid rgba(186, 186, 186, .4);
      }
    }

    .level3 ul li, ul.level3 li {
      min-height: 50px;
      margin-left: 0;

      &:after { top: initial; }

      a {
        display: flex;
        align-items: center;
        min-height: 50px;
        padding: 0 10px 0 20px !important;

        mark {
          left: 0;
        }
      }
    }

    &.mobile_menu_sales {
      border-top: 1px solid rgba(186, 186, 186, 0.4);
    }

    .level3.js_cat_menu {
      &:before {
        border-top: 0.5px solid rgba(186, 186, 186, .4);
      }
    }

    &_icons ul a {
      display: flex;
      align-items: center;
      column-gap: 5px;
      padding: 0 10px 0 5px !important;

      mark {
        left: 0;
      }
    }
  }
}

