@import "spinner";
@import "modal";

.auth_container {
    position: relative;
}

.auth {
    display: grid;
    flex-direction: column;
    width: 100%;
    grid-auto-rows: 0;
    grid-template: 100%/100%;
    position: relative;
}

.auth_block {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
}

.auth_block_first {
    width: 100%;
    padding: 0 7px;
}

.auth_block_logo {
    border-radius: 12px;
    margin-bottom: 32px;
    width: 150px;
}

.auth_block_logo img {
    object-fit: contain;
    width: 100%;
    max-height: 100%;
    max-width: 100%;
}

.auth_block_title {
    margin-bottom: 37px;
    font-weight: 700;
    font-size: 24px;
    line-height: 25px;
    letter-spacing: 0.01em;
    color: #000000;
    text-align: center;
}

.auth_block_text {
    margin-bottom: 20px;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    letter-spacing: 0.01em;
    color: #666666;
    text-align: center;
}

.auth_block_inputs {
    position: relative;
}

.auth_block_inputs_phone {
    display: block;
}

.auth_block_inputs_phone + .auth_block_inputs_phone {
    margin-top: 10px;
}

.auth_block_inputs_phone label {
    display: block;
}

.auth_block_inputs_phone_outer {
    background-color: #fff;
    border: 1px solid #b1b1b1;
    border-radius: 3px;
    box-sizing: border-box;
    color: black;
    display: flex;
    overflow: hidden;
    position: relative;
    text-overflow: ellipsis;
    transition: all .2s linear;
    white-space: nowrap;
    will-change: transform, border-color, top, background-color;
}

.auth_mask_country .auth_block_inputs_phone_outer {
    padding-left: 92px;
}

.auth_flash_call {
    width: 100%;
    text-align: center;
    margin-bottom: 15px;
}

.auth_flash_call span {
    display: inline-block;
    width: 150px;
    height: 120px;
    background-size: 100%;
    background-position: center top;
    background-repeat: no-repeat;
    background-image: url("https://cdn.ms.fandeco.ru/i/711/932553b8a5.png");
}

.auth_block_inputs_phone_outer.auth_focus {
    border-color: var(--redesign-main-color);
}

.auth_block_inputs_phone_outer.auth_error {
    animation: _47-b9 .82s cubic-bezier(.36, .07, .19, .97) both;
    border-color: red;
}

.auth_block_inputs_phone_outer_row {
    display: flex;
    flex: 1;
    max-width: 100%;
    position: relative;
}

.auth_block_inputs_phone_outer_row input {
    writing-mode: horizontal-tb !important;
    text-rendering: auto;
    text-indent: 0;
    text-shadow: none;
    display: inline-block;
    appearance: auto;
    -webkit-rtl-ordering: logical;
    cursor: text;
    border-style: inset;
    border-color: -internal-light-dark(rgb(118, 118, 118), rgb(133, 133, 133));
    border-image: initial;
    box-sizing: border-box;
    font-style: normal;
    letter-spacing: 0.01em;
    text-decoration: none;
    text-transform: none;
    word-spacing: normal;

    /*  -webkit-text-fill-color: #001a34;*/
    background-color: hsla(0, 0%, 100%, 0);
    border: 0;
    caret-color: #000000;
    color: inherit;
    margin: 0;
    outline: none;
    text-align: inherit;
    vertical-align: top;
    width: 100%;
    font-size: 20px;
    line-height: 25px;
    padding: 6px 10px;
    min-height: 48px;
    font-weight: 400;

    @media (max-width: 767px) {
      font-size: 16px;
    }
}

.auth_block_inputs_phone_outer_row input {
    color: #fff;
}

.auth_block_inputs_phone_outer_row input.auth_mask_init {
    color: #000000;
}

.auth_block_inputs_phone_outer_row input.auth_mask_init + .auth_input_placeholder {
    color: inherit;
    font-size: 20px;
    line-height: 25px;
    letter-spacing: 0.01em;
    font-weight: 400;
    top: 12px;
    color: #b1b1b1;

    @media (max-width: 767px) {
        font-size: 16px;
    }
}


#auth_input_code .auth_block_inputs_phone_outer input {
    font-weight: 600;
    font-size: 32px;
    line-height: 25px;
    text-align: center;
    letter-spacing: 0.3em;
    color: #000000;
}

.auth_buttons {
    margin-top: 20px;
}

.auth_buttons .auth_btn {
    width: auto !important;
    margin: 0 auto;
    display: inline-block;
    font-weight: 400;
    text-align: center;
    white-space: nowrap;
    vertical-align: middle;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    border: 1px solid transparent;
    padding: 9px 34px 8px;
    font-size: 1rem;
    line-height: 1.5;
    border-radius: 0.25rem;
    transition: all .15s ease-in-out;
}

.auth_btn:not(:disabled):not(.disabled) {
    cursor: pointer;
}

.auth_btn.disabled, .auth_btn:disabled {
    filter: grayscale(100%);
    opacity: 0.3;
}

.auth_btn.btn-block {
    display: block;
    width: 100%;
}

.auth_btn.btn-info {
    color: #fff;
    background-color: var(--redesign-main-color);
    border-color: var(--redesign-main-color);

    &:hover {
        background-color: var(--redesign-sixth-color);
        border-color: var(--redesign-sixth-color);
    }
}

.auth_error {
    color: red;
    font-size: 12px;
    line-height: 16px;
    margin: 6px 0 0;
}

.auth_success {
    color: var(--redesign-main-color);
    font-size: 12px;
    line-height: 16px;
    margin: 6px 0 0;
}

.auth_message {
    font-size: 14px;
    text-align: center;
    display: none;
    padding: 15px 10px 5px 10px;
}

.auth_or {
    color: #b1b1b1;
    margin: 22px auto 28px;
    padding: 0 16px;
    position: relative;
    text-align: center;
    width: max-content;
    font-size: 14px;
    line-height: 18px;
    font-weight: 400;
    letter-spacing: 0.01em;
}

.auth_or:after, .auth_or:before {
    background-color: #b1b1b1;
    content: "";
    height: 1px;
    position: absolute;
    top: 8px;
    width: 64px;
}

.auth_or:after {
    right: -64px;
}

.auth_or:before {
    left: -64px;
}

.auth_forward_footer {
    display: inline-block;
    line-height: 1.29;
    margin-bottom: 10px;
    margin-top: 20px;
    text-align: center;
    width: 100%;
    font-size: 14px;
}

.auth_forward_links {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 20px;
}

.auth_forward_link {
    margin: 0 18px;
    width: 40px;
    height: 40px;
    position: relative;
}

.auth_forward_link {
    & > img, 
    & > svg {
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: contain;
    }
}

.auth_phone_change.auth_forward {
    display: block;
    margin-top: 7px;
    font-weight: 700;
    font-size: 12px;
    line-height: 18px;
    text-align: center;
    letter-spacing: 0.01em;
    color: var(--redesign-main-color);
}

#auth_input_phone_code .auth_forward_footer {
    margin-top: 0;
}

.auth_block_inputs_phone_outer_row input.auth_input_no_mask {
    padding-top: 10px;
    font-weight: 400;
    font-size: 12px;
    line-height: 16px;
}

.auth_block_inputs_phone_outer_row .auth_input_placeholder {
    font-weight: 400;
    font-size: 12px;
    line-height: 16px;
    top: 16px;
    color: #b1b1b1;

}

.auth_country {
    box-sizing: border-box;
    line-height: 1.15;
    width: 100%;
    max-width: 100px;
    height: 100%;
    left: 0;
    padding: 9px 12px;
    position: absolute;
    top: 0;
}

.auth_country > div {
    align-items: center;
    background-color: #f8f8f8;
    border: 0;
    border-radius: 3px;
    display: flex;
    height: 100%;
    max-height: 34px;
}

.auth_country > div:focus-within {
    background-color: rgba(0, 48, 120, .08);
}

.auth_country_block {
    box-sizing: border-box;
    color: #000000;
    display: flex;
    overflow: hidden;
    position: relative;
    text-overflow: ellipsis;
    transition: border-color .2s cubic-bezier(.25, .8, .25, 1);
    white-space: nowrap;
    will-change: transform, border-color, top, background-color;
}

.auth_country_blocks {
    display: flex;
    flex: 1;
    max-width: 100%;
    position: relative;
}

.auth_country_block_1 {
    display: block;
    width: 100%;
}

.auth_country_block_2 {
    align-items: center;
    bottom: 0;
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-end;
    margin-right: 8px;
    pointer-events: none;
    position: absolute;
    right: 0;
    height: 28px;
    width: 28px;
}

.auth_country_block_1_number {
    box-sizing: border-box;
    display: flex;
    margin: 0;
    position: relative;
    font-size: 14px;
    line-height: 19px;
    min-height: 28px;
    padding: 5px 10px;
}

.auth_country_block_1_number_1 {
    box-sizing: border-box;
    max-width: 100%;
    padding-right: 5px;
    align-items: center;
    display: flex;
    flex: 1;
    flex-wrap: wrap;
    overflow: hidden;
    position: relative;
}

.auth_country_block_1_number span {
    flex-grow: 1;
    text-align: center;
}

.auth_country_block_1_number_2 {
    color: #96a3ae;
    font-size: 12px;
    height: 16px;
    line-height: 16px;
    pointer-events: none;
    position: absolute;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
    width: 16px;
}

.auth_input_placeholder {
    display: block;
    left: 10px;
    pointer-events: none;
    position: absolute;
    text-align: left;
    transform-origin: 0 0;
    transition: transform .2s cubic-bezier(.25, .8, .25, 1);
    white-space: pre-wrap;
    will-change: transform;
    margin: 0;
    font-size: 20px;
    line-height: 25px;
    top: 12px;
    color: #b1b1b1;
    font-weight: 400;
    letter-spacing: 0.01em;
}

.auth_focus .auth_input_placeholder, .auth_input_placeholder_focus {
    transform: translateY(-10px) scale(.75);
}

.auth_block_inputs_phone_outer_row input.auth_input_no_mask {
    color: #000;
}

@keyframes shake-circle-01 {
    0% {
        transform: translate(-4px, -0px);
    }
    33% {
        transform: translate(4px, -0px);
    }
    66% {
        transform: translate(-4px, -0px);
    }
    100% {
        transform: translate(4px, -0px);
    }
}

.auth_block_inputs_phone_outer.shake {
    animation: shake-circle-01 .25s infinite alternate;
}

.auth_country_select .auth_country {
    max-width: 110px;
}

.auth_country_select .auth_country_block_1_number_1 {
    padding-right: 18px;
}

.auth_country_select .auth_block_inputs_phone_outer {
    padding-left: 96px;
}

.auth_country_select .auth_country_block {
    cursor: pointer;
}

.auth_loading .auth_block_inputs_phone_outer {
    background-color: rgba(238, 238, 238, 0.5);
}

.auth_block_error {
    margin-bottom: 4px;
}

.auth_timer_container {
    line-height: 1.29;
    text-align: center;
    width: 100%;
    font-size: 13px;
}

.auth_timer_resend {
    display: none;
}

.auth_timer_text {
    display: block;
    margin-top: 20px;
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    text-align: center;
    letter-spacing: 0.01em;
    color: #798b98;
}

.auth_modal .auth_resend_code {
    display: inline-block;
    margin-top: 7px;
    font-weight: 700;
    font-size: 12px;
    line-height: 18px;
    text-align: center;
    letter-spacing: 0.01em;
    color: var(--redesign-main-color);
}

#auth_modal input[name=email] {
    text-transform: lowercase;
}

/* auth_btn_social */
.auth_btn.auth_btn_social {
    font-size: 14px;
}
.auth_btn.auth_btn_apple {
    background-color: #000;
    border-color: #000;
}

.auth_btn.auth_btn_apple:hover {
    background-color: rgba(0, 0, 0, 0.89);
    border-color: rgba(0, 0, 0, 0.89);
}

.auth_btn.auth_btn_gosuslugi {
    background-color: #f2f5f9;
    border-color: #f2f5f9;
    color: #001a34;
}

.auth_btn.auth_btn_gosuslugi:hover {
    background-color: #dbdee1;
    border-color: #dbdee1;
    color: #001a34;
}

.auth_icon {
    width: 24px;
    height: 24px;
    display: inline-block;
    margin-right: 5px;
}

.auth_icon__apple {
    background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink'%3E %3Crect width='24' height='24' fill='url(%23pattern0)'/%3E %3Cdefs%3E %3Cpattern id='pattern0' patternContentUnits='objectBoundingBox' width='1' height='1'%3E %3Cuse xlink:href='%23image0_1_6' transform='scale(0.0416667)'/%3E %3C/pattern%3E %3Cimage id='image0_1_6' width='24' height='24' xlink:href='data:image/png;base64,'/%3E %3C/defs%3E %3C/svg%3E ");
}

.auth_icon__gosuslugi {
    background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink'%3E %3Crect width='24' height='24' fill='url(%23pattern0)'/%3E %3Cdefs%3E %3Cpattern id='pattern0' patternContentUnits='objectBoundingBox' width='1' height='1'%3E %3Cuse xlink:href='%23image0_1_4' transform='scale(0.0416667)'/%3E %3C/pattern%3E %3Cimage id='image0_1_4' width='24' height='24' xlink:href='data:image/png;base64,'/%3E %3C/defs%3E %3C/svg%3E ");
