.top_0 {
  &:after {
    top: 0 !important;
  }
}

.border_outline {
  &:after {
    content: "";
    width: 100%;
    height: 1px;
    left: 0;
    position: absolute;
  }
}

.border_outline_top {
  @extend .border_outline;

  &:after {
    top: 0;
    border-top: 0.5px dotted rgba(153, 153, 153, .4);
  }
}

.border_outline_bottom {
  @extend .border_outline;
  &:after {
    bottom: 0;
    border-bottom: 0.5px dotted rgba(153, 153, 153, .4);
  }
}
