%btn_hover {
  border-color: var(--redesign-second-color);
}

.banner_resize {
  @include mobile() {
    height: 35px;
  }
}

.inside {
  position: absolute;
  border: 1px solid #999;
  width: 100%;
  height: 100%;
  z-index: 1;
}


.card_men_and_woman {
  padding: 20px 0;

  &_content {
    z-index: 2;
    position: relative;


    @include desktop() {
      min-height: 191px;
      max-height: 191px;
      display: flex;
      align-items: center;
      padding: 20px 23px 20px 0;
    }


    @include mobile() {
      padding: 20px 20px 30px 20px;
    }
  }

  &__img {
    height: 100%;
    text-align: center;
    vertical-align: middle;


    @include desktop() {
      position: absolute;
      max-height: 152px;
      line-height: 194px;
      top: -6px;
      width: 194px;

    }

    @include mobile() {
      min-height: 194px;
      max-height: 194px;
      line-height: 194px;
      margin-bottom: 22px;
    }


  }

  &__text {
    display: block;
    @include desktop() {
      margin-left: 197px;
      padding: 26px 0;
    }
  }

  .btn {
    width: 163px;
    height: 50px;
    line-height: 26px;

    &:hover {
      @extend %btn_hover
    }

    @include mobile() {
      width: 100%;
    }
  }

  &_body {
    position: relative;
  }
}

.card_men_and_woman__text {
  &.hover {
    .btn {
      @extend %btn_hover
    }
  }

}

/* Active */
.card_men_and_woman_body_text {
  display: none;
}

.card_men_and_woman.active {
  .card_men_and_woman__text {
    margin-top: -23px;
  }

  .btn {
    display: none;
  }

  .card_men_and_woman_body_text {
    min-height: 105px;
    display: block;
    top: 10px;
    position: relative;
  }

  .card_men_and_woman__title {
    top: 10px;
  }
}


.maw_body {
  width: 100%;
  color: #000000;
  font-style: normal;
  font-weight: normal;


  @include desktop() {
    /* Auto layout */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    padding: 0;
    height: 140px;
    padding-right: 20px;
  }

  p {
    margin: 5px 0;
    align-items: center;
    flex: none;
    flex-grow: 0;

    @include mobile() {
      margin: 14.5px 0;
    }
  }
}

.maw_title {
  font-weight: bold !important;
  font-size: 21px;
  line-height: 25px;
  order: 0;
}

.maw_title_orange {
  font-size: 17px;
  line-height: 20px;
  color: var(--redesign-second-color);
  order: 1;
}

.maw_title_green {
  font-size: 14px;
  line-height: 16px;
  order: 2;
}

.maw_title_green_2 {
  font-size: 16px;
  line-height: 19px;
  order: 2;
}

.maw_button {
  order: 1;
}

.maw_buttons {
  p {
    margin: 7.5px 0;
  }

  @include mobile() {
    p {
      margin: 0 0 14px 0;
    }
  }

  @include mobile() {
    p.maw_button {
      margin: 0;
    }
  }
}

.receive_gift {
  .maw_title {
    color: var(--redesign-second-color);
  }
}

.fulfill_conditions, .receive_gift, .maw_buttons {
  display: none;
}


.fulfill_conditions_active .fulfill_conditions {
  display: block;
}

.receive_gift_active .receive_gift {
  display: block;
}

.maw_buttons_active .maw_buttons {
  display: block;
}


@include mobile() {
  .fulfill_conditions .maw_title_green {
    margin-bottom: 0;
  }
  .receive_gift .maw_title_green_2 {
    font-size: 14px;
    line-height: 16px;
    margin-bottom: 0;
  }
}
