@media (max-width: 1199px) {
  /*Compare*/
  .compare .compare-goods-unit {
    padding: 15px 15px 0;
  }

  .compare .compare-table .tabunit .flbox .flel {
    padding-left: 15px;
  }
}

@media (max-width: 991px) {
  .compare .compare-goods {
    padding-left: 0;
    width: 100%;
    margin-top: 23px;
  }

  .compare .compare-table .tabunit {
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
  }

  .compare .compare-table .tabunit .flbox {
    padding-left: 0;
  }

  .compare .compare-table .tabunit .tabunit-title {
    -ms-flex-preferred-size: 100%;
    flex-basis: 100%;
    text-align: center;
    border-right: 1px solid #e3e3e3;
  }

  .ncatlist h2 {
    display: none;
  }

  .ncatlist h2.mob {
    display: block;
    position: relative;
  }

  .ncatlist h2.mob .open-filter-categories {
    position: absolute;
    z-index: 1;
    right: 0;
    top: 0;
    border: none;
    background: url("../images/open-filter-categories.png") no-repeat;
    width: 26px;
    height: 24px;
  }

  .ncatlist .ncatfilter form {
    overflow: auto;
    display: none;
    opacity: 0;
    -webkit-transition: opacity 0.3s;
    transition: opacity 0.3s;
    background: #f6f6f6;
    margin-bottom: 30px;
  }

  .ncatlist .ncatfilter form .form-filter-close {
    display: block;
    background: url("../images/mobclosefilter.png") no-repeat;
    width: 12px;
    height: 12px;
    border: none;
  }

  .ncatlist .ncatfilter form.active {
    display: block;
    opacity: 1;
    position: fixed;
    z-index: 9999;
    left: 0;
    top: 0;
    margin-top: 0;
    bottom: 0;
    width: 100%;
    margin-bottom: 0;
  }


  .ncatlist .row > .col-md-10.pl30 {
    padding-left: 0px;
  }

}

@media (max-width: 767px) {
  .shopun-icons {
    padding-left: 22px;
  }

  /**/
  .ncatpaginator {
    clear: both;
  }

  .ncatpaginator a.link-all {
    margin-bottom: 15px;
  }

  .ncat-labels button:nth-child(n + 7) {
    display: none;
  }

  .ncat-labels button:nth-child(n + 7).openmore-ncatlab {
    display: block;
    border: none;
    padding-left: 0;
  }

  .ncat-labels button:nth-child(n + 7).openmore-ncatlab span {
    display: inline-block;
    padding-right: 15px;
    font-size: 13px;
    color: #58afcf;
    font-weight: 400;
    position: relative;
  }

  .ncat-labels button:nth-child(n + 7).openmore-ncatlab span:before {
    content: "";
    position: absolute;
    z-index: 1;
    width: 9px;
    height: 6px;
    right: 0;
    top: 50%;
    margin-top: -3px;
    background: url("../images/down-blue.png") no-repeat;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
  }

  .ncat-labels.active button {
    display: inline-block;
  }

  .ncat-labels.active button.openmore-ncatlab {
    display: block;
  }

  .ncat-labels.active button.openmore-ncatlab span:before {
    -webkit-transform: rotate(180deg);
    transform: rotate(180deg);
  }


}

@media screen and (max-width: 650px) {
  .cart-unit-percent .cart-unit-val-img {
    position: relative;
  }

  .cart-unit-percent .cart-unit-val-img:before {
    content: "";
    position: absolute;
    z-index: 2;
    left: 0;
    top: 10px;
    width: 65px;
    height: 40px;
    background: url("../images/cart/cart-unit-percent.png") no-repeat;
  }
}

@media screen and (max-width: 570px) {
  .cart-unit-percent .cart-unit-val-img:before {
    background-size: 100% 100%;
    height: 20px;
    width: 33px;
  }

  /**/
  .ncatpaginator a,
  .ncatpaginator button,
  .ncatpaginator span {
    display: none !important;
  }

  .ncatpaginator a.link-all {
    display: block !important;
    width: 100%;
  }

}


@media screen and (max-width: 991px) {
  .flat_page h1.sf_h1 {
    display: none;
  }
}

@media screen and (max-width: 570px) {
  .content p img {
    max-width: 100%;
  }
}

@media screen and (max-width: 450px) {
  .content p img {
    max-width: 100%;
    display: block;
  }
}

@media (min-width: 991px) {
  .ncat-labels {
    margin-bottom: 46px;
    display: block;
    margin-top: 6px;
    margin-left: -15px;
  }

  .ncatlist .ncat-labels button.openmore-ncatlab {
    padding-left: 20px;
  }
  .form-filter-title {
    display: none;
  }
  .ncatlist .ncat-labels {
    margin-top: 20px;
  }

  .ncatfiltersectorbtn {
    display: none;
  }
}

