.auth_modal {
    display: block; // default value's none
    overflow: hidden;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1050;
    -webkit-overflow-scrolling: touch;
    outline: 0;
    background-color: rgba(0,0,0,0.4);

    a {
        color: var(--redesign-main-color);
        transition: color .1s linear;

        @media (min-width: 1200px) {
           &:hover {
               color: var(--redesign-second-color);
           }
        }
    }
}

.fade.show {
    opacity: 1;
}

.auth_modal.fade .modal-dialog {
    -webkit-transform: translate(0, -25%);
    -ms-transform: translate(0, -25%);
    -o-transform: translate(0, -25%);
    transform: translate(0, -25%);
    -webkit-transition: -webkit-transform 0.3s ease-out;
    -o-transition: -o-transform 0.3s ease-out;
    transition: transform 0.3s ease-out;
}

.auth_modal.in .auth_modal, .auth_modal.show .auth_modal-dialog {
    -webkit-transform: translate(0, 0);
    -ms-transform: translate(0, 0);
    -o-transform: translate(0, 0);
    transform: translate(0, 0);
}

.auth_modal-open .auth_modal {
    overflow-x: hidden;
    overflow-y: auto;
    display: block;
    padding-left: 15px;
}

.auth_modal-dialog {
    position: relative;
    width: 90%;
    max-width: 416px;
    margin: 30px auto;
}

.auth_modal-content {
    position: relative;
    background-color: #ffffff;
    border: 1px solid #b1b1b1;
    border-radius: 20px;
    box-shadow: none !important;
    -webkit-background-clip: padding-box;
    background-clip: padding-box;
    outline: 0;
    padding: 30px 15px;

    @media (min-width: 768px) {
        padding: 43px 50px;
    }
}

.auth_modal-backdrop {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1040;
    background-color: #000000;
}

.auth_modal-backdrop.fade {
    opacity: 0;
    filter: alpha(opacity=0);
}

.auth_modal-backdrop.in, .auth_modal-backdrop.show {
    opacity: 0.5;
    filter: alpha(opacity=50);
}

.auth_modal-header {
    position: relative;
    margin-bottom: 20px;
    padding: 0;

    &::before {
        content: '';
        display: block;
        margin: 0 auto;
        position: relative;
        z-index: 2;
        width: 170px;
        height: 39px;
        background-color: white;
        background-repeat: no-repeat;
        background-position: center;
        background-image: url("data:image/svg+xml;base64,");
    }

}

.auth_forward_footer .auth_forward {
    display: block;
    font-weight: 700;
    font-size: 12px;
    line-height: 18px;
    text-align: center;
    letter-spacing: 0.01em;
    color: var(--redesign-main-color);
}

.auth .auth_forward_footer {
    margin-bottom: 0;
}

.auth .auth_forward_footer a {
    display: inline-block;
    font-weight: 700;
    font-size: 12px;
    line-height: 18px;
    text-align: center;
    letter-spacing: 0.01em;
    color: var(--redesign-main-color);

    @media (min-width: 1200px) {
        &:hover {
            color: var(--redesign-second-color);
        }
    }
}

.auth_forward_footer .auth_forward_links {
    margin-bottom: 27px;
}

.auth_modal-close {
    padding: 0;
    background-color: transparent;
    border: 0;
    z-index: 1050;
    position: absolute;
    right: -25px;
    top: -33px;
    cursor: pointer;

    @media (max-width: 767px) {
        right: 4px;
        top: -28px;
    }
}

.auth_modal-close span {
    font-size: 2.8rem;
    font-weight: 300;
    line-height: 1;
    color: #000;
    opacity: .45;
    cursor: pointer;
    transition: all .1s linear;
}

.auth_modal-close span:hover {
opacity: .8;
}

.in.auth_modal-header .close {
margin-top: -2px;
}

.auth_modal-title {
    margin: 0;
    line-height: 1.42857143;
}

.auth_modal-body {
position: relative;
}

.auth_modal-backdrop {
opacity: 0.5;
position: fixed;
top: 0;
right: 0;
bottom: 0;
left: 0;
z-index: 1040;
background-color: #000;
}

@media (max-width: 400px) {
    .auth_modal-open .auth_modal {
        padding-left: 0 !important;
    }
}
