.ncatlist .newfilterj .ncatfilcontainer {
    border-top: none;
    padding-left: 10px;
    padding-bottom: 0;
}

.ncatlist .newfilterj .ncatslrange.ncatfilcontainer {
    padding-left: 15px;
    padding-right: 15px;
    margin-top: 5px;
    margin-bottom: 0;
}
.ncatfiltersectoropener {
    position: relative;
}

.ncatlist .ncatfilcontainer {
    padding-top: 10px;
}

.ncatlist .newfilterj .ncatfiltitopener {
    padding-left: 10px;
}

.ncatlist .newfilterj .ncatfiltersector {
    padding: 7px 0 0;
    background: transparent;
    border: none;
}

.ncatlist .newfilterj .ncatfiltit {
    font-size: 13px;
    padding-top: 5px;
    padding-bottom: 5px;
    margin-right: 8px;
    font-weight: 700;
}

.ncatfiltit span {
    position: relative;
}

.ncatlist .ncatfiltitopener:before {
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
    position: absolute;
    z-index: 2;
    content: "";
    width: 9px;
    right: 0;
    top: 50%;
    margin-top: -3px;
    height: 10px;
    background: url("data:image/jpeg;base64,")
        no-repeat center center;
}

.ncatlist .newfilterj .ncatfiltitopener.active:before {
    -webkit-transform: rotate(-90deg);
    transform: rotate(-90deg);
}

/* SEARCH */
.ncatfilcontainer .the_search {
    position: relative;
    padding-top: 5px;
    margin-bottom: 12px;
    margin-top: 0px;
}

.ncatfilcontainer .the_search input {
    width: 100%;
    height: 30px;
    padding: 0 10px;
    border: 1px solid #999999;
    border-radius: 3px;
    font-size: 13px;
}

.btn_reset_search {
    position: absolute;
    display: none;
    right: 6px;
    top: 12px;
}

.main_filters .filter_footer {
    border: none;
    -webkit-box-shadow: none;
    box-shadow: none;
    background: transparent;
    padding: 0;
    font-size: 11px;
    position: absolute;
    top: 33px;
    left: 10px;
    z-index: 2;
    line-height: 13px;
    color: #999999;
}

.newfilterj .filter_reset_btn button {
    border: none;
    -webkit-box-shadow: none;
    box-shadow: none;
    background: transparent;
    padding: 0;
    font-size: 11px;
    position: absolute;
    top: 100%;
    left: 0;
    z-index: 2;
    margin-top: 2px;
    line-height: 13px;
    color: #999999;
}

.newfilterj .filter_reset_btn button:hover {
    border-bottom: 0;
}

.newfilterj .filter_reset_btn button i {
    display: none;
}

.newfilterj .check_wrap {
    max-height: 190px;
}
.ncatfilterun {
    max-height: 310px;
    overflow: hidden;
}

.ncatfilterun .the_search .btn_reset_search {
    position: absolute;
    right: 0;
    top: 5px;
    width: 30px;
    height: 30px;
    border: none;
    z-index: 8;
    background: url("data:image/png;base64,")
        no-repeat center center/11px auto;
    -webkit-transition: 0.2s;
    transition: 0.2s;
}

.terms_filter_empty_results {
    position: absolute;
    top: 101px;
    font-size: 13px;
    font-style: italic;
}
