.hide {
  display: none !important;
}
.widget_wrap {
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 9999;
  padding: 10px 20px;
  background: #222;
  border-bottom-right-radius: 10px;
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
  -webkit-transform: translate(-100%, 0);
  -ms-transform: translate(-100%, 0);
  transform: translate(-100%, 0)
}

.widget_wrap:after {
  content: " ";
  position: absolute;
  top: 0;
  left: 100%;
  width: 24px;
  height: 24px;
  background: #222 url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABAAAAAQAgMAAABinRfyAAAABGdBTUEAALGPC/xhBQAAAAxQTFRF////////AAAA////BQBkwgAAAAN0Uk5TxMMAjAd+zwAAACNJREFUCNdjqP///y/DfyBg+LVq1Xoo8W8/CkFYAmwA0Kg/AFcANT5fe7l4AAAAAElFTkSuQmCC) no-repeat 50% 50%;
  cursor: pointer
}

.widget_wrap.widget_active {
  -webkit-transform: translate(0, 0);
  -ms-transform: translate(0, 0);
  transform: translate(0, 0)
}

.widget_item {
  padding: 0 0 10px
}

.widget_link {
  color: #fff;
  text-decoration: none;
  font-size: 15px;
}

.widget_link:hover {
  color: #fff;
  text-decoration: underline
}

.widget_label {
  color: #fff;
  margin: 10px 0;
}
