html {
  font-size: 14px;
  /*height: 100%;*/
  letter-spacing: 0.01rem;
  /*
    * Blank page
    */
}

html body {
  height: 100%;
  background-color: #fff;
  direction: ltr;
  overflow-wrap: break-word;
}

html body p {
  line-height: 1.5rem;
}

html .container-fix {
  max-width: 1600px;
}

html .content-page {
  padding: 0;
  position: relative;
  transition: 300ms ease all;
  backface-visibility: hidden;
  min-height: calc(100% - 3.35rem);
  margin-left: 260px;
}

html .content-page.app-content {
  padding: calc(20px + 4.45rem + 1.3rem) 20px 0;
}

html .content-page.app-content.show-overlay .content-overlay {
  z-index: 10;
  opacity: 1;
}

html .content-page.app-content.show-overlay .content-overlay ~ .header-navbar-shadow {
  display: none;
}

