  margin: 0;
  height: auto;
  padding: 0;
  transition: color .1s linear;
}

.suggestions-product.updated .suggestions-product_price::before {
  display: none;
}

.suggestions-product.updated .suggestions-product_price i, .suggestions-product.updated .suggestions-product_price mark, .suggestions-product.updated .suggestions-product_price .mark {
  display: flex;
  align-items: center;
  width: auto;
  height: 23px;
}

.suggestions-product.updated .suggestions-product_price i {
  min-width: 95px;
  color: #b1b1b1;
  font-size: 17px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  text-decoration: line-through;
}

.suggestions-product.updated .suggestions-product_price mark, .suggestions-product.updated .suggestions-product_price .mark {
  margin: 0 0 0 3px;
  padding: 2px;
  border-radius: 3px;
  background: var(--redesign-second-color);
  color: var(--redesign-text-color);
  text-align: center;
  font-size: 17px;
  font-style: normal;
  font-weight: 700;
}

.suggestions-product.updated .suggestions-product_price span {
