.auth_checkboxes {
  margin-top: 20px;
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.auth_checkbox {
  position: absolute;
  opacity: 0;
  cursor: pointer;
}

.auth_checkbox_label {
  position: relative;
  padding-left: 35px;
  cursor: pointer;
  display: block;
  user-select: none;
  line-height: 1.5;
  font-size: 14px;
}

.auth_checkbox_label:before {
  content: '';
  position: absolute;
  left: 0;
  top: 2px;
  width: 20px;
  height: 20px;
  border: 2px solid #ddd;
  border-radius: 4px;
  background: white;
  transition: all 0.2s ease;
}

.auth_checkbox:checked + .auth_checkbox_label:before {
  border-color: #ddd;
  background: white;
}

.auth_checkbox:checked + .auth_checkbox_label:after {
  content: '';
  position: absolute;
  left: 0;
  top: 2px;
  width: 20px;
  height: 20px;
  background: url(data:image/png;base64,) no-repeat;
  background-size: 10px auto;
  background-position: 4px center;
}

.auth_checkbox_label a {
  color: #2196F3;
  text-decoration: underline;
}

.auth_checkbox_label a:hover {
  text-decoration: none;
}

.msoneclick_form .auth_checkboxes,
#cooperform .auth_checkboxes {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}

.msoneclick_form .auth_checkboxes > div,
#cooperform .auth_checkboxes > div {
  max-width: 320px;
}

.msoneclick_form .auth_checkbox_label,
#cooperform .auth_checkbox_label {
  text-align: left;
}

.auth_checkboxes.new_comment > div {
  max-width: 100%;
}

