.listing_filters_wrapperList {
    display: none;
    padding: 0 0 15px 0;
    .list {
        padding: 0;
    }
}

.listing_filters_wrapperHeadline {
    font-size: 14px;
    line-height: 16px;
    padding: 12px 20px 11px 10px;
    font-weight: 700;
    border-radius: 0;
    overflow: hidden;
    //background: #f7f7f7;
    border-bottom: 1px dotted #999999;
    position: relative;
    width: auto;
    margin-left: 0px;
    cursor: pointer;
}

.filters_categories .listing_filters_wrapperHeadline {
    background: #f7f7f7;
}

.listing_filters_wrapperHeadline.active:after {
    -webkit-transform: rotate(90deg);
    transform: rotate(90deg);
}

.listing_filters_wrapperHeadline:after {
    content: "";
    position: absolute;
    right: 3px;
    top: 10px;
    z-index: 1;
    width: 20px;
    height: 20px;
    background: url("data:image/svg+xml;base64,")
        no-repeat center center/5px auto;
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
    -webkit-transition: 0.3s;
    transition: 0.3s;
}

.listing_filters_slide_choice {
    clear: both;
    margin-left: 0px;
    font-size: 14px;
    line-height: 16px;
    padding: 0;
    border-bottom: 1px dotted #999999;
    overflow: hidden;
    position: relative;
    background: #f7f7f7;
    -webkit-transition: 0.2s;
    transition: 0.2s;
    list-style: none;

    a {
        display: block;
        padding: 12px 10px 11px 20px;
    }
}

.listing_filters_slide_choice:after {
    content: "";
    position: absolute;
    right: 0;
    width: 20px;
    height: 100%;
    top: 0;
    background: #ffffff;
    background: #f7f7f7;
}

.select_li_choice {
    font-size: 14px;
    line-height: 16px;
    padding: 0;
    border-bottom: 1px dotted #999999;
    overflow: hidden;
    position: relative;
    background: #f7f7f7;
    -webkit-transition: 0.2s;
    transition: 0.2s;
    cursor: pointer;
    list-style: none;

    &:hover {
        color: var(--redesign-second-color);
    }

    &::after {
        content: "";
        z-index: 1;
        left: 17px;
        width: 7px;
        height: 1px;
        background: $-color_b;
        top: 19px;
        position: absolute;
        transition: 0.2s;
    }

    a {
        display: block;
        padding: 12px 30px 11px;
    }
}

.select_li_choice.selected {
    font-weight: bold;
    &::after {
        height: 2px;
        top: 18px;
    }
}

.select_li_choice:hover {
    background: #ffffff;
}

.select_li_choice:hover:before {
    background: #ffffff;
    background: -webkit-gradient(linear, left top, right top, from(transparent), to(#ffffff));
    background: linear-gradient(to right, transparent, #ffffff);
}
