$ecardimg: 224px;

.ecommerce-card {

  @include mobile() {
    .item-dots {
      display: none;
    }
  }


  .eslider {
    position: relative;
    width: $ecardimg;
    height: $ecardimg;
    user-select: none;
    touch-action: pan-y;

    &::before {
      content: "";
      display: block;
      position: absolute;
      top: 0;
      right: -1px;
      width: 2px;
      height: 100%;
      background: white;
      z-index: 3;
    }
  }

  .eslider img {
    poiner-events: none;
  }

  .eslider-list {
    width: $ecardimg;
    height: $ecardimg;
    overflow: hidden;
  }

  .eslider-list.grab {
    cursor: grab;
  }

  .eslider-list.grabbing {
    cursor: grabbing;
  }

  .eslider-track {
    display: flex;
  }

  .eslide {
    width: $ecardimg;
    height: $ecardimg;
    flex-shrink: 0;
    font-size: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 0 solid #000;
  }

  .eslider-arrows {
    margin-top: 15px;
    text-align: center;
  }
}


.grid-view {
  .ecommerce-card {
    .eslider-list, .eslide, .eslider {
      width: 224px;
      height: 224px;
      @include mobile() {
        width: 140px;
        height: 140px;
      }
    }
  }
}

.list-view {
  .ecommerce-card {
    .eslider-list, .eslide, .eslider {
      width: 224px;
      height: 224px;
      @include mobile() {
        width: 280px;
        height: 280px;
      }
    }
  }
}
