.afterword {
    padding: 20px 0;

    .container {
        @media (min-width: 1200px) {
            max-width: 1200px !important;
        }
    }

    h1, h2 {
        display: block;
        margin: 0;
        padding: 20px 20px 20px 0;
        color: #000000;
        font-style: normal;
        font-weight: 700;
        font-size: 16px;
        line-height: 20px;
        letter-spacing: 0.01em;

        @media (min-width: 1200px) {
            font-size: 20px;
            line-height: 24px;
        }
    }

    p {
        display: block;
        margin: 0;
        padding: 10px 0 30px;
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;
        letter-spacing: 0.01em;
        color: #000000;

        @media (min-width: 1200px) {
            padding-top: 0;
            padding-bottom: 40px;
            font-size: 16px;
            line-height: 21px;
            letter-spacing: 0.2px;
        }
    }

    p + ul {
        margin-top: -20px;
    }

    ul, ol {
        display: block;
        list-style: none;
        margin: 0;
        padding: 10px 20px 20px;
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;
        letter-spacing: 0.01em;
        color: #000000;

        @media (min-width: 1200px) {
            padding-left: 26px;
            font-size: 17px;
            line-height: 20px;
        }

        li {
            position: relative;
            list-style: none;
            margin: 0;
            padding: 0 0 0 20px;
            color: #000000;

            &::before {
                content: '';
                display: block;
                position: absolute;
                top: 6px;
                left: 9px;
                width: 3px;
                height: 3px;
                border-radius: 50%;
                background-color: rgba(0, 0, 0, 1);

                @media (min-width: 1200px) {
                    top: 8px;
                    left: 5px;
                }
            }
        }
    }

    ol {
        padding-left: 40px;

        li {
            list-style: auto;
            padding-left: 10px;

            &::before { display: none; }
        }
    }

    a {
        font-weight: inherit;
        font-size: inherit;
        font-style: inherit;
        color: var(--redesign-main-color);
        text-decoration: underline !important;

        @media (min-width: 1200px) {
            &:hover {
                text-decoration: none !important;
            }
        }
    }
}