.apply-filters-float-btn {
  border-radius: 8px;
  width: 80px;
  padding: 0;
  font-size: 14px;
  line-height: 30px;
  height: 44px;
  border: none;
  outline: none;
  cursor: pointer;
  text-align: center;
  color: #fff;
  background-image: linear-gradient(to top, #da4e13, #e1601e);
  font-weight: bold;
  position: absolute;
  right: -90px;
  margin-top: 14px;
  z-index: 8;
}

.apply-filters-float-btn:before {
  display: block;
  content: "";
  position: absolute;
  left: -5px;
  top: 10px;
  background-image: linear-gradient(45deg, #da4e13, #f9621c);
  width: 25px;
  height: 25px;
  border-radius: 4px;
  transform: rotate(135deg);
  z-index: 0;
}

.apply-filters-float-btn:after {
  border-radius: 8px;
  box-shadow: inherit;
  display: block;
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  background-image: inherit;
  border-radius: inherit;
  width: 100%;
  height: 100%;
  z-index: 1;
}

.apply-filters-float-btn:hover {
  background-image: linear-gradient(to bottom, #da4e13, #f9621c);
  box-shadow: inset 0 -2px 0 0 rgba(0, 0, 0, 0.2);
}

.apply-filters-float-btn:hover:before {
  background-image: linear-gradient(45deg, #da4e13, #f9621c);
  box-shadow: inset -2px 0 0 0 rgba(0, 0, 0, 0.2);
}

.apply-filters-float-btn .corner {
  bottom: -30px;
  display: block;
  height: 30px;
  left: 0;
  overflow: hidden;
  position: absolute;
  width: 25px;
}

.apply-filters-float-btn .corner:before {
  bottom: 10px;
  color: transparent;
  content: " ";
  font-size: 0;
  left: -20px;
  position: absolute;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  border: 20px solid transparent;
  border-right-color: var(--redesign-second-color);
}

.apply-filters-float-btn .corner:after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  border: 10px solid transparent;
  border-left-color: var(--redesign-second-color);
  bottom: 0;
  color: transparent;
  content: " ";
  font-size: 0;
  left: 10px;
  position: absolute;
  z-index: -1;
}

.apply-filters-float-btn:hover .corner:before {
  border-right-color: var(--redesign-second-color);
}

.apply-filters-float-btn:hover .corner:after {
  border-left-color: var(--redesign-second-color);
}

