
.fast-view {

  &.fade {
    transition: opacity .15s linear !important;

    .modal-dialog {
      transition: transform .3s ease-out !important;
    }
  }

  &.in {
    .modal-dialog {
      transform: translate(0, 0);
    }
  }

  .modal {

    &-dialog {
      margin-top: 59px;
      margin-bottom: 59px;
      width: 612px;

      @media (min-width: $modal-xl) {
        width: 1000px;
      }
    }

    &-content {
      border: none !important;
      box-shadow: none !important;
      padding: 0 10px 20px;

      @media (min-width: 576px) {
          padding-left: 20px;
          padding-right: 20px;
      }
    }

    &-header {
      padding: 31px 20px 30px 0;
      border-bottom-color: #9b9b9b;
      border-bottom-width: 0.5px;

      .close {
        position: absolute;
        width: 24px;
        height: 24px;
        right: 20px;
        top: 22px;
        opacity: 1 !important;

        &::before,
        &::after {
          content: '';
          display: block;
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background-repeat: no-repeat !important;
          background-position: center !important;
          transition: opacity .17s linear;
        }

        &::before {
          background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M14.2031 12.0002L21.681 4.52153C21.8867 4.31565 22.0002 4.041 22.0005 3.74812C22.0005 3.45509 21.887 3.18011 21.681 2.97456L21.0257 2.31938C20.8196 2.11302 20.545 2 20.2518 2C19.9591 2 19.6845 2.11302 19.4785 2.31938L12.0006 9.79761L4.52234 2.31938C4.31664 2.11302 4.04184 2 3.74882 2C3.45613 2 3.18133 2.11302 2.97563 2.31938L2.32001 2.97456C1.89333 3.40126 1.89333 4.09531 2.32001 4.52153L9.79808 12.0002L2.32001 19.4786C2.11415 19.6848 2.00081 19.9595 2.00081 20.2524C2.00081 20.5452 2.11415 20.8199 2.32001 21.0259L2.97547 21.6811C3.18117 21.8873 3.45613 22.0005 3.74866 22.0005C4.04168 22.0005 4.31648 21.8873 4.52218 21.6811L12.0004 14.2027L19.4783 21.6811C19.6843 21.8873 19.959 22.0005 20.2517 22.0005H20.252C20.5448 22.0005 20.8195 21.8873 21.0255 21.6811L21.6808 21.0259C21.8865 20.8201 22 20.5452 22 20.2524C22 19.9595 21.8865 19.6848 21.6808 19.4788L14.2031 12.0002Z' fill='%23B1B1B1'/%3E%3C/svg%3E") !important;
          opacity: 1;
        }

        &::after {
          background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M14.2031 12.0002L21.681 4.52153C21.8867 4.31565 22.0002 4.041 22.0005 3.74812C22.0005 3.45509 21.887 3.18011 21.681 2.97456L21.0257 2.31938C20.8196 2.11302 20.545 2 20.2518 2C19.9591 2 19.6845 2.11302 19.4785 2.31938L12.0006 9.79761L4.52234 2.31938C4.31664 2.11302 4.04184 2 3.74882 2C3.45613 2 3.18133 2.11302 2.97563 2.31938L2.32001 2.97456C1.89333 3.40126 1.89333 4.09531 2.32001 4.52153L9.79808 12.0002L2.32001 19.4786C2.11415 19.6848 2.00081 19.9595 2.00081 20.2524C2.00081 20.5452 2.11415 20.8199 2.32001 21.0259L2.97547 21.6811C3.18117 21.8873 3.45613 22.0005 3.74866 22.0005C4.04168 22.0005 4.31648 21.8873 4.52218 21.6811L12.0004 14.2027L19.4783 21.6811C19.6843 21.8873 19.959 22.0005 20.2517 22.0005H20.252C20.5448 22.0005 20.8195 21.8873 21.0255 21.6811L21.6808 21.0259C21.8865 20.8201 22 20.5452 22 20.2524C22 19.9595 21.8865 19.6848 21.6808 19.4788L14.2031 12.0002Z' fill='%23505050'/%3E%3C/svg%3E") !important;
          opacity: 0;
        }

        @media (min-width: $modal-xl) {
          &:hover {
            &::before {
              opacity: 0;
            }

            &::after {
              opacity: 1;
            }
          }
        }
      }
    }

    &-title {
      font-family: $font-family;
      font-style: normal;
      font-weight: 400 !important;
      font-size: 32px !important;
      line-height: 38px;
      display: flex;
      align-items: center;
      letter-spacing: 0.01em;
      color: $black !important;
    }

    &-body {
      padding: 10px 0 0;
      display: flex;
      flex-direction: column;
      align-items: stretch;

      @media (min-width: $modal-xl) {
        flex-direction: row;
        justify-content: space-between;
        flex-wrap: wrap;
      }
    }
  }

  .new_card_stickers {
    width: 100%;
    justify-content: flex-end;
  }

  .new_card_stickers_info {
    margin: initial;
  }

  .new_card_stickers_article {
    font-size: 14px;
    line-height: 17px;
    letter-spacing: 0.01em;
  }


  .new_card_sliders {
    margin-bottom: 20px;
  }

  .new_card_main {
    width: 100%;
    margin-bottom: 0;
  }

  .new_card_buy {
    letter-spacing: 0;

    .flex_bl {
      padding: 20px;
    }
  }

  .new_card_buyCost {
    padding-top: 1px;
    margin-bottom: 20px;
    border: none;
  }

  .new_card_buy_old {
    margin-right: 21px;
  }

  .new_card_buy_sale {
    margin-bottom: 9px;
  }

  .new_card_buy_cost {
    font-size: 32px;
    line-height: 38px;
    letter-spacing: 0.01em;
  }

  .new_card_buy_another {
    letter-spacing: 0.01em;
    padding-left: 0;
    margin-top: 0;
  }

  .new_card_buy_existence_all {
    margin-bottom: -1px;
  }

  .new_card_buy_existence_city {
    margin-right: 21px;
  }

  .new_card_buy_anotherLine {
    margin-top: -2px;
    margin-bottom: -6px;
  }

  .new_card_buy_anotherProducer_link {
    letter-spacing: 0;

    a {
      margin-left: 2px;
    }
  }

  .new_card_buy_anotherLine_like {
    min-width: 137px;
    max-width: 137px;
  }

  .new_card_sliders_short-list {
    .new_card_sliderDots {
      padding: 0 !important;

      .swiper-button-prev,
      .swiper-button-next {
        display: none !important;
      }
    }
  }

  .new_card_info_charactersText {
    font-size: 14px;
    line-height: 17px;
    letter-spacing: 0.01em;
    text-decoration: underline !important;

    &:hover {
      text-decoration: none !important;
    }
  }

  .fast_view_go_to_page {
    margin-top: 30px;
    padding-top: 13px;
    height: 50px;
    font-size: 14px;
    line-height: 18px;
    letter-spacing: 0;
    transition: all .17s linear;
  }

  @media (min-width: $modal-xl) {
    .new_card_sliders {
      margin-right: 34px;
      margin-bottom: 0;
    }

    .new_card_buy .btn_addcart {
      height: 50px;
      transition: all .17s linear;
      font-size: 14px;
      line-height: 18px;
      letter-spacing: 0;
      padding-top: 13px;
    }

    .new_card_buy_another {
      margin-top: 35px;
      padding-left: 20px;
    }

    .new_card_sliderDots {
      padding-top: 34px;
      padding-bottom: 34px;
      max-height: 484px;

      .swiper-slide {
        margin-bottom: 0;

        img {
          display: block;
          max-width: 90%;
          max-height: 90%;
          min-width: 90%;
          position: relative;
          top: 5%;
        }

        &_unborder {
          border: none;
        }
      }
    }

    .swiper-button-prev {
      top: 0 !important;
      height: 31px !important;
      margin: 0 !important;

      svg {
        position: relative;
        top: 3px;
      }
    }

    .swiper-button-next {
      top: initial !important;
      bottom: 0 !important;
      height: 35px !important;
      margin: 0 !important;

      svg {
        position: relative;
        top: -1px;
      }
    }
  }
}
