.modal-dialog {
  max-width: 94%;
}

.ncatlist .mob.h2c {
  font-weight: 500;
  font-size: 24px;
  margin-bottom: 24px;
  color: #4e466c;
}
.ncatlist .mob.h2c {
  display: block;
  position: relative;
}

.nice-select .current {
  overflow: hidden;
  display: block;
  width: 90%;
}

@media (min-width: 992px) {
  .ncatlist .mob.h2c {
    display: none;
  }
}
@media (max-width: 992px) {
  .flat_page h1 {
    margin-bottom: 6px;
    margin-top: 7px;
  }
  .agree_check_wrapper {
    margin-left: 0px;
    width: 100%;
  }

  .flat_page h1.sf_h1 {
    display: block;
  }
}

@media screen and (max-width: 450px) {
  .content p img {
    max-width: 100%;
    display: inline-block;
    margin-right: 6px;
  }
  .tablej1 {
    display: block;
    width: 100%;
  }
  .tablej1 tr {
    display: block;
    width: 100%;
  }
  .tablej1 tr td {
    display: block;
  }
  .imgmb7 img {
    margin-bottom: 7px;
  }
}
