.ncatslrange .polzun_input_el .lab {
	font-size: 14px;
}
.ncatslrange .polzun_input input,
.ncatfilcontainer .the_search input {
	font-size: 15px;
	height: 32px;
}
.listing_filters_wrapperHeadline {
	font-size: 16px;
}
.ncatlist .newfilterj .ncatfiltit {
	font-size: 14px;
	font-weight: 400;
}
.ncatfilcheck,
.ncatfilcheck_marker {
	margin-left: 7px;
}
.ncatlist .ncatcheckbox + label span {
	font-size: 14px;
	margin-right: 0;
}
.ncatlist .ncatcheckbox + label:before {
	top: 2px;
}
.ncatfilcheck_marker sup,
.ncatfilcontainer sup {
	left: 0px;
}
.ncatlist .ncatfiltitopener:before {
	margin-top: -6px;
}
.main_filters .fl_chek {
	margin: 5px -1px 8px 0px;
	span {
		line-height: 20px;
	}
}

.js_full_list span.active:hover {
	border-bottom: 1px solid #ffffff;
}
.js_full_list span.active {
	display: block;
	line-height: 18px;
	border-bottom: 1px dashed #2399e2;
	color: #2399e2;
	font-size: 13px;
	-webkit-transition: 0.2s;
	transition: 0.2s;
}
.js_full_list span {
	display: none;
}
.js_full_list {
	border: none;
	background: transparent;
	padding: 0;
	text-align: left;
	height: 30px;
	position: relative;
	top: 10px;
	left: 4px;
}

.check_wrap.ps {
	padding-bottom: 0 !important;
}

.ncatlist .ncatcheckbox {
	height: 0;
}
.ncatfilcontainer sup {
	left: 1px;
}
.ncatlist .ncatcheckbox + label.color span {
	margin-right: 3px;
}

.ncatlist .ncatcheckbox + label span {
	display: block;
	white-space: nowrap;
}
.ps__thumb-y {
	width: 4px;
}

.ps__rail-y:hover {
	width: 10px;
	.ps__thumb-y {
		width: 6px;
	}
}
.ps__rail-y.ps--clicking .ps__thumb-y,
.ps__rail-y:focus > .ps__thumb-y,
.ps__rail-y:hover > .ps__thumb-y {
	width: 6px;
}
.main_filters .fl_chek span > img {
	height: 17px;
}
.ncatslrange .noUi-horizontal .noUi-handle {
	border-radius: 50%;
}
.ncatfilcheck_marker sup,
.ncatfilcontainer sup {
	top: 1px;
}

.clean_filters .btn {
	font-size: 14px;
	height: 50px;
	border: 2px solid #999999;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
	border-radius: 3px;
	color: #000000;
	-webkit-box-shadow: none;
	box-shadow: none;
	background: #ffffff;
	width: 100%;
	-webkit-transition: 0.2s;
	transition: 0.2s;
}
.clean_filters .btn.btn_orange {
	display: none;
}
.clean_filters .btn:hover {
	background: var(--redesign-main-color);
	border: 2px solid var(--redesign-main-color);
	color: #ffffff;
}
.clean_filters {
	position: fixed;
	bottom: 0;
	z-index: 57;
	padding: 10px;
	background: #f7f7f7;
}
.clean_filters.active {
	position: absolute;
	left: 0px !important;
	width: auto !important;
	right: 0px;
	margin-top: 10px;
	margin-bottom: 10px;
}
.ncatlist .newfilterj .ncatfiltersector label {
	margin-left: 3px;
}
.ncatlist .newfilterj .ncatfilcontainer {
	padding-left: 6px;
}
.ncatfilcontainer .the_search {
	padding-left: 4px;
}
@media screen and (max-width: 1100px) {
	.clean_filters {
		width: 100% !important;
	}
}
