.new_product_tags_links {
  padding-top: 10px;
  display: flex;
  flex-wrap: wrap;
  width: auto;
  margin-left: -10px;
  margin-right: -10px;

  @media screen and (max-width: 992px) {
    padding-bottom: 20px;
  }

  a {
    padding: 7px 10px;
    font-size: 14px;
    line-height: 16px;
    color: $-color_9;
    display: flex;

    span {
      color: $-color_b;
      text-decoration: underline;
      margin-right: 4px;
    }

    &:hover {
      span {
        text-decoration: none;
      }
    }
  }

  .tab {
    display: none;

    ul {
      list-style: none;
      margin: 0;
      padding: 0;
    }

    &.active {
      display: block;
    }

    ul {
      display: -webkit-box;
      display: -ms-flexbox;
      display: -webkit-flex;
      display: -moz-box;
      display: flex;
      -ms-flex-wrap: wrap;
      -webkit-flex-wrap: wrap;
      flex-wrap: wrap;
    }
  }
}




.new_product_tags_slider {
  padding-bottom: 16px;

  .swiper-slide {
    width: auto;

    .btn {
      box-shadow: none !important;
      background: $-color_tr;
      height: 30px;
      border-radius: 3px;
      border: 2px solid $-color_9;
      font-size: 13px;
      color: $-color_b;
      transition: 0.2s;
      padding: 4px 35px;

      span {
        color: $-color_9;
      }

      &:hover,
      &.active {
        border: 2px solid #5dac0d;
      }
    }


    @media screen and (max-width: 992px) {
      margin-right: 20px;
    }

  }

  .btn.active, .btn:hover {
    background: #5dac0d;
    color: #fff;

    span {
      color: #fff;
    }
  }
}



