.fade.in {
  opacity: 1;
}

.modal-backdrop.fade {
  opacity: 0.5;
}

.modal-open {
  overflow: hidden;
  padding-right: 15px !important;
  /*.header_new_topline {
    padding-right: -15px!important;
  }*/

  @include mobile() {
    padding-right: 0 !important;
  }
}

.modal-open .modal {
  overflow-x: hidden;
  overflow-y: auto;
}


.modal-dialog {
  margin: 4.75rem auto;
}





