.e_nav__item {
  font-size: 14px;
  line-height: 16px;
  padding: 12px 20px 11px 10px;
  background: #f7f7f7;
  border-bottom: 1px dotted #999;
  position: relative;
  cursor: pointer;
  width: 100%;
  letter-spacing: 0.04px;

  &.caret {
    &:after {
      content: "";
      position: absolute;

      right: 5px;
      top: 10.5px;
      z-index: 1;
      width: 20px;
      height: 20px;
      opacity: 0.6;
      background: url(data:image/png;base64,) no-repeat center center/5px auto;
      -webkit-transform: rotate(0);
      transform: rotate(0);
      -webkit-transition: .3s;
      transition: .3s;
    }
  }

  &:hover, &.hover {
    background-color: #fff;
  }

  &.active {
    font-weight: bold;

    &:after {
      -webkit-transform: rotate(-90deg);
    }
  }
}

.e_nav {
  background-color: #F7F7F7;
  width: 100%;

  &__collapse {
    width: 100%;
  }

  &__header {
    font-weight: 700;
    font-size: 16px;
  }

  &__ul {
    .nav-item > a {
      padding-left: 17px;
    }

    .nav-item > ul > li > a {
      padding-left: 38px;

      &:after {
        right: inherit;
        left: 17px;
        top: 10px;
        background-image: url("data:image/svg+xml,%3Csvg width='5' height='4' viewBox='0 0 5 4' fill='none' xmlns='http://www.w3.org/2000/svg'%3E %3Cpath d='M0.914062 2.02344V1.76367C0.914062 1.30794 1.0599 0.936523 1.35156 0.649414C1.64323 0.357747 2.03516 0.211914 2.52734 0.211914C3.03776 0.211914 3.43652 0.357747 3.72363 0.649414C4.0153 0.936523 4.16113 1.30794 4.16113 1.76367V2.02344C4.16113 2.47461 4.01758 2.84375 3.73047 3.13086C3.44336 3.41797 3.04688 3.56152 2.54102 3.56152C2.04427 3.56152 1.64779 3.41797 1.35156 3.13086C1.0599 2.84375 0.914062 2.47461 0.914062 2.02344Z' fill='black'/%3E %3C/svg%3E ");
      }
    }
  }
}
