@mixin minBannerMedia($screen,$zoom) {
  $max: $screen + 20;
  $minScreen: $screen + unquote("px");
  $max: $max + unquote("px");

  @media (min-width: $minScreen) and (max-width: $max) {
    zoom: $zoom;
    -moz-transform: scale($zoom);
    -moz-transform-origin: top center;
  }
  @if ($screen <= 320) {
    @media (max-width: $max) {
      zoom: $zoom;
      -moz-transform: scale($zoom);
      -moz-transform-origin: top center;
    }
  }
}

//
//   zoom  
// $width -    
// $minWidth -       zoom 

@mixin bannerMedia($width,$minWidth) {
  $minWidthScreen: 320;
  $coefficientZoom: 0;
  $account: ceil(($minWidth - $minWidthScreen)/ 10);
  $zoom: 100%;
  @for $i from 1 through $account {
    $minWidth: $minWidth - 10;
    $zoom: $zoom - $coefficientZoom;
    @if ($minWidth < 400) {
      $coefficientZoom: $coefficientZoom + 0.2;
    }
    @include minBannerMedia($minWidth, $zoom);
  }
}


$banner_width: 808px;
$banner_height: 80px;

.banner_men_and_woman {
  .image {
    min-width: 497px;
    max-width: $banner_width;
    min-height: $banner_height;
    max-height: $banner_height;
    height: $banner_height;

    img {
      max-height: $banner_height;
    }
  }
}

.banner_men_and_woman .image {
  @include bannerMedia(808, 640);
}




