.item-label {
  position: absolute;
  z-index: 3;
  top: 20px;

  &__badge {
    @extend .badge;
    margin-bottom: 6px;
    padding: 8px 7px;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    letter-spacing: 0.07px;
    transition: 0.2s;
    cursor: default;
    margin-right: 3px;


    @include mobile() {
      font-size: 11px;
      padding: 1px 7px;
    }


    div, a {
      display: block;

      @media (max-width: 400px) {
        max-width: 120px !important;
      }

      @include mobile() {
        white-space: nowrap; /*    */
        overflow: hidden; /*  ,      */
        padding: 5px 0; /*    */
        text-overflow: ellipsis; /*   */
      }
    }

    &.bg-black {
      border: 1px solid $black;
      color: #fff !important;
    }

    &.bg-success {
      border: 1px solid $success;
    }

    &.bg-primary {
      border: 1px solid $primary;
    }

    &.bg-info {
      border: 1px solid $info;
    }

    &.bg-danger {
      border: 1px solid $danger;
    }

    &:hover, &.hover {
      background-color: #fff !important;
      color: #000 !important;
    }

    &__color {
      &[data-label=""] {
        @extend .bg-success;
        background-color: #fff !important;
        color: #000;
      }

      &[data-label=""] {
        @extend .bg-danger;
        background-color: #fff !important;
        color: #000;
      }

      &[data-label=" "] {
        border: 1px solid #00a1b6;
        background-color: #fff !important;
        color: #000;
      }

      &[data-label=""] {
        @extend .bg-info;
        background-color: #fff !important;
        color: #000;
      }

      &[data-label=" !"] {
        border: 1px  #000!important;
        background: #000!important;
        color: #fff !important;
        font-weight: 300 !important;

        // @media (hover: hover) and (pointer: fine) {
        //   &:hover {
        //     background-color: #fff !important;
        //     color: $black !important;
        //   }
        // }
      }
    }

    &_fast-view {
      display: none !important;
      justify-content: center;
      align-items: center;
      position: relative;
      width: 20px;
      height: 20px;
      background: white;
      border: 1px solid #b1b1b1;
      border-radius: 3px;
      overflow: hidden;
      transition: all .1s linear;

      @media (min-width: 991px) {
        width: 30px;
        height: 30px;
      }

      @media (min-width: 1200px) {
        display: flex!important;

        &:hover {
          border-color: #838181;
        }
      }

      .btn_oneClick {
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: url("data:image/svg+xml;base64,") center no-repeat;

        @media (min-width: 1200px) {
          &:hover {
            &::before { opacity: 0 }
            &::after { opacity: 1 }
          }
        }

        @media (min-width: 991px) {
          background: none;

          &::before,
          &::after {
            content: '';
            display: block;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            transition: all .1s linear;
          }

          &::before {
            opacity: 1;
            background: url("data:image/svg+xml;base64,") center no-repeat;
          }

          &::after {
            opacity: 0;
            background: url("data:image/svg+xml;base64,") center no-repeat;
          }
        }
      }
    }
  }
  .item-label__caret {
    cursor: pointer;
    transition: 0.2s;
    padding: 0;
    top: 0;
    width: 30px;
    height: 30px;
    margin-left: 3px;
    order: 2;

    @include media-breakpoint-down(xxl) {
      order: 0;
    }

    &:after {
      position: absolute;
      content: "";
      background-color: #fff;
      width: 30px;
      height: 30px;
      background-repeat: no-repeat;
      border: 1px solid #999;
      border-radius: 3px;
      background-position: center center;
      -webkit-transform: rotate(90deg);
      transform: rotate(90deg);
      background-image: url("data:image/svg+xml;base64,");

      @include mobile() {
        height: 24px;
        width: 24px;
      }
    }


    &:hover {
      &:after {
        background-color: rgba(239, 239, 239, 0.89);
      }
    }
  }

  &__badges {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    transition: 0.2s;
  }

  &:not(.active) {
    .item-label__badges {
      overflow: hidden;
      max-height: 30px;
      height: 30px;
    }

    .item-label__badge {
      order: 3;
      flex-basis: 100%;
      transition: 0.2s;

      &:nth-child(1) {
        order: 0;
        flex-basis: auto;
      }

      &:nth-child(2) {
        order: 0;
        flex-basis: auto;
      }
    }

    .item-label__line {
      .item-label__badge {
        display: none;

        &:nth-child(2) {
          display: block;

          @media (min-width: 1200px) {
            display: none;
          }
        }
      }
    }

  }

  &.active {
    .item-label__badges {
      flex-wrap: wrap;
    }

    .item-label__caret {
      flex-basis: 100%;
      order: 10;
      margin-left: 0;

      &:after {
        -webkit-transform: rotate(0deg) !important;
        transform: rotate(0deg) !important;
      }
    }

  }
}
