
.dropdown-toggle {
  // changed icon caret
  &::after {

    background-image: url("data:image/svg+xml;base64,");
    background-repeat: no-repeat;
    border: none !important;
    content: ' ';
    color: $body-color;
    width: 8px;
    height: 12px;
    position: absolute;
    padding: 0;
    margin: 0;
    vertical-align: middle;
    top: 14px;
    right: 16px;
    text-align: center;
    transition: 0.3s;
  }
}

.open {
  .dropdown-toggle {
    // changed icon caret
    &::after {
      transform: rotate(90deg);
    }
  }
}
