// product
.e_fast_link {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  flex: none;
  order: 0;
  flex-grow: 0;
  padding: 10px 10px 0;
  margin: -10px -10px 0;

  .swiper-wrapper {
    margin-bottom: 20px;
  }

  &_card {
    border: 0;
    //border-radius: 4px;
    background: #fff;
    padding: 15px 0 0 0;
    width: 157px;
    max-width: 157px;
    min-width: 157px;
    flex: none;
    order: 0;
    flex-grow: 0;
    margin: 0 10px 0 10px;
    box-shadow: 0 0 0 0 rgba(216, 216, 216, 0);
    transition: all .17s linear;

    @media (min-width: 1200px) {
      &:hover {
        box-shadow: 0 0 6px 3px rgba(216, 216, 216, 0.5);
      }
    }

    @include mobile() {
      width: 85px;
      max-width: 85px;
      min-width: 85px;
      margin: 0 5px 0 5px;
    }

    &:first-child {
      margin-left: 0;
    }

    a {
      z-index: 22;
      position: relative;
    }
  }

  &_pic {
    display: block;
    width: 80%;
    height: auto;
    position: relative;
    margin: 0 auto;

    &::before {
      content: '';
      display: block;
      padding-bottom: 100%;
    }

    img {
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }

  &_title {
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    text-align: center;
    letter-spacing: 0.01em;
    color: #000000;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;

    @include mobile() {
      font-size: 12px;
      line-height: 15px;
      word-wrap: break-word;
    }

    &.active {
      color: var(--redesign-second-color);
    }
  }

  &_body {
    height: 68px;
    z-index: 1;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 7px 10px;

    @include mobile() {
      height: 58px;
      padding: 6.5px;
    }
  }
}

.ecommerce_top {
  padding-bottom: 40px;

  @include mobile() {
    padding: 15px 0 5px;
  }

  h1 {
    font-size: 32px;
    line-height: 38px;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    gap: 0 6px;
    margin: 0;
    @include mobile() {
      font-size: 18px;
      line-height: 110%;
    }
  }

  .ecommerce_total {
    color: #666;
    padding-left: 6px;
    @include mobile() {
      font-size: 18px;
    }
  }

  .ecommerce_info_price {
    font-size: 17px;
    line-height: 20px;
    display: flex;
    align-items: center;
    color: #CCCCCC;
    @include mobile() {
      font-size: 12px;
    }
  }

  .ecommerce_top__filters-results {
    display: block;
    position: relative;
    margin: 0 !important;
    padding: 27px 0 0 0;

    @media (min-width: 768px) {
      padding-top: 30px;
    }

    @media (min-width: 992px) {
      padding-top: 39px;
    }

    .swiper-wrapper {
      @media (min-width: 768px) {
        flex-wrap: wrap;
        column-gap: 10px;
        row-gap: 10px;
      }
    }

    .swiper-slide {
      width: auto;
    }

    a {
      display: flex;
      align-items: center;
      justify-content: space-between;
      position: relative;
      width: 100%;
      max-width: 270px;
      padding: 6px 10px;
      border-radius: 200px;
      background: var(--redesign-seventh-color);
      transition: all .1s linear;

      &._active {
        color: var(--redesign-second-text-color);
        background: var(--redesign-second-color);

        .desc {
          color: var(--redesign-second-text-color);
        }

        .clear {
          display: block;
        }
      }
    }

    .desc {
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      overflow: hidden;
      text-overflow: ellipsis;
      width: 100%;
      height: auto;
      margin: 0;
      padding: 0;
      color: black;
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: 120%; /* 19.2px */
      background: transparent;
      transition: all .1s linear;
      letter-spacing: 0.14px;

      @media (min-width: 767px) {
        font-size: 16px;
        line-height: 130%;
      }

      @media (min-width: 992px) {
        white-space: nowrap;
      }
    }

    .clear {
      display: none;
      width: 15px;
      height: 15px;
      margin-left: 15px;
      background-image: url("data:image/svg+xml;base64,");
      background-position: center;
      background-repeat: no-repeat;
    }
  }
}
