.subfooter_df {
  background: #ededed;
  padding: 23px 20px 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: auto;
  margin-left: -20px;
  margin-right: -20px;
}

.subfooter_headline {
  max-width: 470px;
  min-width: 470px;
  margin-right: 230px;
  padding-top: 6px;
}

.subfooter_title {
  color: #333333;
  font-size: 23px;
  line-height: 27px;
  margin-bottom: 15px;
}

.subfooter_text {
  font-size: 16px;
  line-height: 19px;
  color: #333333;
  margin-bottom: 10px;
}

.subfooter_form {
  max-width: 612px;
  min-width: 612px;
}

.subfooter_form_errorInfo {
  color: #ff0000;
  position: absolute;
  bottom: 6px;
  font-size: 14px;
  line-height: 16px;
  padding-left: 12px;
  left: 0;
}

.subfooter_form_access {
  color: #333333;
  font-size: 23px;
  line-height: 40px;
  padding: 14px 0 0;
}

.error .subfooter_form_checkError {
  display: block;
}

.subfooter_form_check.error {
  position: relative;
}

.subfooter_form_checkError {
  display: none;
  position: absolute;
  z-index: 5;
  top: -43px;
  left: -13px;
  width: 310px;
  max-width: 100%;
  padding-right: 34px;
  padding-left: 13px;
  background: #333333;
  color: #ffffff;
  font-size: 10px;
  line-height: 30px;
}

.subfooter_form_checkError:before {
  content: "";
  position: absolute;
  z-index: 1;
  width: 12px;
  height: 12px;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  background: #333333;
  bottom: -6px;
  left: 17px;
}

.subfooter_form_checkError_but {
  position: absolute;
  width: 30px;
  height: 30px;
  right: 0px;
  top: 0;
  background: url("data:image/png;base64,") no-repeat center center/20px auto;
  border: none;
  padding: 0;
}

.subfooter_form_controll {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  border-bottom: 1px solid #333333;
  padding-bottom: 30px;
  margin-bottom: 12px;
  position: relative;
}

.subfooter_form_controll .subfooter_clear_input {
  position: absolute;
  width: 24px;
  height: 24px;
  right: 180px;
  top: 50%;
  margin-top: -27px;
  background: url("data:image/png;base64,") no-repeat center center/18px auto;
  border: none;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}

.subfooter_form_controll .subfooter_clear_input:hover {
  opacity: 0.85;
}

.subfooter_form_controll .btn {
  height: 40px;
  color: #000000;
  font-size: 14px;
  padding: 0 35px;
}

.subfooter_form_controll .btn {
  &:hover,
  &:active {
    background: var(--redesign-second-color);
    color: #ffffff;
    border: 2px solid var(--redesign-second-color);
  }
}

.subfooter_form_controll input {
  background: transparent;
  border: 1px solid #333333;
  border-radius: 3px;
  color: #333333;
  height: 40px;
  margin-right: 25px;
  -webkit-box-flex: 1;
  flex-grow: 1;
  padding: 0 11px;
  padding-right: 40px;
  font-size: 14px;
}

.subfooter_form_controll input.verify_well {
  background: url("../images/header_new/verify_well.svg") no-repeat calc(100% - 8px) center/24px auto;
}

.subfooter_form_controll input.error {
  border: 1px solid #ff0000;
}

.subfooter_form_controll input::-webkit-input-placeholder {
  color: #333333;
}

.subfooter_form_controll input::-moz-placeholder {
  /* Firefox 19+ */
  color: #333333;
}

.subfooter_form_controll input:-ms-input-placeholder {
  /* IE 10+ */
  color: #333333;
}

.subfooter_form_controll input:-moz-placeholder {
  /* Firefox 18- */
  color: #333333;
}

.subfooter_form_check .checkbox {
  position: absolute;
  z-index: -1;
  opacity: 0;
  margin: 10px 0 0 20px;
}

.subfooter_form_check .checkbox + label {
  position: relative;
  padding: 0 0 0 31px;
  cursor: pointer;
  font-size: 10px;
  line-height: 1.2;
  color: #333333;
  margin-bottom: 5px;
}

.subfooter_form_check .checkbox + label:after {
  content: "";
  position: absolute;
  top: 0px;
  left: 0px;
  width: 19px;
  height: 19px;
  border: 1px solid #333333;
  -webkit-transition: 0.2s;
  transition: 0.2s;
  border-radius: 3px;
  background-position: 3px center;
}

.subfooter_form_check .checkbox:checked + label:after {
  background: url("data:image/png;base64,") no-repeat;
  background-size: 10px auto;
  background-position: 3px center;
}

.btn_footer_design {
  color: #ffffff;
  line-height: 38px;
  font-size: 14px;
  padding: 0 33px;
  margin-top: 10px;

  &:hover {
    color: var(--redesign-second-color);
    border: 2px solid var(--redesign-second-color);
    background: none;
  }
}


@media screen and (max-width: 1439px) {
  .subfooter_headline {
    margin-right: 130px;
  }

  .subfooter_form {
    min-width: initial;
    max-width: initial;
    -webkit-box-flex: 1;
    -ms-flex-positive: 1;
    flex-grow: 1;
  }

  .new_h_fix.active_menu .menu_show_wrapper .menu_show_wrapper_menu {
    width: 1210px;
  }
}

@media screen and (max-width: 1240px) {
  .subfooter_headline {
    margin-right: 90px;
    min-width: 400px;
    max-width: 400px;
  }
}

@media screen and (max-width: 1100px) {
  .subfooter_df {
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
  }

  .subfooter_headline {
    margin-right: 0;
    min-width: 100%;
    max-width: 100%;
    padding-bottom: 15px;
  }

  .elem.elem_catalog_btn {
    display: none;
  }
}

@media screen and (max-width: 776px) {
  .subfooter_form_controll input {
    min-width: initial;
    max-width: initial;
    -webkit-box-flex: 1;
    -ms-flex-positive: 1;
    flex-grow: 1;
  }
  .subfooter_df {
    padding-bottom: 5px;
  }
}

@media screen and (max-width: 767px) {
  .subfooter_df {
    padding-top: 20px;
  }

  .subfooter_text {
    margin-bottom: 2px;
  }
}

.subfooter {
  background: #ededed;
}

.jscroll {
  overflow-y: auto;
  width: auto;
  margin-left: 0;
  margin-right: 0;
}

@media screen and (max-width: 500px) {
  .subfooter_form_checkError {
    max-width: 300px;
    left: -5px;
    padding-right: 30px;
  }

  .subfooter_form_checkError:before {
    left: 9px;
  }

  .subfooter_form_controll .subfooter_clear_input {
    right: 8px;
    margin-top: -56px;
  }

  .subfooter_form_controll .btn {
    width: 100%;
  }

  .subfooter_form_check .checkbox + label:after {
    top: 3px;
  }

  .subfooter_form_controll {
    padding-bottom: 19px;
    margin-bottom: 18px;
  }

  .subfooter_form_controll {
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
  }

  .subfooter_form_controll input {
    margin-right: 0;
    min-width: 100%;
    max-width: 100%;
    margin-bottom: 30px;
  }

  .subfooter_form_errorInfo {
    bottom: 66px;
  }

  .subfooter_form_access {
    font-size: 20px;
  }

  .subfooter_form_access {
    margin-top: 35px;
    margin-bottom: 35px;
  }

  .subfooter_df {
    padding-bottom: 15px;
  }
}

.menu_show_wrapper {
  margin-left: 0;
  margin-right: 0;
}
