.ncatlist .ncatcheckbox {
    position: absolute;
    z-index: -1;
    opacity: 0;
    margin: 0;
    height: 22px;
}

.ncatlist label {
    font-weight: normal;
}

.ncatlist .ncatcheckbox + label,
.ncatlist .ncatradio + label {
    position: relative;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    padding: 2px 0 2px 28px;
    cursor: pointer;
    margin-bottom: 6px;
}

.ncatlist .ncatcheckbox + label:before {
    content: "";
    position: absolute;
    top: 6px;
    left: 0;
    width: 18px;
    height: 18px;
    background: url("data:image/png;base64,")
        no-repeat center center;
    background: 100% auto;
    -webkit-transition: 0.2s;
    transition: 0.2s;
}

.ncatlist .ncatcheckbox:checked + label:before {
    background: url($base64__green_check) no-repeat center center;
    background: 100% auto;
}
.ncatlist .ncatcheckbox + label span {
    margin-right: 5px;
    margin-top: auto;
    margin-bottom: auto;
    font-size: 14px;
    color: #000000;
    max-width: calc(100% - 55px);
    -webkit-box-orient: vertical;
    display: -webkit-box;
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-line-clamp: 1;
}

.ncatfilcontainer sup,
.ncatfilcheck_marker sup {
    position: relative;
    line-height: 0;
    vertical-align: baseline;
    top: 1;
    margin-right: 7px;
    color: #999999;
    margin-top: auto;
    margin-bottom: auto;
    font-size: 12px;
}
.ncatlist .ncatcheckbox.disabled + label,
.ncatlist .ncatcheckbox:disabled + label {
    opacity: 0.7;
}
