.fast-view .presentation,
.presentation {
    flex-shrink: 0;
    display: flex;
    flex-direction: column-reverse;
    align-items: flex-start;
    gap: 10px;
    position: relative;
    width: 100%;
    height: auto;
    user-select: none;

    @media (min-width: 576px) {
        flex-direction: row;
    }

    .swiper-button-prev,
    .swiper-button-next {
        display: block;
        position: absolute;
        top: 50% !important;
        transform: translateY(-50%);
        margin: 0 !important;
        padding: 0;
        width: 40px !important;
        height: 40px !important;
        cursor: pointer;
        line-height: 1;
        transition: all .1s linear;

        border: none;
        border-radius: 100%;

        background: var(--redesign-second-text-color);
        box-shadow: 0 2px 16px rgba(0, 0, 0, .08);

        &::after {
            display: none;
        }

        &::before {
            content: "";
            display: block;
            position: absolute;
            top: 50%;
            left: 50%;
            height: 18px;
            width: 18px;
            background: transparent no-repeat center;
            background-image: url("data:image/svg+xml;base64,");
            transition: all .1s linear;
        }

        @media (hover: hover) and (pointer: fine) {
            &:hover {
                &::before {
                    background-image: url("data:image/svg+xml;base64,");
                }
            }
        }
    }

    &__previews {
        flex-shrink: 0;
        display: flex;
        gap: 10px;
        position: relative;
        width: 100%;
        height: auto;
        overflow: hidden;
    }

    &__video {
        flex-shrink: 0;
        display: flex;
        flex-direction: row;
        gap: 10px;
        width: auto;
        height: auto;

        @media (min-width: 576px) {
            flex-direction: column;
        }

        .presentation__item {
            margin: 0;

            @media (hover: hover) and (pointer: fine) {
                &:hover {
                    border-color: var(--redesign-main-color);

                    &::before {
                        opacity: 0.8;
                    }
                }
            }
        }
    }

    &__previews-slider {
        display: block;
        position: relative;
        overflow: hidden;
        width: 100%;
        height: auto;

        .swiper {
            width: 100%;
            height: auto;
            transition: opacity .1s linear .1s;

            &:not(.swiper-container-initialized) {
                .swiper-button-prev,
                .swiper-button-next {
                    opacity: 0 !important;
                }
            }

            @media (min-width: 576px) {
                height: 100%;
            }
        }

        .swiper-slide {
            width: auto;
            height: auto;

            &:last-of-type {
                .presentation__item {
                    margin: 0;

                    @media (min-width: 576px) {
                        margin-bottom: 2px;
                    }
                }
            }

            &.active {
                .presentation__item {
                    border-color: var(--redesign-main-color);
                }
            }
        }

        .swiper-wrapper {
            @media (min-width: 576px) {
                flex-direction: column;
            }
        }

        .swiper-lazy-preloader {
            border-color: var(--redesign-main-color);
            border-top-color: transparent;
            width: 30px;
            height: 30px;
            border-width: 3px;
            margin-left: -20px;
            margin-top: -15px;
            opacity: 0.5;
            z-index: 0;

            @media (min-width: 768px) {
                margin-left: -15px;
                margin-top: -20px;
            }
        }

        .swiper-button-prev,
        .swiper-button-next {
            display: none;

            @media (min-width: 576px) {
                display: block;
                left: 50% !important;
                transform: translateX(-50%);
            }

            &.swiper-button-disabled {
                opacity: 0;
            }
        }

        .swiper-button-prev {
            &::before {
                transform: translate(-50%, -50%) rotate(-90deg);

                @media (min-width: 576px) {
                    transform: translate(-50%, -50%) rotate(0deg);
                }
            }

            @media (min-width: 576px) {
                top: 10px !important;
            }
        }

        .swiper-button-next {
            &::before {
                transform: translate(-50%, -50%) rotate(90deg);

                @media (min-width: 576px) {
                    transform: translate(-50%, -50%) rotate(180deg);
                }
            }

            @media (min-width: 576px) {
                bottom: 10px !important;
                right: initial !important;
                top: initial !important;
            }
        }
    }

    &__item {
        flex-shrink: 0;
        display: block;
        position: relative;
        width: 60px;
        height: auto;
        aspect-ratio: 1 / 1;
        margin-right: 10px;
        border: 1px solid var(--scroll-color);
        transition: all .1s linear;
        overflow: hidden;
        cursor: pointer;

        @media (min-width: 576px) {
            width: 62.9px;
            margin-right: 0;
            margin-bottom: 10px;
        }

        a {
            display: block;
            position: absolute;
            z-index: 1;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
        }

        img {
            display: block;
            position: absolute;
            z-index: 1;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: calc(100% + 2px);
            height: calc(100% + 2px);
            object-fit: cover;

            &::before {
                content: '';
                display: block;
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                background: var(--redesign-seventh-color);
            }
        }

        &_video {
            cursor: pointer;

            &::before {
                content: '';
                display: inline-block;
                position: absolute;
                z-index: 2;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                width: 32px;
                height: 32px;
                background-color: rgba(36, 36, 36, .8);
                border-radius: 50%;
                line-height: 1;
                vertical-align: middle;
                pointer-events: none;
                transition: all .1s linear;
            }

            &::after {
                content: '';
                display: block;
                position: absolute;
                z-index: 3;
                width: 0;
                height: 0;
                left: 50%;
                top: 50%;
                transform: translate(calc(-50% + 2px), -50%);
                border-bottom: 7px solid transparent;
                border-left: 11px solid var(--redesign-second-text-color);
                border-top: 7px solid transparent;
                pointer-events: none;
            }
        }
    }

    &__display {
        display: block;
        position: relative;
        width: 100%;
        height: auto;
        aspect-ratio: 1 / 1;
        overflow: hidden;

        .swiper-lazy-preloader {
            border-color: var(--redesign-main-color);
            border-top-color: transparent;
            width: 60px;
            height: 60px;
            border-width: 3px;
            margin-left: -30px;
            margin-top: -30px;
            opacity: 0.5;
            z-index: 0;
        }

        .item-badges {
            bottom: 10px;
            left: 10px;

            @media (min-width: 768px) {
                bottom: 20px;
                left: 20px;
            }
        }

        .btn-similar-products {
            bottom: 10px;
            right: 10px;

            @media (min-width: 768px) {
                bottom: 15px;
                right: 15px;
            }
        }

        .swiper-button-prev,
        .swiper-button-next {
            opacity: 1;

            @media (hover: hover) and (pointer: fine) {
                display: none;
            }
        }

        .swiper-button-prev {
            &::before {
                transform: translate(-50%, -50%) rotate(-90deg);
            }
        }

        .swiper-button-next {
            &::before {
                transform: translate(-50%, -50%) rotate(90deg);
            }
        }
    }

    &__display-content {
        display: block;
        position: relative;
        z-index: 1;
        width: 100%;
        height: 100%;

        img {
            display: block;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: calc(100% + 4px);
            height: calc(100% + 4px);
            object-fit: cover;
            cursor: pointer;
        }

        a {
            display: block;
            position: absolute;
            z-index: 1;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;

            @media (hover: hover) and (pointer: fine) {
                &:hover::before {
                    transform: scale(1.1);
                }
            }

            &::before {
                content: '';
                display: block;
                position: absolute;
                z-index: 1;
                inset: 0;
                background: transparent no-repeat center;
                transition: all .1s linear;
                transform: scale(1);
            }

            &._rt {
                &::before {
                    background-image: url("https://aws.massive.ru/fandeco.ru/logo/rt-bg.svg");
                }
            }

            &._yt {
                &::before {
                    background-image: url("https://aws.massive.ru/fandeco.ru/logo/yt-bg.svg");
                }
            }
        }

        @media (hover: hover) and (pointer: fine) {
            &:hover {
                .presentation__display-zoom {
                    opacity: 1;
                }
            }
        }
    }

    &__display-view {
        z-index: 1;
        // pointer-events: none;

        &::before {
            content: '';
            display: block;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: var(--redesign-seventh-color);
        }
    }

    &__display-saver {
        z-index: 3;
        opacity: 0;
        cursor: url("https://aws.massive.ru/fandeco.ru/assert/images/zoom-cursor.svg"), auto;
    }

    &__display-zoom {
        position: absolute;
        z-index: 2;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        transition: opacity 0.1s linear, background-position 0.03s linear;
        transform: translateZ(0);
        opacity: 0;
        background-repeat: no-repeat;
    }
}

.presentation {
    margin-bottom: 10px;

    @media (min-width: 769px) {
        margin-bottom: 40px;
    }

    @media (min-width: 993px) {
        width: 510px;
        margin-right: 20px;
    }

    @media (min-width: 1101px) {
        width: 580px;
    }

    @media (min-width: 1200px) {
        width: 480px;
        margin-right: 40px;
        margin-bottom: 20px;
    }

    @media (min-width: 1400px) {
        width: 577px;
    }

    &__previews {
        @media (min-width: 576px) {
            flex-direction: column;
            width: auto;
            height: 427.11px;
        }

        @media (min-width: 768px) {
            height: 581.31px;
        }

        @media (min-width: 992px) {
            height: 819px;
        }

        @media (min-width: 993px) {
            height: 423px;
        }

        @media (min-width: 1101px) {
            height: 496px;
        }

        @media (min-width: 1200px) {
            height: 399px;
        }

        @media (min-width: 1400px) {
            height: 505px;
        }
    }

    &__item {
        @media (min-width: 768px) {
            width: 88.5px;
        }

        @media (min-width: 992px) {
            width: 93.5px;
        }

        @media (min-width: 993px) {
            width: 76.5px;
        }

        @media (min-width: 1101px) {
            width: 74.4px;
        }

        @media (min-width: 1200px) {
            width: 71.6px;
        }

        @media (min-width: 1400px) {
            width: 63.4px;
        }
    }
}

.fast-view {

    &:has(.presentation) {
        .modal-dialog {
            width: 540px;
            max-width: calc(100% - 10px);

            @media (min-width: 1200px) {
                width: 1000px;
            }
        }

        .modal-body {
            padding: 0;
        }

        .new_card_buy_another {
            margin-top: 2px;
        }

        .new_card_buy .flex_bl {
            @media (min-width: 1240px) {
                margin-bottom: 17px;
            }
        }
    }

    .presentation {
        margin: 0 0 0 0;

        @media (min-width: 768px) {
            margin-bottom: 20px;
        }

        @media (min-width: 1200px) {
            width: 573px;
            margin-bottom: 0;
            margin-right: 20px;
        }

        & + .new_card_main_dolyami {
            width: 100% !important;
            max-width: initial !important;
            min-width: initial !important;
            padding-bottom: 0;

            @media (min-width: 1140px) {
                width: 30% !important;
            }

            .new_card_buy_another + .d-grid {
                width: 100%;
            }
        }


        &__previews {
            @media (min-width: 576px) {
                flex-direction: column;
                width: auto;
                height: 430px;
            }

            @media (min-width: 1200px) {
                height: 502px;
            }
        }
    }
}

