            letter-spacing: 0.17px;
            white-space: normal;
            line-height: initial;
            transition: color .1s linear;
        }

        .autocomplete-suggestion-title {
            display: flex;
            align-items: center;
            padding: 9px 10px 6px 10px;
            color: #b1b1b1;
            font-size: 17px;
            font-style: normal;
            font-weight: 700;
            letter-spacing: 0.17px;
        }

        .autocomplete-suggestion-category-title {
            display: flex;
            align-items: center;
            padding: 17px 10px 6px;
            color: #b1b1b1;
            font-size: 17px;
            font-style: normal;
            font-weight: 700;
            letter-spacing: 0.17px;
        }

        .autocomplete-suggestion_category {
            margin: 0;
            padding: 0;
            flex: initial;
            max-width: initial;
            width: 100%;

            .suggestions-product_price {
                width: 100%;
                height: initial;
                margin: 0;
                padding: 12px 10px;
                font-size: 22px;
                font-style: normal;
                font-weight: 700;
                letter-spacing: 0.22px;
                line-height: initial;
                white-space: initial;

                &::before {
                    display: none;
                }
            }
        }

        .suggestions-product_imgwr {
            flex-shrink: 0;
            width: 80px;
            height: 80px;
            max-width: initial;
            max-height: initial;
            min-width: initial;
            min-height: initial;
            margin: 10px !important;

            span, img {
                display: block;
                position: relative;
                width: 100%;
                height: 100%;
                max-width: initial;
                min-width: initial;
                max-height: initial;
                min-height: initial;
                margin: 0;
                padding: 0;
            }

            img {
                object-fit: cover;

                &::before {
                    content: '';
                    display: block;
                    position: absolute;
                    width: 100%;
                    height: 100%;
                    background-color: #fcfcfc;

                }
            }
        }

        .suggestions-product_imgwr + .suggestions-product_content {
            display: flex;
            flex-direction: column;
            justify-content: center;
            flex-grow: initial;
            gap: 10px;
            margin: 0;
            padding: 0;
            max-width: initial;
            width: 100%;
            height: auto;
            margin: 10px 10px 10px 0;

            .suggestions-product_price  {
                color: var(--redesign-text-color) !important
            }
        }

        .suggestions-product_title {
            width: 100%;
            height: auto;
            margin: 0;
            padding: 0;
            transition: all .1s linear;
            white-space: initial;
            padding: 0 !important;
            color: var(--redesign-text-color);
            font-size: 17px;
            font-style: normal;
            font-weight: 400;
            line-height: 18px; /* 105.882% */
            letter-spacing: 0.17px;
            transition: color .1s linear;

            display: -webkit-box;
            -webkit-line-clamp: 3;
            -webkit-box-orient: vertical;
            overflow: hidden;
            text-overflow: ellipsis;

            &::before {
                display: none;
            }
        }

        .suggestions-product_price {
            display: flex;
            flex-wrap: wrap;
            gap: 10px;
            margin: 0;
            height: auto;
            padding: 0;
            transition: color .1s linear;

            &::before {
                display: none;
            }

            i, mark {
                display: flex;
                align-items: center;
                width: auto;
                height: 23px;
            }

            i {
                min-width: 95px;
                color: #b1b1b1;
                font-size: 17px;
                font-style: normal;
                font-weight: 500;
                line-height: normal;
                text-decoration: line-through;
            }

            mark {
                margin: 0 0 0 3px;
                padding: 2px;
                border-radius: 3px;
                background: var(--redesign-second-color);
                color: var(--redesign-text-color);
                text-align: center;
                font-size: 17px;
                font-style: normal;
                font-weight: 700;
            }

            span {
                display: block;
                position: relative;
                width: 100%;
                height: auto;
                font-size: 17px;
                font-style: normal;
                font-weight: 700;
                line-height: 18px; /* 105.882% */
                letter-spacing: 0.17px;
            }
        }
    }
}
