  font-size: 12px;
  color: #000000;
  font-weight: 300;
}

.breadcrumbs .container {
  padding-left: 0;
  padding-right: 0;
}

.breadcrumbs a,
.breadcrumbs span {
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 14px;
  color: var(--redesign-main-color);
  letter-spacing: 0;
  transition: all .1s linear;
  &:hover {
    color: var(--redesign-second-color);
  }
}

.breadcrumbs.breadcrumbs_adaptive {
  padding: 20px 0 10px;

  @media (min-width: 992px) {
      padding: 27px 0 21px;
  }

  nav {
    @media (max-width: 991px) {
      display: block;
      width: 100vw;
      max-width: none;
      left: 50%;
      position: relative;
      transform: translateX(-50%);
      padding-top: 0;
      padding-bottom: 0;
      overflow-x: auto;
      overflow-y: hidden;
      -ms-overflow-style: none;
      scrollbar-width: none;

      &::-webkit-scrollbar {
        width: 0;
        height: 0;
      }
    }

    @media (max-width: 575px) {
      padding-left: 20px;
      padding-right: 20px;
    }

    @media (min-width: 576px) and (max-width: 767px) {
        padding-left: calc((100vw - 540px) / 2 + 20px);
        padding-right: calc((100vw - 540px) / 2 + 20px);
    }

    @media (min-width: 768px) and (max-width: 991px) {
      padding-left: calc((100vw - 720px) / 2 + 20px);
      padding-right: calc((100vw - 720px) / 2 + 20px);
    }
  }

  .breadcrumb {
    padding: 0;

    @media (min-width: 992px) {
      padding: 0 10px;
    }

    @media (max-width: 991px) {
      display: flex;
      flex-wrap: nowrap;
      align-items: center;
      gap: 10px;
    }
  }

  .breadcrumb-item {
    @media (max-width: 991px) {
      padding: 0;
    }

    &::before {
      @media (max-width: 991px) {
        display: none;
      }
    }

    &.active span {
      @media (max-width: 991px) {
        border-color: var(--redesign-second-color);
      }
    }

    &:last-of-type span {
      @media (max-width: 575px) {
        margin-right: 20px;
      }

