.ecommerce-card {



  /* ACTION|HOVER*/
  &.item__label__hide {
    .item-label {
      display: none;
    }
  }

  &.item__label__hover {
    .item-label {
      display: none;
    }
  }

  &.item__3dmodel__hide {
    .item-article__3dmodel {
      display: none !important;
    }
  }

 /* &.item__item-dots__hide{
    .item-dots {
      display: none;
    }
  }*/

