        color: #000;
        background-color: #fff !important;
      }
    }
  }*/



  &.item__item-label__hover{
    .item-label {
      .bg-danger,.bg-black{
        color: var(--redesign-text-color);
        background-color: var(--redesign-second-text-color) !important;
      }
    }
  }


  &.item-prices__hide_sale {
    .item-prices__mod__sale_price {
