@import "container";
@import "user";
// @import "breadcrumbs";

.header_new_topline {
  background-color: #fff;
}

.nav_category > a {
  text-overflow: ellipsis;
  overflow: hidden;
  display: -webkit-box;
  white-space: nowrap;

  @include media-breakpoint-down(lg) {
    &:nth-child(8) {
      display: none;
    }
  }
}

@include media-breakpoint-down(xl) {
  .header_black_city span {
    display: none;
  }
}


.ecommerce__sidebar__buttons {
  @include mobile() {
    display: none !important;
  }
}


.menu_new_btn_close {
  right: 20px;
  top: 4px;
  position: absolute;
  font-size: 17px;
}
