.ecommerce-selected.updated {

    &.show {
        .selected-filter {
            display: flex;
        }

        .ecommerce-hide {
            transform: scale(-1, 1);
        }
    }

    #mse2_selected {
        gap: 10px;
        width: 100%;
        height: auto;
        padding: 0;
        scrollbar-width: none;

        @media (min-width: 1200px) {
            column-gap: 20px;
        }
    }

    *:not(sup) {
        display: block;
        margin: 0;
        padding: 0;
        border: none;
        background-color: transparent;
        color: black;
        text-align: center;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        letter-spacing: 0.14px;
        white-space: nowrap;
    }

    .selected-filter {
        display: flex;
        align-items: center;
        gap: 10px;
        width: auto;
        height: 30px;
        padding: 5px 10px;
        border-radius: 5px;
        border: 1px solid #666;
        background: white;
        cursor: default;

        @media (min-width: 768px) {
            display: none;
        }

        @media (min-width: 1200px) {
            gap: 20px;
            height: 40px;
        }

        &:first-child,
        &:nth-child(2) {
            display: flex !important;
        }

        &:first-child {
            border: none;
            border-radius: 5px;
            background: #b1b1b1;

            .selected-filter__close {
                color: white;

                sup {
                    background-image: url("data:image/svg+xml;base64,");

                    @media (min-width: 1200px) {
                        background-image: url("data:image/svg+xml;base64,");
                    }

                    @media (hover: hover) and (pointer: fine) {
                        &:hover {
                            background-image: url("data:image/svg+xml;base64,");
                        }
                    }
                }
            }
        }
    }

    .selected-filter__close {
        display: flex;
        align-items: center;
        gap: 10px;
        color: var(--redesign-main-color);
        cursor: default;

        @media (min-width: 1200px) {
            gap: 20px;
        }

        sup {
            display: block;
            top: initial;
            margin: 0;
            padding: 0;
            width: 20px;
            height: 20px;
            filter: none;
            cursor: pointer;
            transition: all .1s linear;
            background: transparent no-repeat center;
            background-image: url("data:image/svg+xml;base64,");

            @media (min-width: 1200px) {
                width: 30px;
                height: 30px;
                background-image: url("data:image/svg+xml;base64,");
            }

            @media (hover: hover) and (pointer: fine) {
                &:hover {
                    background-image: url("data:image/svg+xml;base64,");
                }
            }
        }
    }

    .ecommerce-hide {
        flex-shrink: 0;
        display: none;
        width: 30px !important;
        height: 30px !important;
        cursor: pointer;
        border-radius: 3px !important;
        border: 1px solid #5a5a5a !important;
        background: transparent center no-repeat;
        background-image: url("data:image/svg+xml;base64,") !important;
        transition: border-color .1s linear, background-image .1s linear;

        @media (min-width: 768px) {
            display: block !important;
        }

        @media (min-width: 1200px) {
            width: 40px !important;
            height: 40px !important;
        }

        @media (hover: hover) and (pointer: fine) {
            &:hover {
                background-image: url("data:image/svg+xml;base64,") !important;
            }
        }
    }

    &__selected {
        display: flex;
        flex-wrap: wrap;
        gap: 10px;
        margin-top: 20px;
        padding: 0;
        width: 100%;
        height: auto;

        @media (min-width: 1200px) {
            margin-top: 40px;
        }

        &.invisible {
            display: none;
        }

        ul {
            li {
                &:first-child {
                    display: flex;
                }
            }
        }
    }
}
