.header_second_menu {
  position: relative;
  background-color: #fff;
}


.menu_new_header_full_list .the_element:first-child > .the_element_list li:last-child {
  font-weight: bold !important;
}


.modal-backdrop.modal-megamenu {
  z-index: 1;
}


.menu_show_wrapper {
  .menu_show_wrapper_menu {
    @extend .container;
    padding-right: 0;
    padding-left: 0;

    &.active:before {
      content: "";
      position: absolute;
      z-index: 3;
      bottom: 0;
      height: 20vh;
      left: -1000px;
      right: -1000px;
      background: #fff;
      width: auto;
      /*box-shadow: 1px 7px 7px 2px rgba(0, 0, 0, 0.5);*/
    }
  }
}
