.filters_categories {
    margin-bottom: 20px;
}

.new_listing_inform {
    background: #fff3ca;
    width: auto;
    margin-left: -20px;
    margin-right: -20px;
    padding-left: 20px;
    padding-right: 370px;
    height: 100px;
    padding-top: 19px;
    position: relative;
}

.new_listing_inform:before {
    position: absolute;
    content: "";
    z-index: 1;
    right: 0;
    top: 0;
    height: 100%;
    width: 110px;
    background: #fec50b;
}

.new_listing_inform:after {
    position: absolute;
    content: "";
    z-index: 1;
    right: 110px;
    top: 0;
    height: 100%;
    width: 240px;
    background: #ffe79a;
}

.new_listing_inform h1 {
    font-size: 32px;
    line-height: 37px;
    margin-bottom: 8px;
}

.new_listing_inform .price_info_small {
    font-size: 17px;
    line-height: 20px;
    margin-bottom: 0;
}

.listing_content_choice {
    background: #fff3ca;
    padding: 5px 8px 0;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    margin-bottom: 10px;
    width: 100%;
}

.listing_content_choice i {
    line-height: 40px;
    color: #000000;
    padding: 25px 20px;
    display: inline-block;
    font-size: 27px;
    margin-bottom: 5px;
}

.ncatfiltit_price {
    padding-left: 10px;
    margin-bottom: 10px;
    position: relative;
}

.main_filters .ncatfiltit_price .filter_footer {
    top: 22px;
    font-weight: normal;
}

.ncatslrange {
    padding-left: 15px !important;
    padding-right: 15px !important;
}


.option_select select {
    display: none !important;
}

@media screen and (max-width: 992px) {
    .listing_filters_links {
        display: none;
    }
}

@media (max-width: 1200px) and (min-width: 991px) {
    #mse2_mfilter .row .col-md-2 {
        width: 22.666667%;
    }

    #mse2_mfilter .row .col-md-10 {
        width: 77.333333%;
    }
}

.image_color_interdekor {
    text-align: center;
    margin-bottom: 20px;
}

@media screen and (min-width: 1440px) {
    .image_color_interdekor img {
        width: 215px;
    }
}
@media screen and (max-width: 1439px) {
    .image_color_interdekor img {
        width: 175px;
    }
}


.check_wrap.ps {
    position: relative;
}
